.header {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  transform: translateX(0);
  transition: transform 0.3s;
  z-index: 200;

  &-top {
    background-color: $color-red;
    color: #fff;

    &-height {
      height: 80px;
      display: flex;
      align-items: center;
      white-space: nowrap;
      justify-content: space-between;

      @media #{$mmedia-md} {
        height: 64px;
      }

      & > div {
        min-width: 0;
      }
    }
    &-left {
      flex: 1;
    }
    &-right {}
    .mbtn {
      white-space: nowrap;
      min-width: 133px;
    }
  }
  &-menu {
    background-color: $color-dark;
    position: relative;
    z-index: 201;

    .container-fluid {
      @media #{$mmedia-mlg} {
        max-width: 1140px;
      }
    }
    @media #{$mmedia-md} {
      display: none;
    }

    &-height {
      height: 56px;
      display: flex;
      justify-content: center;
    }
    &-popup {
      position: absolute;
      left: 0;
      top: 100%;
      right: 0;
      background-color: #fff;
      box-shadow: 0 16px 16px rgba(0,0,0,.2);
      z-index: 1;
      display: none;
    }
  }
  &-lcolumn {
    padding: 0;
    margin: 0;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    list-style: none;
    transition: all 0.3s ease;

    & > li {
      display: block;
      position: relative;
      padding-left: 36px;
      margin-left: 36px;
      transition: all 0.3s ease;

      @media #{$mmedia-lg} {
        padding-left: 24px;
        margin-left: 24px;
      }

      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -22px;
        width: 1px;
        height: 44px;
        background-color: rgba(255, 255, 255, 0.495384);
      }
      &:first-child {
        padding-left: 0;
        margin-left: 0;

        &::after {
          display: none;
        }
      }
      &:nth-child(2) {
        min-width: 0;
      }
    }
  }
  &-rcolumn {
    padding: 0 0 0 32px;
    margin: 0;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    list-style: none;

    @media #{$mmedia-md} {
      margin-right: -16px;
      padding-left: 0;
    }

    & > li {
      display: block;
      position: relative;
      padding-left: 8px;

      @media #{$mmedia-md} {
        padding-left: 4px;

        &:last-child {
          padding-left: 0;
        }
      }
      &:first-child {
        padding-left: 0;
      }
    }
  }

  &-logo {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 32px;
    margin-left: 32px;

    @media #{$mmedia-lg} {
      margin-left: 16px;
    }
    @media #{$mmedia-md} {
      height: 27px;
      margin-left: 0;
    }

    img {
      display: block;
      height: 100%;
    }
  }
  &-location {
    position: relative;
    font-family: $font-PN;
    line-height: 1;
    transition: all 0.3s ease;
    margin-left: -4px;

    &.hidden-btns {
      .header-location-city {
        min-width: 1px;
        margin-top: 25px;
      }
      ul {
        opacity: 0;
        margin-left: -9999px;
      }
    }
    &-city {
      display: inline-block;
      font-weight: bold;
      font-size: 18px;;
      line-height: 1.33;
      color: $color-white;
      text-transform: uppercase;
      text-decoration: none;
      min-width: 1px;
      max-width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      position: relative;
      border-radius: 4px;
      padding: 0 4px;
      z-index: 2;

      &:hover {
        color: $color-white;
        background: rgba(255,255,255,.2);
      }
    }
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
      display: flex;
      align-items: center;
      position: relative;
      transition: all 0.3s ease;

      li {
        display: block;
        padding-left: 12px;

        &:first-child {
          padding-left: 4px;
        }
      }
      a {
        display: inline-block;
        text-transform: uppercase;
        text-decoration: underline;
        color: #fff;
        letter-spacing: 0.3px;
        font-size: 14px;
        line-height: 1.43;
        position: relative;
        z-index: 2;

        &::after {
          content: '';
          position: absolute;
          left: -4px;
          top: 0;
          right: -4px;
          bottom: 0;
          background: $color-default;
          border-radius: 4px;
          opacity: 0;
          transition: all 0.3s ease;
          z-index: -1;
        }
        &:hover {
          color: #fff;
          text-decoration: underline;

          &::after {
            opacity: 1;
          }
        }
      }
    }
  }
  &-contacts {
    position: relative;
    font-family: $font-PN;
    line-height: 1.33;
    white-space: nowrap;

    &-phone {
      display: inline-block;
      text-decoration: none;
      font-size: 18px;
      line-height: 1.33;
      letter-spacing: 0.3px;
      color: #fff;
      position: relative;
      z-index: 2;

      &::after {
        content: '';
        position: absolute;
        left: -4px;
        top: 0;
        right: -4px;
        bottom: 0;
        background: rgba(255,255,255,.2);
        border-radius: 4px;
        opacity: 0;
        transition: all 0.3s ease;
        z-index: -1;
      }
      &:hover {
        color: #fff;
        text-decoration: none;

        &::after {
          opacity: 1;
        }
      }
    }
    &-link {
      display: inline-block;
      text-transform: uppercase;
      text-decoration: underline;
      color: #fff;
      letter-spacing: 0.3px;
      font-size: 14px;
      line-height: 1.33;
      position: relative;
      z-index: 2;

      &::after {
        content: '';
        position: absolute;
        left: -4px;
        top: 0;
        right: -4px;
        bottom: 0;
        background: rgba(255,255,255,.2);
        border-radius: 4px;
        opacity: 0;
        transition: all 0.3s ease;
        z-index: -1;
      }
      &:hover {
        color: #fff;
        text-decoration: underline;

        &::after {
          opacity: 1;
        }
      }
    }
  }
  &-cart {
    position: relative;
    display: block;
    width: 52px;
    height: 56px;
    border-radius: 4px;

    @media #{$mmedia-md} {
      width: 48px;
      height: 48px;
    }

    &::after {
      content: '';
      position: absolute;
      width: 25px;
      height: 25px;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      mask-repeat: no-repeat;
      mask-position: center center;
      mask-size: contain;
      mask-image: url('../images/ic/ic-cart.svg');
      background-color: #fff;
      transition: all 0.3s ease;
      z-index: 1;
    }
    &:hover {
      background: rgba(255, 255, 255, 0.2);
    }
    &.active {
      background-color: #fff;
      box-shadow: 0 8px 16px rgba(0,0,0,.2);

      &::after {
        background-color: $color-red;
      }
      &:hover {
        box-shadow: 0 8px 16px rgba(0,0,0,.2);
        background-color: $color-pink;

        &::after {
          background-color: $color-red;
        }
        i {
          border-color: $color-pink;
        }
      }
    }
    i {
      font-style: normal;
      position: absolute;
      left: 21px;
      top: 27px;
      width: 20px;
      height: 20px;
      border: 2px solid #fff;
      text-align: center;
      background-color: $color-red;
      color: #fff;
      font-family: $font-PNC;
      font-size: 12px;
      font-weight: bold;
      line-height: 18px;
      border-radius: 50%;
      transition: all 0.3s ease;
      z-index: 2;

      @media #{$mmedia-md} {
        top: 24px;
        left: 19px;
      }
    }
  }

  &-school {
    display: inline-block;
    font-family: $font-PN;
    font-size: 18px;
    line-height: 1.33;
    letter-spacing: 0.3px;
    text-transform: uppercase;
    padding: 16px 16px 16px 50px;
    text-decoration: none;
    color: #fff;
    border-radius: 4px;

    &::after {
      content: 'S';
      position: absolute;
      left: 16px;
      top: 50%;
      margin-top: -14px;
      width: 28px;
      height: 28px;
      border: 2px solid #fff;
      border-radius: 50%;
      text-align: center;
      line-height: 24px;
      font-size: 18px;
    }
    &:hover {
      color: #fff;
      background: rgba(255,255,255,.2);
    }
  }
  &-btnsearch {
    display: block;
    outline: none !important;
    border: none;
    padding: 0;
    width: 48px;
    height: 48px;
    background-color: transparent;
    border-radius: 4px;

    &::after {
      content: '';
      position: absolute;
      width: 25px;
      height: 25px;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      mask-repeat: no-repeat;
      mask-position: center center;
      mask-size: contain;
      mask-image: url('../images/ic/loop.svg');
      background-color: #fff;
      transition: all 0.3s ease;
      z-index: 1;
    }
    &:hover {
      background: rgba(255, 255, 255, 0.2);
    }
  }

  &-btnmenu {
    display: block;
    outline: none !important;
    border: none;
    padding: 0;
    width: 60px;
    height: 64px;
    background-color: $color-default;
    text-decoration: none;
    color: #fff;
    position: relative;
    cursor: pointer;
    margin-left: 12px;

    span {
      position: absolute;
      left: 16px;
      width: 28px;
      height: 2px;
      border-radius: 2px;
      background-color: #fff;
      transition: all 0.3s ease;
      z-index: 1;

      &.l00 {
        top: 21px;
      }

      &.l01 {
        top: 31px;
      }

      &.l02 {
        top: 41px;
      }
    }

    &::after {
      content: '';
      position: absolute;
      left: 37px;
      top: 37px;
      width: 13px;
      height: 13px;
      border: 2px solid $color-default;
      background-color: $color-red;
      border-radius: 50%;
      display: none;
      z-index: 3;
    }

    &.inlogin::after {
      display: block;
    }

    &.active {
      background-color: $color-red;

      span {
        &.l00 {
          top: 30px;
          transform: rotate(45deg);
        }

        &.l01 {
          opacity: 0;
        }

        &.l02 {
          top: 30px;
          transform: rotate(-45deg);
        }
      }

      &.inlogin::after {
        opacity: 0;
      }
    }
  }

  &-user {
    position: relative;
    line-height: 1;
    padding: 0 8px;
    margin-left: 8px;
    margin-right: 8px;
    height: 55px;
    white-space: nowrap;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    transition: all 0.3s ease;

    &::after {
      content: '';
      position: absolute;
      left: -8px;
      top: 50%;
      margin-top: -22px;
      width: 1px;
      height: 44px;
      background: rgba(255,255,255,.495384);
    }
    &::before {
      content: '';
      position: absolute;
      right: -8px;
      top: 50%;
      margin-top: -22px;
      width: 1px;
      height: 44px;
      background: rgba(255,255,255,.495384);
    }

    &:hover {
      background: rgba(255,255,255,.2);
    }

    &-link {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 5;
    }
    &-name {
      padding-right: 4px;

      @media #{$mmedia-lg} {
        display: none;
      }

      span {
        display: block !important;
        padding: 0 8px;
        font-family: $font-PN;
        font-size: 18px;
        line-height: 24px;
        text-transform: uppercase;
        letter-spacing: 0.3px;
        border-radius: 4px;
        color: #fff;
        text-decoration: none;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
    }
    &-bonus {
      padding: 0 28px 0 8px;
      font-family: $font-PN;
      font-size: 16px;
      line-height: 24px;
      font-weight: bold;
      color: $color-red;
      background: #fff url('../images/ic/smarket.svg') right 8px top 4px no-repeat;
      background-size: auto 15px;
      border-radius: 4px;
      flex: 0 0 auto;
      width: auto;
      max-width: 100%;
    }
  }

  &-nav {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    width: 940px;

    @media #{$mmedia-md} {
      display: none;
    }

    & > li {
      display: block;
      z-index: 1;

      &.header-nav-categoryend:not(:last-child) {
        padding-right: 9px;
        margin-right: 8px;

        & > a::after {
          display: block;
        }
      }

      &:hover {
        z-index: 2;

        & > a {
          background-color: #fff;
          color: $color-default;
          text-decoration: none;
        }
        //.header-sub {
        //  display: block;
        //}
      }

      & > a {
        display: block;
        position: relative;
        white-space: nowrap;
        font-family: $font-PN;
        font-weight: bold;
        font-size: 14px;
        line-height: 56px;
        text-transform: uppercase;
        text-decoration: none;
        color: $color-white;
        padding: 0 8px 0 8px;
        border-radius: 4px 4px 0 0;

        &::after {
          content: '';
          position: absolute;
          top: 50%;
          margin-top: -8px;
          right: -9px;
          width: 1px;
          height: 16px;
          background-color: $color-white;
          display: none;
        }
      }
    }
  }
  &-sub {
    position: absolute;
    left: 0;
    top: 100%;
    right: 0;
    z-index: 20;
    display: none;

    &-body {
      padding: 32px 0 40px 0;
      width: 940px;
      margin-left: auto;
      margin-right: auto;

      &.paddfsalon {
        padding-left: 217px;
      }
      &.paddfschool {
        padding-left: 415px;
      }
    }
    .lmenu {
      margin-top: -8px;
      margin-bottom: -8px;

      li {
        padding-top: 8px;
        padding-bottom: 8px;
      }

      & + .ltitle,
      & + .lmenu {
        margin-top: 30px;
      }

      a i:first-child {
        border-color: $color-red;
        background-color: $color-red;
        margin-left: 0;
        margin-right: 8px;
        color: #fff;
        font-weight: 300;
      }
    }
  }

  &-deskmbtn {
    opacity: 0;
    transition: all 0.2s ease;
    //visibility: hidden;
    position: fixed;
    transform: translateY(-100%);
    left: 50%;
    margin-left: -24px;
    top: 16px;
    z-index: 250;
    width: 48px;
    height: 48px;
    box-shadow: 0 0 30px rgba(79,79,79,.2);
    background: #fff url('../images/system/smarket-red.svg') 50% 50% no-repeat;
    border-radius: 50% 50%;
  }
}
