.pdelivery {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  padding-right: 320px;

  @media #{$mmedia-md} {
    padding-right: 0;
  }

  .order-aside {
    order: 1;
  }
  .order-content {
    order: 2;
  }
  &.checkcity {
    .pdelivery-text {
      margin-bottom: 32px;

      @media #{$mmedia-md} {
        margin-bottom: 8px;
      }
    }
  }
  &-content {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;

    @media #{$mmedia-md} {
      width: 100%;
      order: 1;
    }
  }
  &-side {
    position: absolute;
    width: 288px;
    right: 0;
    top: -24px;
    bottom: -32px;
    background-color: #fff;
    z-index: 2;

    @media #{$mmedia-md} {
      position: relative;
      right: auto;
      top: auto;
      bottom: auto;
      width: 100%;
      background-color: #fff;
      padding-top: 32px;
      padding-bottom: 32px;
      margin-top: 56px;
      margin-bottom: 56px;
      z-index: 15;
    }

    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 32px;
      bottom: 42px;
      width: 114px;
      box-shadow: -10px 0 20px $color-white;
      z-index: -2;

      @media #{$mmedia-md} {
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        width: calc(100vh - 128px);
        transform: translateX(-50%);
        margin-left: 50%;
        box-shadow: 0 0 160px rgba(79,79,79,.2);
        border-radius: 40px;
      }
    }
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-color: #fff;
      width: 100vh;
      transform: translateX(-50%);
      margin-left: 50%;
      display: none;
      z-index: -1;

      @media #{$mmedia-md} {
        display: block;
      }
    }
  }
  &-aside {
    position: relative;
    padding-top: 24px;
    padding-left: 24px;
    padding-bottom: 32px;
    width: 288px;
    background-color: #fff;
    z-index: 2;

    @media #{$mmedia-md} {
      width: 100%;
      padding-top: 0;
      padding-left: 0;
      padding-bottom: 0;
    }
  }
  &-city {
    position: relative;

    &-head {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;

      @media #{$mmedia-md} {
        flex-direction: row;
      }
    }
    &-icon {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: $color-grey;
      margin-bottom: 24px;

      @media #{$mmedia-md} {
        margin-bottom: 0;
        margin-right: 24px;
        flex: 0 0 48px;
        max-width: 48px;
      }

      img {
        display: block;
      }
    }
    &-text {
      font-family: $font-PN;
      font-weight: bold;
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 15px;

      @media #{$mmedia-md} {
        font-size: 14px;
        line-height: 20px;
        flex-basis: 0;
        flex-grow: 1;
        max-width: 436px;
      }
    }
    &-form {
      position: relative;
    }
  }
  &-text {
    font-weight: 300;
    font-size: 18px;
    line-height: 32px;
    margin-bottom: 40px;

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 28px;
      margin-bottom: 32px;
    }

    b {
      font-weight: bold;
    }
  }
  &-column {
    position: relative;
    margin-bottom: 56px;
    margin-top: -24px;

    &-item {
      margin-top: 24px;
    }
  }
  &-twi {
    position: relative;
    padding-left: 36px;
    font-family: $font-PN;
    font-size: 14px;
    line-height: 20px;
    color: $color-grey;

    @media #{$mmedia-md} {
      font-size: 12px;
      line-height: 16px;
    }

    &-icon {
      position: absolute;
      left: 0;
      top: 0;

      img {
        display: block;
      }
    }
    &-title {
      position: relative;
      margin-bottom: 8px;
      margin-top: 16px;

      img {
        display: block;
      }
    }
    &-list {
      list-style: none;
      margin: -2px;
      padding: 0;
      display: flex;
      flex-wrap: wrap;
      align-items: center;

      & > li {
        padding: 2px;

        img {
          display: block;
          width: auto;
          height: 20px;
        }
        span {
          display: block;
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background-color: $color-line;
        }
      }
    }
  }
  &-settings {
    font-family: $font-PN;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 56px;

    &-text {
      margin-bottom: 4px;
    }
    &-ul {
      list-style: none;
      margin: -2px -6px;
      padding: 0;
      display: flex;
      flex-wrap: wrap;
      font-family: $font-PN;
      font-size: 14px;
      line-height: 20px;
      text-transform: uppercase;

      & > li {
        padding: 2px;

        a {
          display: block;
          text-decoration: underline;
          padding: 0 4px;
          border-radius: 4px;

          &.color-red {
            color: $color-red;

            &:hover {
              color: #fff;
              text-decoration: underline;
              background-color: $color-red;
            }
          }
          &.color-grey {
            color: $color-grey;

            &:hover {
              color: #fff;
              text-decoration: underline;
              background-color: $color-grey;
            }
          }
        }
      }
    }
  }
}