.mfp-fade {
	&.mfp-bg {
		opacity: 0;
		transition: all 0.15s ease-out;
		&.mfp-ready {
			opacity: 0.7;
		}
		&.mfp-removing {
			opacity: 0;
		}
	}
	&.mfp-wrap {
		.mfp-content {
			transform: translateY(-5rem);
			opacity: 0;
			transition: all 0.15s ease-out;
		}
		&.mfp-ready {
			.mfp-content {
				transform: translateY(0);
				opacity: 1;
			}
		}
		&.mfp-removing {
			.mfp-content {
				transform: translateY(-5rem);
				opacity: 0;
			}
		}
	}
}
.mfp-bg{
	background-color: #000;
}