.catalogblock {
  position: relative;
  margin-top: -40px;

  @media #{$mmedia-md} {
    margin-left: -8px !important;
    margin-right: -8px !important;
  }

  &-el {
    transition: all .1s ease;
    position: relative;
    z-index: 1;
    padding-top: 40px;

    @media #{$mmedia-md} {
      padding-left: 8px !important;
      padding-right: 8px !important;
    }

    &:hover {
      z-index: 2;
    }
  }
}