.page505 {
  position: relative;
  z-index: 2;

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    top: 136px;
    width: 562px;
    height: 264px;
    background: url('../images/system/bg-500.jpg') 0 -56px no-repeat;
    -webkit-background-size: 100% auto;
    z-index: -1;

    @media #{$mmedia-md} {
      top: 69px;
      left: 50%;
      margin-left: -344px;
      width: 357px;
      height: 203px;
      background-position: 0 0;
    }
    @media #{$mmedia-sm} {
      margin-left: -254px;
    }
    @media #{$mmedia-mm} {
      left: 0;
      margin-left: 16px;
    }
  }
  &-content {
    @media #{$mmedia-md} {
      padding-top: 130px;
    }
  }
  &-title {
    font-family: $font-PNC;
    font-weight: 900;
    font-size: 64px;
    line-height: 64px;
    color: $color-grey;
    margin-bottom: 32px;

    @media #{$mmedia-md} {
      font-size: 32px;
      line-height: 32px;
      margin-bottom: 24px;
    }
  }
  &-text {
    max-width: 608px;
    font-weight: 300;
    font-size: 24px;
    line-height: 36px;

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 28px;
    }

    p {
      margin-bottom: 16px;

      @media #{$mmedia-md} {
        margin-bottom: 8px;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}