.community {

  &-subtitle {
    font: 400 24px/32px $font-PNC;
    margin-top: 42px;
    letter-spacing: -.032em;

    @media #{$mmedia-md} {
      margin-bottom: -14px;
    }

    @media #{$mmedia-mmd} {
      font-size: 32px;
      margin-top: 80px;
      letter-spacing: -.024em;
    }
  }

  &-info {
    padding-bottom: 20px;

    &_promo {
      font: 300 14px/24px $font-default;

      @media #{$mmedia-mmd} {
        font-size: 24px;
        line-height: 36px;
        margin-bottom: 20px;
      }
    }

    &_title {
      color: $color-red;
      margin-bottom: 15px;
      line-height: 1;
      font-size: 44px;
      position: relative;

      &.stick-end {
        position: static;

        .pagecard-nav {
          bottom: 0 !important;
        }
      }

      > {
        span {
          margin-left: -10px;
        }
      }

      .scroll-fixed-end & {
        position: static;
      }

      @media #{$mmedia-mmd} {
        font-size: 77px;

        > span {
          margin-left: -15px;
        }
      }
    }

    p {
      font: 300 12px/22px $font-default;
      color: #000;

      @media #{$mmedia-mmd} {
        font-size: 14px;
        line-height: 24px;
      }
    }

    > p {
      @media #{$mmedia-mmd} {
        font-size: 24px;
        line-height: 36px;

        &:last-child {
          margin-bottom: 20px;
        }
      }
    }

    .education-card p {
      + p {
        margin-top: -4px;
      }
    }

    @media #{$mmedia-md} {
      .education-card:not(.__premium) {
        .education-school_logo {
          width: 32px;
          height: 32px;
        }
      }
    }
  }

  &-hero {
    margin: 0 -16px;
    position: relative;

    @media #{$mmedia-md} {

      .sticky-spacer {
        //display: none !important;
      }
    }

    @media #{$mmedia-mmd} {
      margin: 0 -80px;
    }

    .container {
      position: relative;
      z-index: 2;
    }

    .col {
      position: static;
    }

    .filter-result {
      transform: none;
      margin-top: auto;
      margin-left: 0 !important;

      li {
        padding: 8px 8px 0 0 !important;
      }

      @media #{$mmedia-md} {
        margin-bottom: 5px;
      }

      @media #{$mmedia-mmd} {
        margin-bottom: 30px;
      }
    }

    .education-card-img {
      margin-bottom: 0 !important;
    }

    .education-school_logo {
      width: 48px;
      height: 48px;
    }

    .education-school_name {
      text-transform: none;

      @media #{$mmedia-md} {
        font-size: 18px !important;
      }

      @media #{$mmedia-mmd} {
        font-size: 24px !important;
        padding-left: 0 !important;
      }
    }

    .community-hero_title.__wide {

      @media #{$mmedia-mmd} {
        margin-bottom: 34px;

        .community-hero_info {
          padding-bottom: 36px;
        }

        .education-school_logo {
          width: 80px;
          height: 80px;
        }

        .education-school_name {
          font-family: $font-PNC;
          padding-left: 0;
          font-size: 48px !important;
          font-weight: 900;
        }
      }
    }

    p {
      font-family: $font-default;
      font-weight: 300;
      font-size: 14px;
      line-height: 24px;

      @media #{$mmedia-md} {
        margin-bottom: 4px;
      }

      @media #{$mmedia-mmd} {
        font-size: 16px;
        line-height: 28px;
      }
    }

    &_swap {
      @extend %global_flex_block_column_nowrap_center_stretch;

      > .community-hero {
        @media #{$mmedia-md} {
          order: 1;
          margin-top: 6px;

          &:only-child {
            margin-top: 0;
          }
        }
      }
    }

    &_info {
      @include flex_block(column nowrap, flex-start, stretch);
      padding-bottom: 25px;
      height: 100%;

      @media #{$mmedia-mlg} {
        //padding-bottom: 56px;
      }
    }

    &_title {
      margin-bottom: 20px;
      padding-top: 38px;

      @media #{$mmedia-mmd} {
        .container {
          padding: 0;
        }
      }

      @media #{$mmedia-md} {
        position: absolute;
        top: -76px;
        right: 0;
        left: 0;
        z-index: 4;
        padding-top: 24px;
      }
    }

    @media #{$mmedia-md} {
      padding-top: 76px;

      .education-card {
        margin-left: -16px;
        margin-right: -16px;
        margin-bottom: 20px;
      }

      .education-school_logo {
        .main-content & {
          width: 28px !important;
          height: 28px !important;
          margin-right: 16px;
        }
      }

      .pagecard-cashback-info-more {
        font-size: 12px;
        line-height: 16px;
      }
    }

    &_bg {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      overflow: hidden;

      &:after {
        content: '';
        position: absolute;
        top: 528px;
        bottom: 0;
        right: 0;
        left: 0;
        z-index: 1;
      }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 1;
      }

      img {
        @extend %hor_centr;
        position: absolute;
        top: 0;
        min-width: 100%;

        @media #{$mmedia-md} {
          min-height: 100%;
        }
      }
    }

    &.__dark {
      p {
        color: #fff;
      }

      .education-school:not(:hover),
      .pagecard-cashback-info-more:not(:hover) {
        color: #fff;
      }

      .community-hero_bg {
        &:after {
          background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000 88px, #000 100%);
        }

        &::before {
          background: rgba(0, 0, 0, 0.7);
          backdrop-filter: blur(3px);
        }
      }
    }

    &.__light {
      p {
        color: $color-default;
      }

      .community-hero_bg {
        &:after {
          background: linear-gradient(180deg, rgba(245, 245, 245, 0) 0%, #f5f5f5 88px, #f5f5f5 100%);
        }

        &::before {
          background: rgba(245, 245, 245, 0.7);
          backdrop-filter: blur(3px);
        }
      }
    }
  }
}
