.datepicker-v1 {
  .lightpick {
    margin: 0;
    border-radius: 0;
    border: 0;
    padding: 0;
    box-shadow: none;
    color: #000;
    width: 100%;
    z-index: 0;

    &::after,
    &::before {
      display: none;
    }

    .lightpick__month-title {
      margin: 0;
      padding: 3px 0 0;
      pointer-events: none;

      span {
        font: 700 18px/28px $font-PN;
        color: #000 !important;
        padding-right: .3em;

        &:first-letter {
          text-transform: uppercase;
        }
      }
    }

    .lightpick__next-action,
    .lightpick__previous-action {
      @extend %icon_pseudo;
      background: transparent;
      border-radius: 0;
      margin: 0;

      &:before {
        border: 0;
        display: block;
        color: #000;
        font-size: 18px;
        line-height: 28px;
      }

      &:hover {
        background: #eee;
      }

      &.disabled {
        pointer-events: none;
        opacity: 0.5;
      }
    }

    .lightpick__previous-action {
      width: 20px;
      min-width: 20px;
      height: 30px;

      &::before {
        content: $icon-chevron_left;
      }
    }

    .lightpick__next-action {
      width: 20px;
      min-width: 20px;
      height: 30px;

      &::before {
        content: $icon-chevron_right;
      }
    }

    .lightpick__month-title-bar {
      margin-bottom: 23px;
    }

    .lightpick__month {
      padding: 0;
      width: 100%;
    }

    .lightpick__footer {
      @extend %global_flex_block_row_nowrap_flex_end_stretch;
      border: 0;
      padding: 55px 0 0;
      margin-bottom: -5px;

      .drp-selected {
        display: none;
      }

      .btn {
        text-transform: uppercase;
        text-decoration: underline;
        background: none;
        border: 0;
        margin: 0 5px 0 15px;
        padding: 0;
        color: $color-grey;
        font: 400 14px/20px $font-PN;

        &.btn-primary {
          color: $color-red;
        }

        &:hover {
          text-decoration: none;
        }
      }
    }

    .lightpick__days {
      padding-left: 1px;
      padding-top: 1px;
    }

    .lightpick__day {
      background-image: none !important;
      border-radius: 0 !important;
      height: 41px;
      width: 40px;
      min-width: 40px;
      font: 400 14px/20px $font-PN;
      border: 1px solid #dfdfdf;
      margin-left: -1px;
      margin-top: -1px;

      &.is-available {
        cursor: pointer;
      }

      &:hover {
        background: #eee;
        //border-color: transparent;
        color: inherit;
      }

      &.is-today {
        background: $color-pink;
        color: $color-default;
      }

      &.is-previous-month,
      &.is-next-month {
        opacity: 1;
        color: rgba(0, 0, 0, .5);
      }

      &.is-in-range,
      &.is-start-date {
        background: $color-red;
        border-color: $color-red;
        color: #fff;
      }

      &.is-disabled {
        text-decoration: none;
        background: $color-white;
        color: $color-line;
        cursor: not-allowed;
        opacity: 1;
        pointer-events: all;
        border-color: #dfdfdf !important;
      }
    }

    .lightpick__day-of-the-week {
      text-align: center;
      vertical-align: middle;
      min-width: 40px;
      width: 40px;
      line-height: 24px;
      font-size: 12px;
      border-radius: 4px;
      border: 1px solid transparent;
      white-space: nowrap;
      height: 34px;
      padding-bottom: 5px;
      text-transform: uppercase;
    }

    .drp-calendar {
      max-width: 100%;
      padding: 0;
      float: none;

      .calendar-table {
        border-radius: 0;
        padding: 55px 0 0;
        position: relative;

        table {
          td, th {
            border-radius: 0;
          }

          thead {
            tr {
              &:first-child {
                th {
                  &.month {
                    text-align: left;
                    font: 700 18px/28px $font-PN;
                    position: absolute;
                    top: 1px;
                    left: 0;
                  }

                  &:first-child,
                  &:last-child {
                    @extend %icon_pseudo;
                    position: absolute;
                    top: 1px;
                    width: 20px;
                    min-width: 20px;
                    height: 30px;
                    text-align: center;
                    cursor: default;

                    &::before {
                      border: 0;
                      display: block;
                      color: #000;
                      font-size: 18px;
                      line-height: 28px;
                    }

                    span {
                      position: absolute;
                      top: 0;
                      bottom: 0;
                      left: 0;
                      right: 0;
                      border: 0;
                      transform: none;
                      cursor: pointer;
                    }

                    &:empty {
                      opacity: .5;
                    }
                  }

                  &:last-child {
                    right: 0;

                    &::before {
                      content: $icon-chevron_right;
                    }
                  }

                  &:first-child {
                    right: 20px;

                    &::before {
                      content: $icon-chevron_left;
                    }
                  }
                }
              }

              &:last-child {
                th {
                  height: 32px;
                  padding-bottom: 5px;
                  border-bottom: 1px solid #dfdfdf;
                }
              }
            }
          }

          tbody {

            td {
              height: 40px;
              width: 40px;
              min-width: 40px;
              font: 400 14px/20px $font-PN;
              border: 1px solid #dfdfdf;

              &.today {
                background: $color-pink;
                color: $color-default;
              }

              &.in-range,
              &.active {
                background: $color-red;
                border-color: $color-red;
                color: #fff;
              }

              &.disabled {
                text-decoration: none;
                background: $color-white;
                color: $color-line;
              }
            }
          }
        }
      }

      &.left {
        .calendar-table table thead tr:first-child th:last-child {
          display: none;
        }
      }
    }
  }
}

