.seminar-forms {
  .s_seminar {
    padding: 6rem 0 10rem;
  }

  .s_seminar__center-holder {
    max-width: 90rem;
    margin: 0 auto;
    padding-right: 5rem;
    padding-left: 5rem;

    .main-content & {
      max-width: 716px;
    }
  }

  .s_seminar__section {
    padding-bottom: 70px;
    margin: 0 auto;
    font-size: 16px;
    line-height: 29px;

    .main-content & {
      max-width: 604px;
    }

    .mbtn {
      font-weight: 400;
      border-radius: 0;
    }

    .pag_wrap {
      padding-right: 20px;
    }

    .triangle_list {
      max-width: 620px;
    }

    > p {

      + p {
        margin-top: 28px;

        @media #{$mmedia-md} {
          margin-top: 10px;
        }
      }
    }

    &._pre-pag {
      padding-bottom: 20px;
    }

    form {
      font-family: $font-PN;
    }

    @media #{$mmedia-md} {
      padding-bottom: 40px;
      font-size: 14px;
      line-height: 25px;
      //padding-left: 20px;
      //padding-right: 20px;

      ul.triangle_list li::before {
        display: none;
      }
    }
  }

  .page_title {
    font-size: 4.8rem;
    line-height: 4.8rem;
    text-align: center;
    padding-bottom: 4rem;
  }

  .s_seminar__h2 {
    font: 48px/58px $font-PN;
    text-transform: uppercase;
    font-weight: 300;
    color: #4a4a4a;
    letter-spacing: -.03em;
    margin: 0 0 2.8rem;

    @media #{$mmedia-md} {
      font-size: 23px;
      line-height: 28px;
    }
  }

  .s_seminar__h3 {
    font-family: $font-PN;
    font-size: 2.6rem;
    text-transform: uppercase;
    font-weight: 400;
    color: #4a4a4a;
    margin: 0 0 2.8rem;
    letter-spacing: 0;
  }

  .seminar_panel {
    //max-width: 608px;
    border: 2px solid #fad4d8;
    margin: 0 0 30px;
    padding: 0 2.1rem;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  .schedule_panel__head {
    padding-top: 30px;
    padding-bottom: 30px;
    font: 300 26px/32px $font-PN;
    text-transform: uppercase;

    &[data-toggle="collapse"] {
      position: relative;
      color: $color-red;
      padding-right: 5rem;
      cursor: pointer;

      &::after {
        @extend %transition_all;
        position: absolute;
        top: 50%;
        right: 2rem;
        margin-top: -.7rem;
        content: '';
        width: 0;
        height: 0;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-top: 14px solid $color-red;

        @media #{$mmedia-md} {
          right: 1rem;
        }
      }
    }

    &[aria-expanded="true"] {
      &::after {
        transform: scale(-1, -1);
        -webkit-transform: scale(-1, -1);
      }
    }
  }

  .schedule_panel__content {
    border-top: 2px solid #fad4d8;
    padding: 30px 0 40px;

    p {
      margin-bottom: 20px;
    }
  }

  .mentor-wrap {
    margin: 0 -10px -35px;
  }

  .mentor {
    display: inline-block;
    vertical-align: top;
    margin: 0 1em 2.5em;
    color: $color-red;
  }

  .mentor__img {
    border-radius: 50%;
    width: 25.3rem;
    height: 25.3rem;
    overflow: hidden;
    margin-bottom: 22px;

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      max-width: 100%;
    }
  }

  .mentor__name {
    font-size: 20px;
    line-height: 25px;
    text-align: center;
  }

  .feedback {
    padding-bottom: 45px;

    @media #{$mmedia-md} {
      padding-bottom: 20px;
    }

    &:last-of-type {
      padding-bottom: 0;
    }
  }

  .feedback__head {
    @include flex_block_5(row, nowrap, flex-start, center, flex-start);
    padding-bottom: 1.8rem;

    @media #{$mmedia-md} {
      padding-bottom: 10px;
    }
  }

  .feedback__img {
    border-radius: 50%;
    width: 9rem;
    height: 9rem;
    overflow: hidden;

    img {
      max-width: 100%;
    }

    @media #{$mmedia-md} {
      width: 8rem;
      height: 8rem;
    }
  }

  .feedback__content {
    max-width: 585px;
  }

  .feedback__head-text {
    padding-left: 26px;

    @media #{$mmedia-md} {
      padding-left: 20px;
    }
  }

  .feedback__date {
    margin-bottom: 0;
  }

  p.feedback__name {
    font-size: 20px;
    font-weight: 700;
    margin: 4px 0 2px;
    font-family: $font-PN;

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 19px;
    }
  }

  ul.feedback__rating {
    @include flex_block_5(row, nowrap, flex-start, center, flex-start);
    padding-top: 0;

    li {
      border-radius: 50%;
      width: 1.5rem;
      height: 1.5rem;
      background-color: #b7b7b7;
      margin-right: .5rem;

      &:last-child {
        margin-right: 0;
      }

      &.checked {
        background-color: $color-red;
      }

      @media #{$mmedia-md} {
        width: 12px;
        height: 12px;
      }
    }
  }

  .seminar_bar {
    @extend %transition_all;
    @include flex_block_5(row, nowrap, space-between, stretch, flex-start);
    left: 0;
    width: 100%;
    z-index: -1;
    background-color: #fff;
    visibility: hidden;
    height: 7.2rem;
    font-size: 1.6rem;
    text-transform: uppercase;
    position: fixed;

    &.sticky {
      visibility: visible;
      z-index: 999;
    }

    &.-top {
      transform: translateY(-100%);
      box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.26);
      top: 0;

      &.sticky {
        transform: translateY(0);
        -webkit-transform: translateY(0);

        & + .s_seminar__scroll_to_top {
          opacity: 1;
          visibility: visible;
        }
      }
    }

    &.-bottom {
      transform: translateY(100%);
      box-shadow: 0 -5px 10px 0 rgba(0, 0, 0, 0.11);
      bottom: 0;

      &.sticky {
        transform: translateY(0);
        z-index: 9999;

        ~ .cookieapply {
          transform: translateY(-72px);
        }
      }
    }

    .mbtn {
      border-radius: 0;
    }

  }

  .s_seminar__scroll_to_top {
    @extend %transition_all;
    @include flex_block_5(row, nowrap, center, center, center);
    width: 5.4rem;
    height: 5.4rem;
    background: #e62739;
    bottom: 6.9rem;
    left: calc(100vw - 13.9rem);
    opacity: 0;
    visibility: hidden;
    position: fixed;
    cursor: pointer;
    z-index: 4;
    color: #fff;
    border: 1px solid #e62739;

    .icon {
      font-size: 2.2rem;
    }

    &:hover,
    &:focus {
      background-color: #fff;
      color: #e62739;
    }
  }

  .seminar_bar__title {
    @include flex_block_5(row, wrap, center, center, center);
    padding: 0 4vw;
    font-family: $font-PN;
  }

  .seminar_booking__open,
  .seminar_booking__close,
  .seminar_bar__btn {
    padding: 0 4vw;
    min-width: 24.4rem;
    height: 7.2rem;
    line-height: 7rem;
  }

  .seminar_bar__submit-wrap {
    width: calc(100% - 24.4rem);
    position: relative;
    background-color: rgba(#4a4a4a, .7);
  }

  .seminar_bar__submit {
    color: #fff;
    font: 1.6rem $font-PN;
    line-height: 1.4;
    text-transform: uppercase;
    cursor: pointer;
    pointer-events: auto;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 0 4vw;

    &.-hidden {
      transform: translateY(100%);
    }

    &.-promotion {
      transform: translateY(100%);

      &.-visible {
        transform: translateY(0);
      }
    }
  }

  .mfp-seminar {
    .mfp-container {
      padding: 0;
      overflow-x: hidden;
    }

    &.mfp-bg {
      display: none;
    }

    &.mfp-wrap {
      .mfp-container {
        transform: translateY(-100%);
        transition: all 0.15s ease-out;
      }

      &.mfp-ready {
        .mfp-container {
          transform: translateY(0);
        }
      }

      &.mfp-removing {
        .mfp-container {
          transform: translateY(-100%);
        }
      }
    }
  }

  a.mfp-close {
    top: -3rem;
    right: -3rem;
    opacity: 1;
    width: 2.2rem;
    height: 2.2rem;
    padding: 0;
    border: none;

    svg {
      width: 2.2rem;
      height: 2.2rem;
      pointer-events: none;
    }

    &:active {
      top: -3rem;
    }
  }

  .mfp-close-btn-in {
    .mfp-close {
      color: #fff;
    }
  }

  .seminar_booking {
    background-color: #fff;
    padding-bottom: 16rem;
    font-family: $font-PN;

    .content_holder {
      font-family: $font-PN;
    }
  }

  .seminar-booking__top-area {
    text-transform: uppercase;
    background-color: #ebebeb;
    margin: 0 -9999px 3rem;
    padding: 3rem 9999px;
  }


  .seminar-form {


  }

  .seminar_form_validate {
    .hidden {
      display: none;
    }
  }

  .radio_group {
    margin-right: -2.2rem;
    margin-left: -2.2rem;
  }

  .seminar_form_validate {
    padding-top: 6rem;
  }

  .radio_group__item {
    p {
      line-height: 1.4;
    }

    [type="radio"] {
      &:not(:checked),
      &:checked {
        position: absolute;
        left: -9999px;

        & ~ label {
          @include flex_block_5(row, wrap, space-between, flex-start, flex-start);
          @extend %transition_all;
          position: relative;
          cursor: pointer;
          line-height: 1.4;
          font-size: 2rem;
          border: .1rem solid #ecf8f0;
          padding: 1.2rem 2.2rem 1.2rem 6.4rem;
          width: 100%;

          &::before {
            @extend %transition_all;
            border-radius: 50%;
            content: '';
            position: absolute;
            left: 2rem;
            top: 1.2rem;
            width: 2.6rem;
            height: 2.6rem;
            background-color: transparent;
            border: .2rem solid $color-green;
          }

          &::after {
            @extend %transition_all;
            content: '';
            border-radius: 50%;
            position: absolute;
            top: 1.9rem;
            left: 2.7rem;
            width: 1.2rem;
            height: 1.2rem;
            background-color: transparent;
          }
        }
      }

      &:not(:checked) {
        & ~ label {
          &::after {
            transform: scale(0);
            opacity: 0;
          }
        }
      }

      &:checked,
      &:focus {
        & ~ label {
          background-color: #ecf8f0;

          &::after {
            transform: scale(1);
            opacity: 1;
            background-color: $color-green;
          }
        }
      }

      &:disabled {
        &:not(:checked),
        &:checked {
          & ~ label {
            &::before,
            &::after {
              opacity: 0.4;
            }
          }
        }

        & ~ label {
          color: #aaa;
        }
      }
    }
  }

  #seminar_agreement_policy-error,
  #seminar_radio-error {
    display: none !important;
  }

  .radio_group__left_side {
    width: 65%;
    text-align: left;
  }

  .radio_group__right_side {
    width: 35%;
    text-align: right;

    &.c-success {
      color: #3ebd6b;
    }
  }

  .radio_group__dl {
    dt {
      float: left;
      width: 15rem;
      padding: .8rem 1.5rem 0 0;
    }

    dd {
      overflow: hidden;
      padding-bottom: .1rem;
    }
  }

  .s_seminar__section-head {
    @include flex_block_5(row, nowrap, space-between, center, flex-start);
    margin-bottom: 2.8rem;
  }

  .form_group {
    &.-inline {
      @include flex_block_5(row, nowrap, flex-start, flex-start, flex-start);
      margin: 0 -.75rem;

      .input_group,
      .mbtn {
        margin: 0 .75rem;
      }

      .mbtn {
        height: 5.8rem;
        //line-height: 5.6rem;
        min-width: 5.2rem;
      }
    }

    &.-ticket-wrap {
      max-width: 30rem;

      .mbtn {
        width: 5.2rem;
      }
    }
  }

  .input_group {
    position: relative;
  }

  .form_control {
    height: 5.8rem;
    border: .1rem solid #4a4a4a;
    font: 1.8rem $font-PN;
    line-height: 1.4;
    color: #4a4a4a;
    display: block;
    width: 100%;
    padding: .8rem 1.9rem;

    &:focus {
      & ~ .input_group__label {
        font-size: 1.4rem;
        top: 0;
        color: #4a4a4a;
      }
    }

    &[aria-required="true"][aria-invalid="false"] {
      border-color: $color-green;

      & ~ .input_group__label {
        font-size: 1.4rem;
        top: 0;
        color: #4a4a4a;
      }
    }

    &.error[aria-required="true"] {
      border-color: #e62739;

      & ~ .input_group__label {
        display: none;
      }

      & ~ label.error {
        font-size: 1.4rem;
        top: 0;
      }
    }

    &.-ticket {
      border-color: $color-green;
      color: $color-green;
      text-align: center;
      text-transform: uppercase;
    }
  }

  .seminar_bar__submit-text {
    z-index: 1;
    position: relative;
  }

  .seminar_bar__progress {
    @extend %transition_all;
    transition: all .3s ease;
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    height: .6rem;
    width: 1%;
    background-color: $color-green;
    opacity: 0;
  }

  .seminar_checkbox {
    [type="checkbox"] {
      &:not(:checked),
      &:checked {
        position: absolute;
        left: -9999px;

        & ~ label {
          position: relative;
          cursor: pointer;
          padding: 0 0 0 4.2rem;
          line-height: 1.4;
          font-size: 2rem;
          font-weight: 700;
          display: block;

          &::before {
            @extend %transition_all;
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 2.4rem;
            height: 2.4rem;
            background-color: transparent;
            border: .2rem solid $color-green;
          }

          &::after {
            @extend %transition_all;
            content: '';
            position: absolute;
            top: .6rem;
            left: .6rem;
            width: 1.2rem;
            height: 1.2rem;
            background-color: $color-green;
          }
        }
      }

      &:not(:checked) {
        & ~ label {
          &::after {
            transform: scale(0);
            opacity: 0;
          }
        }
      }

      &:checked {
        & ~ label {
          &::after {
            transform: scale(1);
            opacity: 1;
          }
        }
      }

      &:disabled {
        &:not(:checked),
        &:checked {
          & ~ label {
            &::before,
            &::after {
              opacity: 0.4;
            }
          }
        }

        & ~ label {
          color: #aaa;
        }
      }
    }
  }

  p.final_price_text {
    font-size: 1.2rem;
    line-height: 1.4;
    text-transform: none;
    color: rgba(#4a4a4a, .5);
  }

  .s_seminar__intro {
    background-color: #4a4a4a;
    color: #fff;
    margin: 0 -999.9rem 7.3rem;
    padding: 3.9rem 1003.9rem;
    font: 2rem/3.4rem $font-PN;

    p {
      //max-width: 608px;
      margin-bottom: 20px;
    }

    @media #{$mmedia-md} {
      padding: 3.1rem 999.9rem 2.9rem;

      p {
        margin-bottom: 0;
      }
    }
  }

  p.s_seminar__lead {
    font: 300 2.6rem/1.4 $font-PN;
  }

  .seminar_preview__img {
    height: 25.3rem;
    margin-bottom: 1.4rem;
  }

  .img-fit-container {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .seminar_preview__title_wrap {
    font: 700 2rem/1.4 $font-PN;
  }

  .seminar_preview__title {
    @extend %transition_all;
    border-bottom: 1px solid rgba($color-red, 0.4);
  }

  .seminar_preview {
    &:hover,
    &:focus {
      text-decoration: none;

      .seminar_preview__title {
        border-bottom-color: $color-red;
      }
    }
  }

  .s_seminar__price_wrap {
    padding-top: 6rem;
  }

  .s_seminar__price {
    font-size: 4.8rem;
    line-height: 5.6rem;
    font-weight: 700;
    margin-right: 0.8rem;
  }

  p.radio_group__text-sm {
    font-size: 1.8rem;
    line-height: 1.8rem;
    padding-top: 0.9rem;
    display: none;
  }

  .seminar_bar__submit-wrap {
    &.-success-message-shown {
      .seminar_bar__progress {
        left: -244px;
        width: calc(100% + 244px);
      }

      .form-success-message {
        z-index: 2;
        opacity: 1;
        visibility: visible;
        width: calc(100% + 244px);
        height: 224px;
        animation-duration: .8s;
        animation-name: successMessageSlideTopDesctop;
      }

      .form-success-message__inner {
        transition-delay: 500ms;
        opacity: 1;
        transform: translateY(0);
      }
    }
  }

  @keyframes successMessageSlideTopDesctop {
    0% {
      width: 100%;
      height: 72px;
    }

    50% {
      width: calc(100% + 244px);
      height: 72px;
    }

    100% {
      height: 224px;
      width: calc(100% + 244px);
    }
  }

  .form-success-message {
    @include flex_block_5(row, wrap, center, center, center);
    @extend %transition_all;
    position: absolute;
    bottom: 0;
    right: 0;
    opacity: 0;
    visibility: hidden;
    width: 100%;
    height: 100%;
    background-color: #3ebd6b;
    color: #fff;
    overflow: hidden;
  }

  .form-success-message__inner {
    @include flex_block_5(row, wrap, center, center, center);
    @extend %transition_all;
    transform: translateY(50px);
    opacity: 0;
  }

  .seminar_booking__close {
    &.-hidden {
      transform: translateX(-100%);
    }
  }

  .form-success-message__text {
    width: 100%;
    text-align: center;
    margin-bottom: 29px;
  }

  .form-success-message__btn {
    @extend %transition_all;
    width: 503px;
    height: 54px;
    border: 1px solid #fff;
    font-size: 16px;
    text-transform: uppercase;
    color: #fff;
    line-height: 54px;
    text-align: center;
    text-decoration: none;

    &:hover {
      color: #3ebd6b;
      background-color: #fff;
      text-decoration: none;
    }
  }

  @media screen and (min-width: 1025px) {
    .tablet-visible {
      display: none !important;
    }
  }

  @media screen and (max-width: 1025px) {
    .tablet-hidden {
      display: none !important;
    }
  }

  @media screen and (min-width: 1171px) {
    .seminar_page {
      .header {
        position: absolute;
        left: 0;
        right: auto;
        height: 11.8rem;
      }

      .header_main {
        left: 0;
      }
    }
  }

  @media screen and (max-width: $point_1) {
    .prevent-scroll {
      position: fixed;
    }
  }

  @media screen and (max-width: $point_2) {
    body {
      -webkit-overflow-scrolling: touch;
    }

    body.mfp-zoom-out-cur {
      overflow: hidden;
      -webkit-overflow-scrolling: auto;
    }

    .mfp-wrap {
      position: fixed;
      overflow: auto;
    }
  }

  @media screen and (max-width: 767px) {
    .s_seminar {
      padding: 4rem 0 12rem;
      position: relative;
    }
    .page_title {
      font-size: 2.3rem;
      line-height: 3rem;
      padding-bottom: 2rem;
    }
    .s_seminar__h2 {
      font-size: 2.3rem;
      letter-spacing: 0;
      margin-bottom: 1.8rem;
    }
    .s_seminar__center-holder {
      padding: 0 3rem;
    }
    .s_seminar__section {
      padding-bottom: 3em;
    }
    .seminar_panel {
      padding: 0 1.2rem;
      margin: 0 -1.2rem 2rem;
    }
    .schedule_panel__head {
      font-size: 1.6rem;
      line-height: 1.9rem;
      padding-top: 1.8rem;
      padding-bottom: 1.5rem;
      font-weight: 400;
    }
    .schedule_panel__content {
      padding-top: 2rem;
      padding-bottom: 2rem;
    }
    .mentor {
      //max-width: calc(50% - .4rem);
    }
    .mentor__img {
      width: 11.7rem;
      height: 11.7rem;
      margin: 0 auto .2rem;
    }
    .mentor__name {
      font-size: 14px;
      line-height: 18px;
    }

    .seminar_bar__title {
      display: none;
    }
    .seminar_booking__open,
    .seminar_booking__close {
      width: 100%;
    }
    .seminar_booking__close {
      width: auto;
      min-width: 8.4rem;
    }
    .seminar_bar {
      &.-top {
        transform: translateY(100%);
        box-shadow: 0 -5px 10px 0 rgba(0, 0, 0, 0.11);
        bottom: 0;
        top: auto;

        &.sticky {
          transform: translateY(0);
        }

        &.absolute {
          bottom: 0;
          position: absolute;
          transform: translateY(0);
          opacity: 1;
          z-index: 9;
          top: auto;
          visibility: visible;
          width: 100%;
        }
      }
    }
    .seminar_bar__submit-wrap {
      width: calc(100% - 8.4rem);
    }
    p.final_price_text {
      font-size: 1.4rem;
    }
    .s_seminar__h3 {
      font-size: 2.3rem;
      margin-bottom: 1.5rem;
    }
    .radio_group {
      margin-right: -1.2rem;
      margin-left: -1.2rem;
    }
    .seminar_form_validate {
      padding-top: 4rem;
    }
    .radio_group__item {
      [type="radio"] {
        &:not(:checked),
        &:checked {
          & ~ label {
            font-size: 1.6rem;
            flex-wrap: wrap;
            padding: 1.2rem 1.2rem 1.2rem 5.4rem;

            &::before {
              left: 1rem;
            }

            &::after {
              left: 1.7rem;
            }
          }
        }
      }
    }
    .radio_group__left_side {
      padding-bottom: 1rem;
    }
    .radio_group__left_side,
    .radio_group__right_side {
      width: 100%;
    }
    .s_seminar__section-head {
      flex-wrap: wrap;
      margin-bottom: 2rem;

      .text_muted {
        padding-top: .3rem;
      }
    }
    .input_group {
      margin-left: -1rem;
      margin-right: -1rem;
    }
    .form_group {
      &.-ticket-wrap {
        max-width: none;
        margin: 0 -1.75rem;
      }

      &.-inline {
        margin: 0 -1.75rem;

        .input_group {
          width: auto;

          & + .mbtn {
            width: auto;
          }
        }
      }
    }
    .seminar_checkbox {
      [type="checkbox"] {
        &:not(:checked),
        &:checked {
          & ~ label {
            font-size: 1.6rem;
          }
        }
      }
    }
    .input_group__label,
    label.error {
      height: auto;
      line-height: inherit;
      top: 1.8rem;
    }
    .form_control:focus,
    .form_control.error[aria-required=true] {
      & ~ .input_group__label,
      & ~ label.error {
        top: -1rem;
      }
    }
    .form_control[aria-required="true"][aria-invalid="false"] {
      & ~ .input_group__label {
        top: -1rem;
      }
    }
    .s_seminar__scroll_to_top {
      display: none;
    }
    .s_seminar__intro {
      font-size: 1.6rem;
      margin-bottom: 3.9rem;

      p {
        line-height: 2.6rem;
      }
    }
    p.s_seminar__lead {
      font-size: 1.8rem;
    }
    .seminar_preview:not(:last-child) {
      padding-bottom: 4rem;
    }
    .seminar_preview__img {
      height: 28.3rem;
    }
    .seminar_booking {
      padding-bottom: 11rem;
    }
    .s_seminar__price_wrap {
      padding-top: 4rem;
    }
    .s_seminar__price {
      font-size: 3.8rem;
      line-height: 4.8rem;
    }

    .seminar_bar__submit-wrap {
      &.-success-message-shown {
        .seminar_bar__progress {
          left: -95px;
          width: calc(100% + 95px);
        }

        .form-success-message {
          width: calc(100% + 95px);
          height: 180px;
          animation-name: none;
          animation-duration: 0s;
        }
      }
    }

    .form-success-message__text {
      margin-bottom: 20px;
    }
    .form-success-message__btn {
      width: 220px;
    }
  }

  @media screen and (max-width: 380px) {
    .form_group.-inline.promocode_wrap {
      flex-wrap: wrap;

      .input_group {
        width: 100%;

        & + .mbtn {
          width: 100%;
          margin-top: 1rem;
        }
      }
    }
  }

  @media screen and (max-width: 360px) {
    .seminar_bar__submit-text {
      font-size: 13px;
    }
  }
}

