.quantity-mini {
  position: relative;
  height: 40px;

  &-button {
    position: absolute;
    top: 0;
    cursor: pointer;
    width: 40px;
    height: 40px;
    text-align: center;
    color: #fff;
    user-select: none;
    background-color: $color-default;
    box-shadow: 0 8px 16px rgba(44,44,44,.1);
    border-radius: 50%;
    border: none;
    outline: none !important;
    transition: all 0.3s ease;
    z-index: 2;

    &:not(:disabled):hover {
      background-color: $color-red;
    }
    &:disabled {
      background-color: $color-light;
      cursor: default;
    }
    &.quantity-mini-up {
      right: 0;
      background-image: url('../images/system/plus.svg');
      background-position: 50% 50%;
      background-size: 16px auto;
      background-repeat: no-repeat;

      &.rotate {
        animation: rotate .5s linear;
      }
    }
    &.quantity-mini-down {
      left: 0;
      background-image: url('../images/system/minus.svg') ;
      background-position: 50% 50%;
      background-size: 16px auto;
      background-repeat: no-repeat;

      &.rotateminus {
        animation: rotateminus .5s linear;
      }
    }
  }
  input {
    display: block;
    width: 100%;
    height: 40px;
    padding: 0 40px;
    text-align: center;
    margin: 0;
    border: none;
    font-family: $font-PN;
    font-size: 18px;
    line-height: 28px;
    color: $color-grey;
    opacity: 0;
    z-index: -100;

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 20px;
    }

    &:focus {
      outline: 0;
    }
  }
  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type=number] {
    -moz-appearance: textfield;
  }

  &-ul {
    position: absolute;
    left: 40px;
    top: 0;
    right: 40px;
    bottom: 0;
    overflow: hidden;
    z-index: 2;
  }
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    font-family: $font-PN;
    font-size: 18px;
    line-height: 28px;
    color: $color-grey;
    z-index: 2;

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 20px;
    }

    & > li {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 40px;
    }
  }
}

.cart {
  position: relative;
  font-family: $font-PN;

  &-faq {
    position: relative;
    padding-top: 8px;

    &-item {
      margin-top: 16px;

      &:first-child {
        margin-top: 0;
      }
      &.active {
        .cart-faq-head {
          color: $color-red;

          &::after {
            background-color: $color-red;
            transform: rotate(180deg);
          }
        }
      }
    }
    &-head {
      font-size: 16px;
      line-height: 24px;
      position: relative;
      padding-left: 24px;
      cursor: pointer;
      transition: all 0.3s ease;

      &:hover {
        color: $color-red;

        &::after {
          background-color: $color-red;
        }
      }

      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 11px;
        width: 16px;
        height: 16px;
        margin-top: -8px;
        mask-repeat: no-repeat !important;
        mask-position: center center !important;
        mask-size: contain !important;
        mask-image: url('../images/ic/arrow-down.svg');
        background-color: $color-grey;
        transition: all 0.3s ease;
        z-index: 5;
      }
    }
    &-slide {
      font-size: 14px;
      line-height: 20px;
      padding-top: 8px;
      display: none;

      a {
        color: $color-default;
        text-decoration: underline;

        &:hover {
          color: $color-red;
          text-decoration: underline;
        }
      }
    }
  }
  &-payinfo {
    position: relative;
    padding: 16px 16px 16px 64px;
    background: $color-white url('../images/ic/pub.svg') 18px 18px no-repeat;
    border-radius: 4px;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 40px;

    a {
      color: $color-default;
      text-decoration: underline;

      &:hover {
        text-decoration: underline;
        color: $color-red;
      }
    }
  }
  &-sbonusinfo {
    font-size: 14px;
    line-height: 20px;
    border-radius: 4px;
    margin-bottom: 40px;
    padding: 16px;
    background-color: $color-bggreen;
    display: flex;

    @media #{$mmedia-md} {
      flex-wrap: wrap;
    }

    &-aside {
      flex: 0 0 100px;
      max-width: 100px;
      margin-right: 24px;
      display: flex;
      flex-direction: column;

      @media #{$mmedia-md} {
        flex-direction: row;
        margin-right: 0;
        margin-bottom: 16px;
        flex: 0 0 100%;
        max-width: 100%;
        align-items: flex-end;
      }

      &-head {
        @media #{$mmedia-md} {
          flex: 0 0 auto;
          width: auto;
          max-width: 100%;
          margin-right: 32px;
        }
      }
      &-text {
        font-size: 12px;
        line-height: 16px;

        @media #{$mmedia-md} {
          flex-basis: 0;
          flex-grow: 1;
          max-width: 100%;
        }
      }
      &-title {
        position: relative;
        margin-bottom: 9px;
        font-size: 12px;
        line-height: 16px;
        text-transform: uppercase;
        font-weight: bold;

        & > span {
          display: inline-block;
          padding: 4px 8px;
          border-radius: 4px;
          color: #fff;
          background-color: $color-green;
        }
      }
      &-bonus {
        position: relative;
        margin-bottom: 23px;
        font-family: $font-PNC;
        font-size: 32px;
        line-height: 32px;
        height: 32px;
        letter-spacing: -0.4px;
        white-space: nowrap;

        @media #{$mmedia-md} {
          margin-bottom: 0;
        }

        & > span {
          display: inline-block;
          padding: 3px 36px 0 0;
          height: 32px;
          background: url('../images/ic/sbonus-grey.svg') 100% 0 no-repeat;
        }
      }
    }
    &-content {
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;
    }

    a {
      color: $color-default;
      text-decoration: underline;

      &:hover {
        text-decoration: underline;
        color: $color-red;
      }
    }
  }
  &-mininfo {
    background: $color-pink url('../images/ic/warning-grey.svg') 22px 16px no-repeat;
    font-size: 14px;
    line-height: 20px;
    padding: 16px 16px 16px 64px;
    border-radius: 4px;
    margin-bottom: 40px;

    a {
      color: $color-default;
      text-decoration: underline;

      &:hover {
        text-decoration: underline;
        color: $color-red;
      }
    }
  }
  &-blockresult {
    @media #{$mmedia-md} {
      padding-top: 24px;
      padding-bottom: 32px;
      margin-bottom: 40px;
      background-color: #fff;
      position: relative;

      &::after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 247px;
        box-shadow: 0 0 160px rgba(79,79,79,.2);
        border-radius: 40px;
        z-index: -2;
      }
      &::before {
        content: '';
        position: absolute;
        left: -100px;
        right: -100px;
        top: 0;
        bottom: 0;
        background-color: #fff;
        z-index: -1;
      }
    }
    .mbtn-lg {
      @media #{$mmedia-mm} {
        padding-left: 10px;
        padding-right: 10px;
        font-size: 15px;
      }
      @media #{$mmedia-xm} {
        padding: 18px 10px 17px 10px;
        font-size: 15px;
      }
    }
  }
  &-result {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 24px;

    @media #{$mmedia-md} {
      text-align: center;
    }

    &-content {
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;

      @media #{$mmedia-md} {
        width: 100%;
      }
    }
    &-aside {
      flex: 0 0 160px;
      max-width: 160px;
      margin-left: 24px;
      padding-top: 4px;

      @media #{$mmedia-md} {
        flex: 0 0 100%;
        max-width: 100%;
        margin-left: 0;
      }
    }
    &-title {
      font-family: $font-PNC;
      font-size: 48px;
      line-height: 48px;
      letter-spacing: -0.75px;
      color: $color-grey;
      margin-bottom: 24px;

      @media #{$mmedia-md} {
        font-size: 32px;
        line-height: 32px;
        margin-bottom: 16px;
      }
    }
    .pcard-bonus {
      height: 31px;

      @media #{$mmedia-md} {
        justify-content: center;
        margin-bottom: 17px;
        margin-top: 16px;
      }

      &-info {
        font-size: 16px;
        padding: 4px 28px 4px 8px;

        &::after {
          top: 8px;
        }
      }
    }
    .pcard-price {
      @media #{$mmedia-md} {
        font-size: 64px;
        line-height: 56px;
        height: 56px;
        letter-spacing: -1.5px;
      }
    }
    .pcard-priceold {
      font-size: 32px;
      line-height: 32px;
      height: 32px;
      padding-top: 3px;
    }
    .pcard-info {
      margin: -2px -4px;

      @media #{$mmedia-md} {
        justify-content: center;
      }

      & + .pcard-info {
        margin-top: 2px;
      }
      & > li {
        padding: 2px 4px;
      }

      .micon {
        width: 32px;
        height: 32px;

        .ic {
          width: 16px;
          height: 16px;
        }
      }
    }
  }

  &-promo {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 24px;

    &-link {
      display: inline-block;
      position: relative;
      font-size: 14px;
      line-height: 20px;
      color: $color-default;
      letter-spacing: 0.3px;
      text-decoration-line: underline;
      text-transform: uppercase;
      padding-left: 60px;

      &::after {
        content: '';
        position: absolute;
        left: 16px;
        top: 50%;
        margin-top: -15px;
        width: 28px;
        height: 28px;
        mask-repeat: no-repeat !important;
        mask-position: center center !important;
        mask-size: contain !important;
        background-color: $color-default;
        transition: all 0.5s ease;
        mask-image: url('../images/ic/coupon.svg');
      }
      &:hover, &.active {
        color: $color-red;
        text-decoration: underline;

        &::after {
          background-color: $color-red;
        }
      }
    }
    &-content {
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;

      @media #{$mmedia-sm} {
        width: 100%;
        margin-bottom: 16px;
      }
    }
    &-button {
      flex: 0 0 208px;
      max-width: 208px;
      margin-left: 24px;

      @media #{$mmedia-sm} {
        flex: 0 0 100%;
        max-width: 100%;
        margin-left: 0;
      }
    }
    &-check {
      position: relative;

      & > div {
        flex-wrap: wrap;
        flex-direction: column;
      }

      @media #{$mmedia-sm} {
        align-items: center;
        justify-content: center;
        text-align: center;
      }

      span {
        display: inline-block;
        text-align: left;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        margin-bottom: 4px;
        padding-left: 30px;
        padding-top: 2px;
        background: url('../images/ic/check-bg.svg') 0 0 no-repeat;
      }
      button {
        display: inline-block;
        outline: none;
        border: none;
        outline: none;
        padding: 0 32px 0 8px;
        border-radius: 4px;
        font-family: $font-PN;
        font-weight: bold;
        font-size: 12px;
        line-height: 24px;
        white-space: nowrap;
        color: #fff;
        text-transform: uppercase;
        background-color: $color-grey;
        position: relative;
        margin-left: 30px;

        @media #{$mmedia-sm} {
          margin-left: 0;
        }

        &::after {
          content: '';
          position: absolute;
          right: 8px;
          width: 16px;
          height: 16px;
          top: 50%;
          margin-top: -8px;
          background: url('../images/ic/fs-delete.svg') 50% 50% no-repeat;
          background-size: contain;
          z-index: 1;
          transition: all 0.3s ease;
        }
        &::before {
          content: '';
          position: absolute;
          right: 8px;
          width: 16px;
          height: 16px;
          top: 50%;
          margin-top: -8px;
          background: url('../images/ic/fs-delete-hover.svg') 50% 50% no-repeat;
          background-size: contain;
          z-index: 2;
          opacity: 0;
          transition: all 0.3s ease;
        }
        &:hover {
          background-color: $color-red;

          &::after {
            opacity: 0;
          }
          &::before {
            opacity: 1;
          }
        }
        &:focus {
          outline: none;
        }
      }
    }
    .forminput {
      margin-top: -9px;
    }
    .search-status {
      @media #{$mmedia-sm} {
        top: 5px;
      }
    }
  }
  &-gifts {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 56px;

    @media #{$mmedia-md} {
      flex-direction: column;
    }

    &-info {
      flex: 0 0 320px;
      max-width: 320px;

      @media #{$mmedia-md} {
        flex: 0 0 100%;
        max-width: 100%;
      }

      & > div {
        background: $color-bggreen url('../images/ic/gift-status-0.svg') 16px 16px no-repeat;
        padding: 80px 16px 16px 16px;
        height: 160px;
        margin-right: 24px;
        font-size: 24px;
        line-height: 32px;
        color: $color-grey;
        border-radius: 4px 0 0 4px;
        position: relative;

        @media #{$mmedia-md} {
          max-width: 342px;
          margin-right: auto;
          margin-left: auto;
          margin-bottom: 48px;
          height: 116px;
          padding: 60px 16px 16px 16px;
          background-position: 50% 16px;
          background-size: 40px 40px;
          font-size: 18px;
          line-height: 20px;
          text-align: center;
        }
        @media #{$mmedia-xm} {
          max-width: 288px;
        }

        &::after {
          content: '';
          position: absolute;
          right: -24px;
          top: 0;
          bottom: 0;
          width: 0;
          height: 0;
          border-top: 80px solid transparent;
          border-left: 24px solid $color-bggreen;
          border-bottom: 80px solid transparent;

          @media #{$mmedia-md} {
            right: 0;
            top: auto;
            bottom: -24px;
            border-left: 171px solid transparent;
            border-right: 171px solid transparent;
            border-top: 24px solid $color-bggreen;
            border-bottom: none;
          }
          @media #{$mmedia-xm} {
            max-width: 288px;
            border-left: 144px solid transparent;
            border-right: 144px solid transparent;
          }
        }
      }
    }
    &-content {
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;

      @media #{$mmedia-md} {
        width: 100%;
      }
    }
  }
  &-h3 {
    font-family: $font-PNC;
    font-weight: 900;
    font-size: 32px;
    line-height: 32px;
    margin-bottom: 24px;
    color: $color-grey;

    @media #{$mmedia-md} {
      font-size: 28px;
      line-height: 28px;
      margin-bottom: 16px;
    }
  }
  &-textinfo {
    font-family: $font-default;
    font-weight: 300;
    font-size: 18px;
    line-height: 32px;
    margin-bottom: 40px;
    max-width: 843px;

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 26px;
      margin-bottom: 32px;
      margin-top: -12px;
    }
  }
  &-columns {
    & > .row {
      margin-left: -40px;
      margin-right: -40px;

      & > div {
        padding-left: 40px;
        padding-right: 40px;

        @media #{$mmedia-md} {
          margin-top: 40px;

          &:first-child {
            margin-top: 0;
          }
        }
      }
    }
  }
  &-docs {
    list-style: none;
    margin: -7px;
    padding: 9px 0 0 0;
    display: flex;
    flex-wrap: wrap;

    li {
      padding: 7px;
    }
    a {
      display: block;
      width: 40px;
      height: 40px;
      border-radius: 4px;
      border: 1px solid $color-grey;
      box-shadow: 0 8px 16px transparentize($translucent-default,.9);
      position: relative;

      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        mask-repeat: no-repeat !important;
        mask-position: center center !important;
        mask-size: auto auto !important;
        background-color: $color-grey;
        transition: all 0.3s ease;
      }
      &.pdf {
        &::after {
          mask-image: url('../images/ic/ic-pdf.svg');
        }
      }
      &.print {
        &::after {
          mask-image: url('../images/ic/ic-print.svg');
        }
      }
      &:hover {
        border-color: $color-red;
        &::after {
          background-color: $color-red;
        }
      }
    }
  }
  &-ilist {
    list-style: none;
    margin: 0;
    padding: 0;
    font-family: $font-default;
    font-weight: 300;
    font-size: 16px;
    line-height: 28px;

    & > li {
      margin-top: 24px;

      &:first-child {
        margin-top: 0;
      }
    }
  }
  &-iitems {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &-slist {
    list-style: none;
    margin: 0 0 24px 0;
    padding: 0;

    & > li {
      margin-top: 8px;

      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-top: 16px;

        .cart-slist-label {
          font-family: $font-PNC;
          font-weight: normal;
          font-size: 48px;
          line-height: 48px;
          letter-spacing: -0.75px;
          padding-top: 3px;

          @media #{$mmedia-sm} {
            font-size: 32px;
            line-height: 32px;
          }
        }
        .cart-slist-bonus {
          padding-right: 50px;

          @media #{$mmedia-sm} {
            padding-right: 38px;
          }
        }
        .cart-slist-price,
        .cart-slist-bonus {
          font-family: $font-PNC;
          font-size: 48px;
          line-height: 48px;
          height: 48px;
          letter-spacing: -2px;

          @media #{$mmedia-sm} {
            font-size: 36px;
            line-height: 36px;
            height: 36px;
          }

          &::before {
            width: 42px;
            height: 40px;

            @media #{$mmedia-sm} {
              width: 34px;
              height: 32px;
              top: 1px;
            }
          }
        }
      }
    }
    &-label {
      font-family: $font-PN;
      font-weight: bold;
      font-size: 18px;
      line-height: 24px;
      color: $color-grey;

      @media #{$mmedia-sm} {
        font-size: 16px;
        line-height: 20px;
      }
    }
    &-price {
      font-family: $font-PNC;
      font-size: 32px;
      line-height: 32px;
      height: 32px;
      letter-spacing: -0.4px;
      padding-top: 2px;
      padding-left: 23px;
      position: relative;
      white-space: nowrap;
      color: $color-grey;

      @media #{$mmedia-sm} {
        font-size: 26px;
        line-height: 26px;
        height: 26px;
      }
    }
    &-rcart {
      min-width: 170px;

      @media #{$mmedia-sm} {
        min-width: 130px;
      }
    }
    &-bonus {
      display: inline-block;
      font-family: $font-PNC;
      font-size: 32px;
      line-height: 32px;
      height: 32px;
      letter-spacing: -0.4px;
      padding-top: 2px;
      padding-left: 23px;
      padding-right: 36px;
      position: relative;
      white-space: nowrap;
      color: $color-grey;

      @media #{$mmedia-sm} {
        font-size: 26px;
        line-height: 26px;
        height: 26px;
        padding-right: 32px;
      }

      &::before {
        content: '';
        position: absolute;
        right: 0;
        top: 2px;
        width: 32px;
        height: 30px;
        mask-repeat: no-repeat !important;
        mask-position: center center !important;
        mask-size: auto 100% !important;
        mask-image: url('../images/ic/ic-smoney.svg');
        background-color: $color-grey;
        z-index: 1;

        @media #{$mmedia-sm} {
          width: 28px;
          height: 26px;
          top: 1px;
        }
      }
    }

    .minus {
      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        z-index: 1;
      }}
    .plus {
      &::after {
        content: '+';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        z-index: 1;
      }
    }
    .equally {
      &::after {
        content: '=';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        z-index: 1;
      }
    }
  }
}
.carts {
  position: relative;
  margin-bottom: 56px;

  @media #{$mmedia-md} {
    margin-bottom: 40px;
  }

  &-head {
    position: relative;

    .carts-item {
      padding-top: 0;
      padding-bottom: 6px;

      @media #{$mmedia-md} {
        padding-bottom: 0;
      }
    }
  }
  &-item {
    padding: 32px 0 16px 0;
    border-top: 1px solid $color-line;
    position: relative;
    background-color: #fff;

    @media #{$mmedia-md} {
      padding-top: 24px;
    }

    &:first-child {
      border-top: none;

      @media #{$mmedia-sm} {
        padding-top: 0;
      }
    }
    &:last-child {
      padding-bottom: 0;
    }
  }
  &-label {
    font-size: 14px;
    line-height: 20px;
  }
  &-good {
    margin-left: -32px;

    @media #{$mmedia-sm} {
      margin-left: 0;
    }

    .carts-label {
      padding-left: 128px;
    }
    .lcard-body {
      @media #{$mmedia-sm} {
        min-height: 126px;
      }
    }
    .lcard-img {
      flex: 0 0 112px;
      max-width: 112px;
      height: 140px;

      @media #{$mmedia-sm} {
        flex: 0 0 74px;
        max-width: 74px;
        height: 98px;
      }
    }
    .pcard-price {
      font-size: 32px;
      line-height: 32px;
      height: 32px;
    }
    .pcard-priceold {
      font-size: 24px;
      line-height: 24px;
      height: 24px;

      @media #{$mmedia-sm} {
        font-size: 18px;
        line-height: 18px;
        height: 18px;
      }
    }
    .pcard-volume {
      margin-top: 4px;
    }
  }
  &-summ {
    width: 160px;
    margin-left: 88px;

    @media #{$mmedia-lg} {
      margin-left: 30px;
    }
    @media #{$mmedia-md} {
      margin-left: 0;
      width: 110px;
    }

    .pcard-bonus {
      margin-top: 8px;
    }
  }
  &-number {
    width: 48px;
    text-align: center;

    @media #{$mmedia-md} {
      width: 32px;
    }

    &-text {
      font-family: $font-PNC;
      font-size: 32px;
      line-height: 31px;
      letter-spacing: -0.4px;

      @media #{$mmedia-md} {
        font-size: 26px;
        line-height: 26px;
      }
    }
  }
  &-quantity {
    width: 168px;
    text-align: center;

    @media #{$mmedia-md} {
      width: 134px;
    }

    .quantity-mini {
      margin-bottom: 16px;

      @media #{$mmedia-xm} {
        margin-top: 8px;
        margin-bottom: 0;
      }
    }
    &-label {
      font-size: 18px;
      line-height: 28px;
      color: $color-grey;
    }
  }
  &-delete {
    display: inline-block;
    font-size: 14px;
    line-height: 20px;
    color: $color-default;
    text-decoration: underline;

    @media #{$mmedia-sm} {
      position: absolute;
      left: 17px;
      bottom: 0;
    }

    &:hover {
      color: $color-red;
      text-decoration: underline;
    }
  }
  &-animdelete {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255,255,255,.9);
    opacity: 0;
    z-index: -1;

    & > div {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    &-return {
      display: inline-block;
      font-size: 14px;
      line-height: 20px;
      color: $color-default;
      text-decoration: underline;

      &:hover {
        color: $color-red;
        text-decoration: underline;
      }
    }
    &-circle {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      transform: rotate(-90deg);
      z-index: -1;

      .green-halo {
        stroke-dasharray: 1000;
        stroke-dashoffset: 1000;
      }
    }
    .myTimer {
      text-align: center;
      font-family: $font-PNC;
      font-size: 48px;
      line-height: 1;
      height: 70px;
      padding-top: 15px;
      letter-spacing: -2px;
      color: $color-grey;

      @media #{$mmedia-sm} {
        font-size: 32px;
        height: 56px;
        letter-spacing: -0.4px;
      }
    }
    &-ul {
      position: relative;
      width: 80px;
      height: 80px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 auto 8px auto;

      @media #{$mmedia-sm} {
        width: 56px;
        height: 56px;
      }
    }
    &.active {
      .green-halo {
        animation: stroke 20s ease-out forwards;
      }
    }
  }
  &-addfix {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(180deg, rgba(255,255,255,0) 0%, #FFF 100%);
    padding: 16px 16px 32px 16px;
    transition: all 0.3s ease;
    z-index: 100;

    &.hidden {
      transform: translateY(100%);
    }
  }
}