.ab_scroller {
  overflow: visible;
}

.seminar-scroller {
  @include flex_block_5(row, nowrap, flex-start, center);
  margin-bottom: 70px;

  & + .s_seminar__center-holder {
    @include transition;
    transform: translateY(10rem);
    opacity: 0;
    visibility: hidden;

    &.shown {
      transform: translateY(0);
      opacity: 1;
      visibility: visible;
    }
  }
}

.ab_scroller-item {
  &:first-of-type {
    padding-left: 6rem;
  }

  &:last-of-type {
    padding-right: 6rem;
  }
}

.ab_scroller-scrollwrap {
  @include transition;
  bottom: -3.5rem;
  opacity: 0;
  visibility: hidden;
  height: .5rem;
  left: 6rem;
  right: 6rem;

  &.shown {
    bottom: -2.8rem;
    opacity: 1;
    visibility: visible;
  }
}

.ab_scroller-scrollbar {
  background-color: #fad4d8;
}

.seminar-scroller__item {
  @include transition;
  transform: translateY(50rem) rotate(45deg);
  text-align: center;
  font-size: 18px;
  opacity: 0;
  visibility: hidden;
  transition-duration: 600ms;

  &.shown {
    transform: translateY(0) rotate(0);
    opacity: 1;
    visibility: visible;
  }

  &._sm {
    min-width: 27.5rem;
    max-width: 27.5rem;
  }

  &._md {
    min-width: 34.4rem;
    max-width: 34.4rem;
  }

  &.video-block-wrap {
    min-width: 61.3rem;
    max-width: 61.3rem;
    height: 34.4rem;
    overflow: hidden;

    img {
      vertical-align: top;
      max-width: 100%;
    }
  }
}

.ab_scroller-item,
.seminar-scroller__item {
  @include flex_block_5(row, nowrap, center, center);
}

.ab_scroller-border {
  display: none;
}

@media screen and (max-width: 767px) {
  .seminar-scroller {
    margin-bottom: 4.5rem;
  }
  .seminar-scroller__item {
    img {
      max-width: 100%;
      height: auto;
    }

    &._sm {
      min-width: 13rem;
      max-width: 13rem;
    }

    &._md {
      min-width: 18rem;
      max-width: 18rem;
    }

    &.video-block-wrap {
      min-width: 28rem;
      max-width: 28rem;
      height: 18rem;
    }
  }
  .ab_scroller-item {
    &:first-of-type {
      padding-left: 3rem;
    }

    &:last-of-type {
      padding-right: 3rem;
    }
  }
  .ab_scroller-scrollwrap {
    left: 3rem;
    right: 3rem;

    &.shown {
      bottom: -1.2rem;
    }
  }
}
