// footer
.footer {
  position: relative;
  background-color: $color-dark;
  padding: 72px 0;
  font-family: $font-PN;
  color: #fff;
  overflow: hidden;

  @media #{$mmedia-md} {
    padding: 56px 0;
  }

  a {
    color: #fff;

    &:hover {
      color: $color-red;
    }
  }
  .row {
    margin-left: -24px;
    margin-right: -24px;

    & > div {
      padding-left: 24px;
      padding-right: 24px;
    }
  }

  &-logo {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 202px;
    padding: 32px;
    background-color: $color-red;
    margin-bottom: 32px;

    @media #{$mmedia-md} {
      height: 121px;
      padding: 23px;
      min-width: 270px;
    }
    @media #{$mmedia-sm} {
      height: 86px;
      min-width: 100%;
      margin-bottom: 24px;
    }

    & > img {
      display: block;
      max-width: 176px;
    }
  }
  &-copy {
    position: relative;
    margin-bottom: 40px;
    font-size: 16px;
    line-height: 1.5;

    @media #{$mmedia-md} {
      margin-bottom: 20px;
    }
    @media #{$mmedia-sm} {
      margin-bottom: 40px;
    }

    span {
      display: block;
      font-weight: bold;
      text-transform: uppercase;
      margin-bottom: 8px;
      font-size: 16px;
      line-height: 1.33;
    }
    p {
      margin: 0;
    }
  }
  &-tosimushka {
    display: inline-block;
    position: relative;
    text-transform: uppercase;
    font-size: 18px;
    line-height: 1.33;
    padding-left: 35px;
    text-decoration: none;

    @media #{$mmedia-md} {
      margin-bottom: 32px;
    }
    @media #{$mmedia-sm} {
      margin-bottom: 40px;
    }

    &:hover {
      color: $color-red;
    }
    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 26px;
      height: 26px;
      border-radius: 50%;
      background: $color-red url('../images/ic/logo_s.svg') 50% 50% no-repeat;
      background-size: 10px auto;
    }
  }
  &-item {
    margin-bottom: 32px;

    @media #{$mmedia-sm} {
      margin-bottom: 32px;
    }
  }
  &-title {
    display: inline-block;
    padding: 4px 8px;
    border-radius: 4px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
    line-height: 1.33;
    margin-bottom: 16px;

    &.grey {
      background-color: $color-grey;
    }
    &.red {
      background-color: $color-red;
    }
  }
  &-menu {
    list-style: none;
    margin: -4px -12px;
    padding: 0;
    display: flex;
    position: relative;
    flex-wrap: wrap;
    letter-spacing: 0.3px;
    font-size: 14px;
    line-height: 1;
    text-transform: uppercase;

    li {
      padding: 4px 12px;
    }
    a {
      display: flex;
      align-items: center;
      height: 20px;
      text-decoration: none;
      white-space: nowrap;

      &:hover {
        color: $color-red;
      }
    }
    .micon {
      margin-right: 4px;
      margin-top: -2px;
    }
    &-teacher {
      display: block;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-size: cover;
      margin-right: 4px;
      margin-top: -2px;
    }
  }
  &-contacts {
    position: relative;
    font-family: $font-PNC;
    font-size: 32px;
    line-height: 1;

    @media #{$mmedia-md} {
      font-size: 24px;
    }
    @media #{$mmedia-sm} {
      font-size: 32px;
    }

    div {
      margin-top: 4px;

      &:first-child {
        margin-top: 0;
      }
    }
    a {
      text-decoration: none;
      color: $color-red;
      
      &:hover {
        color: #fff;
      }
    }
  }
  &-address {
    position: relative;
    font-size: 16px;
    line-height: 1.5;

    @media #{$mmedia-md} {
      font-size: 14px;
    }

    p {
      margin-bottom: 4px;
    }
    a {
      display: inline-block;
      font-size: 12px;
      line-height: 1.33;
      letter-spacing: 0.3px;
      text-decoration-line: underline;
      text-transform: uppercase;

      &:hover {
        text-decoration: underline;
      }
    }
  }
  &-socio {
    list-style: none;
    margin: -2px;
    padding: 0;
    position: relative;
    display: flex;

    li {
      padding: 2px;
    }
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 42px;
      height: 42px;
      border-radius: 4px;

      img {
        display: block;
        max-width: 80%;
        max-height: 80%;
      }
      &:hover {
        background-color: $color-red;
      }
    }
  }
  &-yandex {
    position: relative;
    padding-left: 120px;
    font-size: 14px;
    line-height: 1.43;
    margin-top: 6px;

    @media #{$mmedia-mm} {
      padding-left: 0;
      padding-top: 30px;
    }

    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 96px;
      height: 20px;
      background: url('../images/system/ya_logo.svg') 0 0 no-repeat;
      background-size: contain;
    }
  }
  &-lcolumn {
    width: 240px;

    @media #{$mmedia-md} {
      width: 100%;
    }
  }
  &-rcolumn {
    width: 230px;

    @media #{$mmedia-md} {
      width: 100%;
    }
  }
}
// /footer

.cookieapply {
  position: fixed;
  left: 0;
  right: 0;
  bottom: -100%;
  padding: 0 0 30px 0;
  z-index: 200;

  @media #{$mmedia-md} {
    padding-bottom: 20px;
  }

  &.cookiehide {
    bottom: -100% !important;
  }

  &-body {
    padding: 24px;
    display: flex;
    flex-wrap: wrap;
    box-shadow: 0 0 32px rgba(0,0,0,.3);
    border-radius: 4px;
    background-color: $color-white;
  }
  &-img {
    margin-right: 16px;
    flex: 0 0 48px;
    max-width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;

    @media #{$mmedia-md} {
      display: none;
    }
  }
  &-text {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    font-family: $font-PN;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.4;

    @media #{$mmedia-md} {
      width: 100%;
      margin-bottom: 24px;
    }
  }
  &-btn {
    flex: 0 0 auto;
    width: auto;
    max-width: 100%;
    margin-left: 24px;

    @media #{$mmedia-md} {
      margin-left: 0;
      width: 100%;

      .mbtn {
        display: block;
        width: 100%;
      }
    }
  }
}