.cartnogoods {
  position: relative;

  &-des {
    margin-bottom: 40px;
    font-size: 18px;
    max-width: 688px;
    line-height: 32px;

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 28px;
      margin-bottom: 24px;
    }
  }
  &-info {
    position: relative;
    font-family: $font-PN;
    margin-bottom: 24px;

    &-content {
      font-size: 16px;
      line-height: 24px;
      max-width: 688px;
    }
  }
  &-refresh {
    display: block;
    width: 72px;
    height: 72px;
    border-radius: 50%;
    box-shadow: 0 8px 16px rgba(0,0,0,.2);
    background: $color-default url('../images/ic/reload.svg') 50% 50% no-repeat;

    @media #{$mmedia-sm} {
      width: 52px;
      height: 52px;
      background-size: 26px auto;
    }

    &:hover {
      background-color: $color-red;
      box-shadow: none;
    }
  }
  &-source {}
  &-slider {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    z-index: 5;

    @media #{$mmedia-sm} {
      border-left: 1px solid $color-line;
    }

    &-el {
      padding: 16px;

      @media #{$mmedia-sm} {
        padding: 0;
      }
    }
    &-wrapper {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      z-index: 1;
    }
  }
  &-sl {
    position: relative;
    display: flex;
    margin-bottom: 40px;
    overflow: hidden;

    @media #{$mmedia-sm} {
      width: 100vw;
      margin-left: 50%;
      transform: translateX(-50%);
      overflow-x: scroll;
      padding-bottom: 16px;

      &::-webkit-scrollbar {
        height: 4px;
      }
      &::-webkit-scrollbar-thumb {
        background: $color-red;
        border-radius: 4px;
      }
    }

    &-el {
      flex: 0 0 33.33333%;
      max-width: 33.33333%;
      display: flex;
      padding: 16px;
      border-top: 1px solid $color-line;
      border-right: 1px solid $color-line;
      border-bottom: 1px solid $color-line;
      position: relative;

      @media #{$mmedia-sm} {
        flex: 0 0 auto;
        width: auto;
        max-width: 100%;
        border: none !important;
        padding: 0;

        .pcard {
          width: 215px;
        }
      }

      &:first-child {
        border-left: 1px solid $color-line;

        @media #{$mmedia-sm} {
          border-top: none;

          .cartnogoods-slider {
            border-left: none;
          }
        }
      }
      &:nth-child(1) {
        .cartnogoods-slider-wrapper {
          transition: all 1s ease;
        }
      }
      &:nth-child(2) {
        .cartnogoods-slider-wrapper {
          transition: all .7s ease;
        }
      }
      &:nth-child(3) {
        .cartnogoods-slider-wrapper {
          transition: all 1.3s ease;
        }
      }
    }
    .pcard {
      @media #{$mmedia-sm} {
        padding: 16px 16px 24px 16px;
      }
    }
    .pcard .lineinfo {
      height: 24px;

      @media #{$mmedia-sm} {
        margin-left: 16px;
        margin-top: 16px;
      }
    }
    &.active {
      .pcard {
        opacity: .2;
      }
      .cartnogoods-slider-wrapper {
        //transform: translateY(-100%);
      }
    }
  }
  &-btns {
    text-align: center;

    .row {
      margin-left: -12px;
      margin-right: -12px;

      & > div {
        padding-left: 12px;
        padding-right: 12px;
      }
    }

    .mbtn {
      max-width: 528px;
      margin-left: auto;
      margin-right: auto;

      @media #{$mmedia-sm} {
        font-size: 14px;
        line-height: 20px;
        padding-top: 15px;
        padding-bottom: 13px;
      }
    }
    small {
      display: block;
      font-family: $font-PN;
      font-size: 14px;
      line-height: 20px;
      margin-top: 24px;

      @media #{$mmedia-sm} {
        margin-top: 8px;
      }
    }
  }
}