%delete_link {
  font-weight: bold;
  font-size: 12px;
  line-height: 18px;
  padding: 1px 7px;
  white-space: normal;
  background-color: $color-default;
  text-decoration: none;

  @media #{$mmedia-mmd} {
    //line-height: 20px;

    .__large & {
      font-size: 14px;
      line-height: 28px;
    }
  }

  @media #{$mmedia-md} {
    .education-el.__community & {
      line-height: 16px;
      padding-top: 2px;
      padding-bottom: 2px;
    }

    .__large & {
      font-size: 12px;
      line-height: 24px;
    }
  }

  span {
    display: inline-block;
    vertical-align: middle;

    &[class^="icon-"], &[class*=" icon-"] {
      margin-top: -1px;
      font-size: 1.2em;
    }

    + span {
      padding-left: 3px;
    }
  }

  &.active {
    background: $color-red;


    &:hover {
      background: $color-grey;
    }
  }

  &:not(.__transp):hover {
    color: #fff;
  }

  &::before,
  &::after {
    display: none;
  }
}

.daterangepicker {
  display: none !important;
}

.filter {
  position: relative;
  z-index: 100;

  &-datepicker {
    position: relative;

    .daterangepicker {
      position: relative;
      top: auto !important;
      left: auto !important;
      right: auto !important;
      z-index: 0;
      display: block !important;

      .drp-calendar.left {
        position: absolute;
        top: 0;
        right: 20px;
        width: 20px;
        height: 30px;

        .calendar-table thead tr:first-child th:not(:last-child),
        .calendar-table thead tr:not(:first-child),
        .calendar-table tbody,
        .calendar-time,
        .drp-buttons {
          display: none !important;
        }
      }
    }

    &_info {
      position: absolute;
      bottom: 48px;
      z-index: 1;
      left: 1px;
      right: 1px;
      text-align: left;
      font: 12px/16px $font-PN;
    }
  }

  &-body {
    display: flex;
    position: relative;
    z-index: 200;

    @media #{$mmedia-md} {
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      padding-left: 60px;
      z-index: 201;
      transform: translateX(100%);
      transition: transform 0.5s;

      &.active {
        transform: translateX(0);
      }
    }

    & + .filter-result {
      margin-top: 15px;

      @media #{$mmedia-md} {
        margin-top: 12px;
      }
    }
  }

  &-result {
    list-style: none;
    margin: 0 -8px 10px;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    z-index: 1;

    @media #{$mmedia-md} {
      width: 100vw;
      margin: -8px 0 0 50%;
      padding-bottom: 17px;
      transform: translateX(-50%);
      flex-wrap: nowrap;
      overflow-y: hidden;
      overflow-x: auto;

      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }

      .education-card & {
        width: 100%;
        margin: 0;
        transform: none;
        overflow: visible;
        flex-wrap: wrap;

        li {
          padding: 4px 8px 0 0 !important;
        }
      }
    }

    @media #{$mmedia-mmd} {
      .education-el & {
        padding-top: 4px;
      }

      .education-card & {
        padding-bottom: 6px;

        li {
          padding: 4px 0 0 8px !important;
        }
      }
    }

    li {
      padding-left: 8px;
      padding-top: 8px;

      @media #{$mmedia-md} {
        padding: 8px 8px 0 0;
      }
    }

    &.__large li {
      padding-top: 0;
      margin-bottom: 8px;
      transition: none;

      a {
        white-space: nowrap;
      }
    }

    &-delete {
      display: block;
      outline: none;
      border: none;
      outline: none;
      padding: 0 32px 0 8px;
      border-radius: 4px;
      font-family: $font-PN;
      font-weight: bold;
      font-size: 12px;
      line-height: 24px;
      white-space: nowrap;
      color: #fff;
      text-transform: uppercase;
      background-color: $color-grey;
      position: relative;

      &::after {
        content: '';
        position: absolute;
        right: 8px;
        width: 16px;
        height: 16px;
        top: 50%;
        margin-top: -8px;
        background: url('../images/ic/fs-delete.svg') 50% 50% no-repeat;
        background-size: contain;
        z-index: 1;
        transition: all 0.3s ease;
      }

      &::before {
        content: '';
        position: absolute;
        right: 8px;
        width: 16px;
        height: 16px;
        top: 50%;
        margin-top: -8px;
        background: url('../images/ic/fs-delete-hover.svg') 50% 50% no-repeat;
        background-size: contain;
        z-index: 2;
        opacity: 0;
        transition: all 0.3s ease;
      }

      &.__transp {
        color: $color-default;
        background-color: transparent !important;
        padding-left: 0 !important;
        padding-right: 0 !important;

        .community-hero.__dark & {
          color: #fff;
        }
      }

      &.__white {
        background-color: #fff;
        color: $color-default;
      }

      &:hover {
        background-color: $color-red;

        &::after {
          opacity: 0;
        }

        &::before {
          opacity: 1;
        }
      }

      &:focus {
        outline: none;
      }

      @at-root a#{&} {
        @extend %delete_link;
      }

      @at-root span#{&} {
        @extend %delete_link;
      }
    }

    &-clear {
      display: block;
      outline: none;
      border: none;
      outline: none;
      background: none;
      padding: 0;
      font-family: $font-PN;
      font-size: 14px;
      line-height: 20px;
      text-decoration: underline;
      color: $color-grey;
      margin-left: 8px;
      padding-top: 2px;
      white-space: nowrap;

      &:hover {
        color: $color-red;
        text-decoration: underline;
      }

      &:focus {
        outline: none;
      }
    }
  }

  &-iulradios {
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 16px;
    line-height: 1.5;

    & > li {
      margin-top: 6px;

      &:first-child {
        margin-top: 0;
      }

      .bcheck > span {
        letter-spacing: 0;
      }
    }
  }

  &-fprice {
    &-text,
    &-textmobile {
      font-size: 12px;
      line-height: 1.4;
      color: $color-default;
      display: none;
    }

    &-info,
    &-infomobile {
      font-size: 12px;
      color: $color-light;
      line-height: 1.33;
    }
  }

  &-btngrey {
    outline: none !important;
    border: none;
    background: none;
    padding: 0;
    display: inline-block;
    position: relative;
    line-height: 20px;
    font-size: 14px;
    padding-left: 4px;
    padding-right: 4px;
    text-decoration: underline;
    text-transform: uppercase;
    color: $color-grey;
    border-radius: 4px;
    margin-left: -4px;
    transition: all 0.3s ease;
    z-index: 2;

    &:hover {
      color: #fff;
      text-decoration: underline;
      background-color: $color-grey;
    }
  }

  &-btnred {
    cursor: pointer;
    outline: none !important;
    border: none;
    background: none;
    padding: 0;
    display: inline-block;
    position: relative;
    line-height: 20px;
    font-size: 14px;
    padding-left: 4px;
    padding-right: 4px;
    text-decoration: underline;
    text-transform: uppercase;
    color: $color-red;
    border-radius: 4px;
    margin-left: -4px;
    transition: all 0.3s ease;
    z-index: 2;

    &:hover {
      color: #fff;
      text-decoration: underline;
      background-color: $color-red;
    }
  }

  &-btns {
    transition: all 0.3s ease;
    margin: 16px -16px -16px -16px;
    border-top: 1px solid $color-red;
    display: none;

    & > div {
      display: flex;
      position: relative;
      justify-content: flex-end;
      padding: 0 8px 8px 8px;

      & > div {
        padding: 8px;
        flex: 0 0 auto;
        width: auto;
        max-width: 100%;
      }
    }

  }

  &-list {
    list-style: none;
    margin: -8px;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    transition: all 0.3s ease;
    z-index: 100;

    @media #{$mmedia-md} {
      margin: -3px;
    }

    &-item {
      margin: 8px;
      position: relative;
      z-index: 101;
      transition: all 0.3s ease;

      @media #{$mmedia-md} {
        margin: 3px;
      }

      &:hover {
        z-index: 102;
      }

      &.active {
        z-index: 102;

        .filter-list-ihead {
          border-color: $color-red;
          color: $color-red;

          &::after {
            background-color: $color-red;
            transform: rotate(180deg);
          }
        }
      }
    }

    &-ihead {
      border: 1px solid $color-grey;
      border-radius: 4px;
      padding: 0 42px 0 16px;
      display: flex;
      align-items: center;
      font-family: $font-PN;
      color: $color-grey;
      height: 40px;
      box-shadow: 0 8px 16px transparentize($translucent-default, 0.9);
      cursor: pointer;
      transition: all 0.5s ease;

      &:hover {
        border-color: $color-red;

        &::after {
          background-color: $color-red;
        }
      }

      &::after {
        content: '';
        position: absolute;
        right: 16px;
        width: 16px;
        height: 16px;
        top: 50%;
        margin-top: -8px;
        mask-repeat: no-repeat;
        mask-position: center center;
        mask-size: contain;
        background-color: $color-grey;
        mask-image: url('../images/ic/arrow-down.svg');
        transition: all 0.3s ease;
        z-index: 1;
      }

      &-name {
        font-size: 16px;
        line-height: 1;
        text-transform: uppercase;
      }

      &-number {
        width: 17px;
        height: 17px;
        border-radius: 50%;
        background-color: $color-red;
        color: #fff;
        font-family: $font-PNC;
        font-weight: normal;
        font-size: 10px;
        line-height: 18px;
        margin-left: 6px;
        text-align: center;
        transition: all 0.5s ease;
        display: none;
      }

      &-status {
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: $color-red;
        margin-left: 6px;
        transition: all 0.5s ease;
        display: none;
      }
    }

    &-ipopup {
      position: absolute;
      left: 0;
      top: 100%;
      padding-top: 8px;
      font-family: $font-PN;
      display: none;
      z-index: 15;

      & > div {
        border: 1px solid $color-red;
        border-radius: 4px;
        box-shadow: 0 8px 16px transparentize($translucent-red, .84);
        padding: 16px;
        background-color: #fff;

        &.wlist_00 {
          width: 240px;
        }

        &.wlist_01 {
          width: 321px;
        }

        &.wlist_02 {
          width: 330px;
        }

        &.wlist_03 {
          width: 262px;
        }

        &.wlist_04 {
          width: 262px;
        }

        &.wlist_05 {
          width: 550px;
        }
      }
    }
  }

  .blockscroll {
    margin-top: -16px;
    margin-bottom: -16px;
  }

  .blockscroll-fix {
    max-height: 370px;
  }

  &-fsearch {
    position: relative;
    margin-bottom: 16px;
    margin-right: 8px;

    &-input {
      outline: none !important;
      display: block;
      width: 100%;
      border: none;
      border-bottom: 1px solid $color-grey;
      font-family: $font-PN;
      font-size: 16px;
      font-weight: normal;
      line-height: 25px;
      padding: 0 0 15px 35px;
      color: $color-grey;
      border-radius: 0;

      &:focus,
      &:hover {
        color: $color-grey;
        border-color: $color-red;

        & + .filter-fsearch-icon .sicon {
          background-color: $color-grey;
        }
      }
    }

    &-icon {
      position: absolute;
      left: 0;
      top: 3px;

      .sicon {
        width: 18px;
        height: 18px;
        background-color: $color-light;
      }
    }

    .search-status {
      right: -16px;
      top: -15px;
    }
  }

  &-fbrands {
    position: relative;

    &-item {
      margin-top: 24px;
      position: relative;
      padding-left: 36px;

      &:first-child {
        margin-top: 0;
      }
    }

    &-letter {
      position: absolute;
      left: 0;
      top: 3px;
      min-width: 17px;
      text-align: center;
      padding: 0 5px;
      font-family: $font-PN;
      font-size: 12px;
      line-height: 16px;
      font-weight: bold;
      color: #fff;
      background-color: $color-red;
      border-radius: 4px;
    }

    &-list {
      list-style: none;
      margin: 0;
      padding: 0;

      & > li {
        margin-top: 8px;

        &:first-child {
          margin-top: 0;
        }
      }
    }
  }

  &-fcatalog {
    position: relative;
  }

  &-mobileclose {
    outline: none !important;
    padding: 0;
    border: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 60px;
    height: 64px;
    background: $color-red url('../images/ic/close.svg') 50% 50% no-repeat;
    z-index: 10;

    &:hover {
      background-color: $color-redlight;
    }
  }

  &-mobile {
    position: relative;
    display: none;

    @media #{$mmedia-md} {
      display: block;
    }

    & > div {
      display: flex;
      position: relative;
      flex-wrap: wrap;
      margin: -6px;

      & > div {
        padding: 6px;
      }
    }

    &-btn {
      border: 1px solid $color-grey;
      border-radius: 4px;
      padding: 0 16px;
      display: flex;
      align-items: center;
      font-family: $font-PN;
      color: $color-grey;
      text-decoration: none;
      height: 36px;
      box-shadow: 0 8px 16px transparentize($translucent-default, 0.9);
      cursor: pointer;
      transition: all 0.5s ease;

      &:hover {
        border-color: $color-red;
      }

      &.tofmobile {
        .filter-list-ihead-status {
          display: block;
        }
      }
    }
  }

  &-allcheck {
    position: absolute;
    left: 0;
    top: 0;
    width: 28px;
    height: 28px;
    z-index: 5;
  }

  .layoutwhite {
    z-index: 100;
  }
}

.flmenu {
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: $font-PN;
  font-size: 16px;
  line-height: 20px;
  font-weight: normal;

  & > li {
    margin-top: 16px;
    position: relative;

    &:first-child {
      margin-top: 0;
    }

    &.active {
      & > .flmenu-link_lv1 {
        color: $color-red;
      }
    }
  }

  li.flmenu-wsub {
    & > .flmenu-link_lv1 {
      &::before {
        display: block;
      }
    }

    &.active {
      & > .flmenu-link_lv1 {
        color: $color-red;

        &::before {
          background-color: $color-red;
          transform: rotate(180deg);
        }
      }

      & > .flmenu-slide {
        display: block;
      }
    }
  }

  &-slide {
    display: none;
  }

  &-wresult {
    .flmenu-link_lv1 {
      padding-left: 0;
    }
  }

  &-submenu_lv2 {
    list-style: none;
    margin: 0;
    padding: 0 0 0 24px;
    border-left: 1px solid #e62739;
    font-size: 16px;
    line-height: 1.25;

    & > li {
      margin-top: 16px;
      position: relative;

      &:first-child {
        margin-top: 0;
      }

      &.active {
        & > .flmenu-link_lv1 {
          color: $color-red;
        }
      }
    }
  }

  &-link_lv1 {
    display: block;
    position: relative;
    padding: 0 0 0 24px;
    text-transform: uppercase;
    text-decoration: none;
    z-index: 1;
    color: $color-grey;
    font-size: 16px;
    line-height: 24px;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      width: 16px;
      height: 16px;
      top: 3px;
      mask-repeat: no-repeat;
      mask-position: center center;
      mask-size: contain;
      background-color: #4f4f4f;
      mask-image: url('../images/ic/arrow-down.svg');
      z-index: 1;
      display: none;
      transition: all 0.3s ease;
    }

    i {
      display: inline-block;
      margin-left: 8px;
      font-family: $font-PNC;
      font-weight: 300;
      font-size: 14px;
      line-height: 1;
      font-style: normal;
      color: $color-light;
      padding: 5px 8px 3px 8px;
      min-width: 23px;
      text-align: center;
      border: 1px solid $color-light;
      border-radius: 4px;
      transition: all 0.3s ease;
    }
  }

  &-check_lv3 {
    display: block;
    padding: 0;
    margin: 0;
    cursor: pointer;

    & > input[type="checkbox"] {
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      z-index: 2;

      &:checked + span {
        color: $color-red;

        &::after {
          border-color: $color-red;
          background-color: $color-red;
        }

        i {
          border-color: $color-red;
          color: $color-red;
        }
      }
    }

    & > span {
      display: inline-block;
      position: relative;
      padding: 0 0 0 28px;
      margin: 0;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: 0.3px;
      text-transform: uppercase;

      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 1px;
        width: 20px;
        height: 20px;
        border-radius: 4px;
        border: 1px solid $color-light;
        background-color: $color-white;
        transition: all 0.3s ease;
        z-index: 1;
      }

      i {
        display: inline-block;
        font-family: $font-PNC;
        font-weight: 300;
        font-size: 14px;
        line-height: 1;
        color: $color-light;
        font-style: normal;
        padding: 5px 8px 3px 8px;
        min-width: 23px;
        margin-left: 8px;
        border: 1px solid $color-light;
        border-radius: 4px;
        transition: all 0.3s ease;
      }
    }
  }

  &-more {
    display: inline-block;
    position: relative;
    line-height: 24px;
    font-size: 16px;
    text-decoration: underline;
    color: $color-grey;
    margin-bottom: 16px;
    margin-left: 24px;
    z-index: 2;

    &:hover {
      color: $color-red;
      text-decoration: underline;
    }
  }

  &-parentmini {
    color: $color-grey;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.3px;
    text-transform: uppercase;
    margin-top: 2px;
  }
}

.pagefilter {
  position: relative;
  max-width: 688px;

  &-title {
    font-weight: 300;
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 25px;

    @media #{$mmedia-md} {
      font-size: 20px;
      line-height: 32px;
      margin-bottom: 24px;
    }
  }

  &-text {
    font-family: $font-PN;
    font-size: 18px;
    line-height: 28px;

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 24px;
    }
  }

  &-list {
    list-style: none;
    margin: -4px;
    padding: 0;
    display: flex;
    flex-wrap: wrap;

    & > li {
      padding: 4px;

      & > a {
        display: block;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.3px;
        text-decoration-line: underline;
        text-transform: uppercase;

        &:hover {
          color: $color-red;
          text-decoration: underline;
        }
      }
    }
  }
}
