*,
*::after,
*::before {
  box-sizing: border-box;
  border-collapse: collapse;
  outline: 0;
}

a,
li,
img,
button,
input {
  transition: all 0.3s ease;
}

input[type='submit'],
button {
  cursor: pointer;
}

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="password"] {
  appearance: none;
  box-sizing: border-box;
  background-color: #fff;
  background-image: none;
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0;
  border-radius: 0;
  text-align: left;
}

@-webkit-keyframes autofill {
  to {
    color: inherit;
    background: transparent;
  }
}

input:-webkit-autofill {
  -webkit-animation-name: autofill;
  -webkit-animation-fill-mode: both;
}

button {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}

