html {
  font-size: 10px; // for rem from seducation
  overflow-x: hidden;

  &.webkit-scroll {
    &::-webkit-scrollbar {
      width: 8px;
    }

    &::-webkit-scrollbar-thumb {
      background: $color-red;
      border-radius: 4px;
    }

    * {
      &::-webkit-scrollbar {
        width: 8px;
      }

      &::-webkit-scrollbar-thumb {
        background: $color-red;
        border-radius: 4px;
      }
    }
  }

  &.fixscroll {
    @media #{$mmedia-md} {
      overflow: hidden !important;

      body {
        //overflow: hidden !important;
      }
    }
  }

  &.fixscrollsearch {
    overflow: hidden !important;

    body {
      overflow: hidden !important;
    }
  }

  &.smactive {
    overflow-y: hidden !important;

    header,
    body {
      @media only screen {
        padding-right: var(--scrollbarWidth);
      }
    }

    .header-body::before {
      opacity: 0;
    }

    .header-logo a {
      height: 66px;

      img {
        width: 75px;
      }
    }
  }

  &.empty-menu {
    .page .main-content {
      @media #{$mmedia-mmd} {
        margin-top: -40px;
      }
    }
  }
}

body {
  font-family: $font-default;
  font-size: 18px;
  line-height: 1.78;
  color: $color-default;
  padding-top: 176px;
  -webkit-text-size-adjust: none;

  &.hide-header {
    .scroll-wrapper .scrollbar-track,
    .getSeminar,
    .cookieapply,
    .overlayLoader,
    .header-deskmbtn,
    .header {
      display: none !important;
    }
  }

  &.header-v2 {

    .globalmenu {
      display: none !important;
    }

    @media #{$mmedia-mmd} {
      padding-top: 200px;
    }
  }

  @media #{$mmedia-md} {
    font-size: 16px;
    line-height: 1.75;
    padding-top: 88px;
  }

  .overlayLoader {
    display: none;
  }

  &.modal-open {
    @media only screen and (min-width: 992px) {
      padding-right: 8px !important;
    }

    .header {
      @media only screen and (min-width: 992px) {
        margin-right: 8px;
      }
    }

    .cookieapply {
      padding-right: 8px;
    }
  }

  &.scroll-down:not(.menu-opened)  {
    .header {
      transform: translateY(-100%);
    }

    .header-deskmbtn {
      transform: translateY(0);
      opacity: 1;
      visibility: visible;
    }

    .filter .layoutwhite {
      top: 0;
    }
  }

  &.scroll-up {
    .header {
      transform: translateY(0);
    }

    .header-deskmbtn {
      transform: translateY(-100%);
      opacity: 0;
      //visibility: hidden;
    }

    &.bodypcard {
      @media #{$mmedia-mmd} {
        .header {
          transform: translateY(-100%);
        }
        .header-deskmbtn {
          transform: translateY(0);
          opacity: 1;
          visibility: visible;
        }
      }
    }
  }

  a {
    outline: none !important;
    color: $color-default;
    text-decoration: underline;

    &:focus,
    &:active {
      outline: none !important;
    }

    &:hover {
      color: $color-dark;
      text-decoration: none;
    }
  }

  p {
    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .h1, h1 {
    font-family: $font-PNC;
    font-size: 64px;
    font-weight: 900;
    line-height: 1;
    color: $color-grey;
    padding: 0;
    margin: 0 0 32px 0;

    @media #{$mmedia-lg} {
      font-size: 56px;
    }
    @media #{$mmedia-md} {
      font-size: 48px;
    }
    @media #{$mmedia-sm} {
      font-size: 32px;
    }
  }

  .h2, h2 {
    font-family: $font-PNC;
    font-size: 48px;
    line-height: 1;
    font-weight: normal;
    letter-spacing: -0.75px;
    padding: 0;
    margin: 0 0 24px 0;

    @media #{$mmedia-md} {
      font-size: 32px;
    }

    & > a {
      display: inline-block;
      text-decoration: none;
      color: $color-default;

      &:hover {
        text-decoration: none;
        color: $color-red;
      }
    }
  }

  .h3, h3 {
    font-family: $font-PNC;
    font-size: 32px;
    line-height: 1;
    font-weight: normal;
    letter-spacing: -0.75px;
    padding: 0;
    margin: 0 0 16px 0;

    & > a {
      display: inline-block;
      text-decoration: none;
      color: $color-default;

      &:hover {
        text-decoration: none;
        color: $color-red;
      }
    }
  }

  .h4, h4 {
    font-family: $font-default;
    font-size: 24px;
    line-height: 1.4;
    font-weight: bold;
    padding: 24px 0 0 0;
    margin: 0 0 8px 0;

    @media #{$mmedia-md} {
      font-size: 20px;
    }
    @media #{$mmedia-sm} {
      font-size: 16px;
    }

    &:first-child {
      padding-top: 0;
    }
  }

  .h5, h5 {
    font-family: $font-PN;
    font-size: 14px;
    line-height: 1.43;
    font-weight: bold;
    padding: 16px 0 0 0;
    margin: 0 0 4px 0;
    text-transform: uppercase;

    &:first-child {
      margin-top: 0;
      padding-top: 0;
    }

    & > a {
      display: inline-block;
      text-decoration: none;
      color: $color-default;

      &:hover {
        text-decoration: none;
        color: $color-red;
      }
    }
  }

  .row {
    margin-left: -16px;
    margin-right: -16px;

    & > div {
      padding-left: 16px;
      padding-right: 16px;
    }

    &.no-gutters {
      margin-left: 0;
      margin-right: 0;
    }
  }

  .container, .container-fluid {
    padding-left: 16px;
    padding-right: 16px;
  }

  form, .form {

    small {
      display: block;
      font-family: $font-PN;
      font-size: 16px;
      line-height: 24px;
      margin-top: 4px;

      a {
        display: inline-block;
        text-decoration: underline;

        &:hover {
          text-decoration: underline;
          color: $color-red;
        }
      }

      p {
        margin-bottom: 8px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      & + div {
        padding-top: 8px;
      }
    }

    &-group {
      margin-bottom: 16px;

      &-lg {
        margin-bottom: 24px;
      }
    }

    &-minfo {
      font-style: italic;
      font-weight: 300;
      font-size: 16px;
      line-height: 28px;

      a {
        text-decoration: underline;

        &:hover {
          text-decoration: underline;
          color: $color-red;
        }
      }
    }

    &-supinfo {
      padding: 8px;
      background-color: $color-white;
      font-family: $font-PN;
      font-size: 14px;
      line-height: 20px;
      border-radius: 4px;
      max-width: 397px;

      img {
        display: inline-block;
        margin-left: 4px;
        margin-right: 4px;
        vertical-align: middle;
      }
    }
  }
}

main {
  position: relative;
  z-index: 10;

  &.mainfilter {
    z-index: 10;
  }
}

.scroll-wrapper {
  .webkit-scroll & {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
    overflow-y: auto;
  }
}

.page {
  height: 100%;
  max-width: 100%;
  position: relative;

  > .scroll-wrapper {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    > .scroll-content {
      @extend %global_flex_block_column_nowrap_space_between_stretch;
      min-height: 100%;
    }

    .menu-opened & {
      > .scrollbar-track {
        display: none !important;
      }
    }
  }
}

.main-content {
  flex: 1 0 auto;

  section:not(.search-wrapper) {
    overflow: hidden;
  }

  .search-wrapper {
    //min-height: 200vh;
  }
}

.fixed-content {
  position: fixed;
  z-index: 15;
  pointer-events: none;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  > * {
    pointer-events: all;
  }
}

.iti__flag {
  background-image: url("../images/system/flags.png");
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .iti__flag {
    background-image: url("../images/system/flags@2x.png");
  }
}

.layoutwhite {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 199;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.7);
  transition: all 0.5s linear;
  opacity: 0;
  visibility: hidden;

  &.active {
    visibility: visible;
    opacity: 1;
  }
}

.headerlayout {
  position: fixed;
  top: 136px;
  left: 0;
  z-index: 200;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.7);
  transition: all 0.5s linear;
  opacity: 0;
  visibility: hidden;

  @media #{$mmedia-md} {
    top: 88px;
  }

  &.active {
    visibility: visible;
    opacity: 1;
  }
}

.bg-lgreen {
  background-color: $color-bggreen !important;
}

.bg-lred {
  background-color: $color-pink !important;
}

.block-padd {
  padding: 44px 0;

  @media #{$mmedia-sm} {
    padding: 32px 0;
  }

  &:last-child {
    padding-bottom: 88px;

    @media #{$mmedia-sm} {
      padding-bottom: 64px;
    }
  }

  &:first-child {
    padding-top: 0;
  }

  & + .search {
    margin-top: -44px;

    @media #{$mmedia-sm} {
      margin-top: -32px;
    }
  }
}

.ic {
  display: block;
  font-style: normal;
  width: 16px;
  height: 16px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;

  &.ic-flame {
    background-image: url('../images/ic/flame.svg');
  }

  &.ic-gift {
    background-image: url('../images/ic/gift.svg');
  }

  &.ic-party {
    background-image: url('../images/ic/party.svg');
  }

  &.ic-sale {
    background-image: url('../images/ic/sale.svg');
  }

  &.ic-bonus {
    background-image: url('../images/ic/rotatebonus.svg');
  }
}

.micon {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2px;
  min-width: 24px;
  height: 24px;
  border-radius: 4px;
  text-align: center;
  font-size: 12px;
  line-height: 20px;
  font-weight: normal;
  font-style: normal;
  color: #fff !important;

  &.grey {
    background-color: $color-grey;
  }

  &.red {
    background-color: $color-red;
  }

  &.green {
    background-color: $color-green;
  }

  &.mini {
    min-width: 20px;
    height: 20px;
    line-height: 20px;

    i {
      width: 14px;
      height: 14px;
    }
  }

  span {
    display: block;
    padding: 0 2px;
  }
}

.sicon {
  outline: none !important;
  padding: 0;
  border: none;
  display: inline-block;
  width: 22px;
  height: 22px;
  mask-repeat: no-repeat !important;
  mask-position: center center !important;
  mask-size: contain !important;
  background-color: $color-light;
  transition: all 0.5s ease;

  &.delete {
    mask-image: url('../images/ic/delete.svg');
  }

  &.loop {
    mask-image: url('../images/ic/loop.svg');
  }

  &.si-blocks {
    background-color: $color-grey;
    mask-image: url('../images/ic/si-blocks.svg');
  }

  &.si-list {
    background-color: $color-grey;
    mask-image: url('../images/ic/si-list.svg');
  }
}

a.sicon, button.sicon {
  &:hover {
    background-color: $color-red;
  }
}

.ltitle {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 4px;
  text-transform: uppercase;
  font-family: $font-PN;
  font-size: 12px;
  font-weight: bold;
  line-height: 1.33;
  margin-bottom: 8px;

  &.grey {
    background-color: $color-grey;
    color: #fff;
  }

  &.red {
    background-color: $color-red;
    color: #fff;
  }
}

.lmenu {
  list-style: none;
  margin: -4px -10px;
  padding: 0;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  font-family: $font-PN;

  li {
    padding: 4px 10px;
    overflow: hidden;
  }

  a {
    display: block;
    color: $color-default;
    text-decoration: none;
    font-size: 16px;
    line-height: 28px;
    text-transform: uppercase;

    &.wlefti {
      i:first-child {
        margin-left: 0;
        margin-right: 8px;
        line-height: 20px;
        border: none;
        color: #fff;
        background-color: $color-red;
        padding: 1px 4px 0 4px;
        font-size: 12px;
        font-weight: bold;
        float: left;
        margin-top: 4px;
      }
    }

    &:hover {
      color: $color-red;

      i {
        border-color: $color-red;
        color: $color-red;
      }
    }

    span {
      display: inline;
      font-size: 16px;
      text-transform: uppercase;
      overflow: hidden;
    }

    i {
      display: inline-block;
      font-style: normal;
      font-weight: 300;
      color: $color-light;
      border: 1px solid $color-light;
      font-family: $font-PNC;
      font-size: 16px;
      line-height: 1;
      padding: 6px 6px 4px 6px;
      border-radius: 4px;
      min-width: 28px;
      max-width: 100%;
      text-align: center;
      margin-left: 8px;
      transition: all 0.3s ease;
    }

    s {
      display: inline-block;
      width: 40px;
      height: 40px;
      margin-right: 8px;
      background-position: 50% 50% !important;
      background-repeat: no-repeat !important;
      background-size: cover !important;
      border-radius: 50%;
      float: left;

      & ~ span,
      & ~ i {
        margin-top: 6px;
      }
    }

    .micon {
      margin-right: 8px;
      float: left;
    }
  }
}

.lcatalog {
  position: relative;
  font-family: $font-PN;

  &-el {
    padding-top: 11px;
    padding-bottom: 11px;
    min-height: 60px;
    display: flex;
    align-items: center;
    border-top: 1px solid $color-pink;
    position: relative;

    &:hover {
      .lcatalog-name {
        color: $color-red;
      }
    }

    &:first-child {
      border: none;
    }
  }

  &-link {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 5;
  }

  &-img {
    flex: 0 0 40px;
    max-width: 40px;
    margin-right: 8px;
    height: 53px;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }

  &-body {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;

    b {
      font-weight: bold;
    }
  }

  &-name {
    font-size: 18px;
    line-height: 20px;
    transition: all 0.3s ease;
  }

  &-des {
    list-style: none;
    margin: 4px 0 -4px 0;
    padding: 0;
    text-transform: uppercase;
    font-size: 12px;
    line-height: 1.33;
    display: flex;
    flex-wrap: wrap;

    li {
      position: relative;
      flex: 0 0 auto;
      width: auto;
      max-width: 100%;
      padding-top: 4px;
      padding-bottom: 4px;
      margin-right: 8px;

      &:last-child {
        margin-right: 0;
      }

      & + li {
        padding-left: 12px;

        &::after {
          content: '';
          position: absolute;
          left: 0;
          top: 50%;
          margin-top: -2px;
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background-color: $color-default;
        }
      }
    }
  }
}

.lineinfo {
  display: flex;
  align-items: center;
  height: 36px;
  line-height: 1;
  font-family: $font-PN;
  white-space: nowrap;
  position: relative;
  min-width: 1px;

  @media #{$mmedia-md} {
    height: 32px;
  }

  &.red {
    .lineinfo-body {
      color: #fff;
      background-color: $color-red;
    }

    .lineinfo-icon {
      background-color: $color-default;
    }

    .lineinfo-list li::after {
      background-color: $color-white;
    }
  }

  &.dark {
    .lineinfo-body {
      color: #fff;
      background-color: $color-dgrey;
    }

    .lineinfo-icon {
      background-color: $color-red;
    }

    .lineinfo-list li::after {
      background-color: $color-white;
    }
  }

  &.white {
    .lineinfo-body {
      color: $color-default;
      background-color: #fff;
    }

    .lineinfo-icon {
      background-color: $color-red;
    }

    .lineinfo-list li::after {
      background-color: $color-default;
    }
  }

  &.mini {
    .lineinfo-icon {
      flex: 0 0 24px;
      max-width: 24px;
      height: 24px;

      .ic {
        height: 16px;
        width: 16px;
      }
    }

    .lineinfo-name,
    .lineinfo-des {
      font-size: 12px;
    }
  }

  &-body {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    overflow: hidden;
    border-radius: 4px;
    min-height: 24px;
    min-width: 1px;
    transition: all 0.3s ease;
  }

  &-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 36px;
    float: left;
    height: 36px;
    background-color: $color-red;

    @media #{$mmedia-md} {
      width: 32px;
      height: 32px;
    }

    .ic {
      height: 22px;
      width: 22px;

      @media #{$mmedia-md} {
        height: 18px;
        width: 18px;
      }
    }
  }

  &-name {
    font-weight: bold;
    font-size: 14px;
    text-transform: uppercase;

    @media #{$mmedia-md} {
      font-size: 12px;
    }
  }

  &-des {
    font-style: italic;
    text-transform: uppercase;
    font-size: 14px;

    @media #{$mmedia-md} {
      font-size: 12px;
    }
  }

  &-list {
    list-style: none;
    margin: 0;
    padding: 0 8px;
    overflow: hidden;
    display: flex;
    flex-wrap: nowrap;
    max-width: 100%;

    li {
      position: relative;

      & + li {
        padding-left: 9px;
        margin-left: 8px;
        margin-right: 8px;
        position: relative;
        min-width: 1px;

        &::after {
          content: '';
          position: absolute;
          left: 0;
          top: 50%;
          margin-top: -8px;
          width: 1px;
          background-color: $color-default;
          height: 16px;
        }

        div {
          min-width: 1px;
          max-width: 100%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }

  &-link {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 5;

    &:hover {
      & ~ div {
        opacity: .9;
      }
    }
  }
}

.lactions {
  list-style: none;
  padding: 0;
  margin: -10px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  & > li {
    flex: 0 0 auto;
    width: auto;
    max-width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    padding: 10px;

    &:hover {
      & > div {
        color: $color-red;
      }
    }

    & > div {
      font-family: $font-PN;
      font-weight: 300;
      font-size: 16px;
      color: #000;
      transition: all 0.3s ease;
    }
  }

  &-link {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 5;
  }

  &-status {
    list-style: none;
    margin: 0 8px 0 0;
    padding: 0;
    display: flex;
    font-family: $font-PN;
    font-weight: bold;
    font-size: 12px;
    line-height: 24px;
    text-transform: uppercase;
    color: #fff;
    white-space: nowrap;

    li {
      padding: 0 8px;

      &:first-child {
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
      }

      &:last-child {
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
      }

      &.bg-dark {
        background-color: $color-default;
      }

      &.bg-red {
        background-color: $color-red;
      }
    }
  }
}

.pcard {
  position: relative;
  padding: 28px 0 0 0;
  transition: all 0.3s ease;
  font-family: $font-PN;
  width: 100%;
  z-index: 5;

  &.pcard-theme {
    height: 100%;

    .pcard-popup {
      top: -3px;
      bottom: -34px !important;
      opacity: 1;
      visibility: visible;
      border-radius: 0;
      box-shadow: 0 0 1px #cdcdcd;
    }
  }

  @media #{$mmedia-md} {
    padding-top: 0;
  }

  &-popup {
    position: absolute;
    left: -16px;
    top: -16px;
    right: -16px;
    bottom: -56px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    box-shadow: 0 8px 16px transparentize($translucent-default, 0.9);
    background-color: #fff;
    opacity: 0;
    border-radius: 4px;
    visibility: hidden;
    transition: all .3s ease;
    animation-delay: .1s;
    z-index: -1;

    @media #{$mmedia-md} {
      display: none !important;
    }

    .mbtn {
      border-radius: 0 0 4px 4px;
    }
  }

  &:hover {
    z-index: 6;

    .pcard-popup {
      bottom: -70px;
      opacity: 1;
      visibility: visible;
    }

    .pcard-name, .pcard-text {
      color: $color-red;
    }
  }

  &-img {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 256px;
    margin-bottom: 12px;
    position: relative;

    @media #{$mmedia-md} {
      height: 192px;
    }

    img {
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }

  &-info {
    display: flex;
    align-items: center;
    position: relative;
    list-style: none;
    margin: -5px;
    padding: 0;

    li {
      padding: 5px;
    }
  }

  &-price {
    font-family: $font-PNC;
    font-size: 48px;
    color: $color-grey;
    line-height: 1;
    height: 48px;
    letter-spacing: -2px;
    white-space: nowrap;

    @media #{$mmedia-md} {
      font-size: 32px;
      height: 32px;
      letter-spacing: -.4px;
    }
  }

  &-priceold {
    font-family: $font-PNC;
    font-size: 24px;
    font-weight: 300;
    line-height: 1;
    color: $color-light;
    letter-spacing: -.4px;
    text-decoration-line: line-through;
    white-space: nowrap;

    @media #{$mmedia-md} {
      font-size: 18px;
      letter-spacing: normal;
    }
  }

  &-simushka {
    display: block;
    border: 2px solid $color-red;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    position: relative;
    text-align: center;
    font-family: $font-PN;
    line-height: 24px;
    font-size: 18px;
    color: $color-red;
    font-weight: bold;

    @media #{$mmedia-md} {
      width: 20px;
      height: 20px;
      line-height: 16px;
      font-size: 13px;
    }
  }

  &-name {
    font-size: 16px;
    font-weight: bold;
    color: $color-default;
    line-height: 1.5;
    text-decoration: none;
    text-transform: uppercase;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;

    @media #{$mmedia-md} {
      font-size: 16px;
    }
  }

  &-text {
    display: block;
    font-size: 16px;
    line-height: 1.25;
    margin-bottom: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    text-decoration: none;
    transition: all .3s ease;

    @media #{$mmedia-md} {
      font-size: 14px;
    }

    @media #{$mmedia-md} {
      margin-bottom: 8px;
    }

    &:hover {
      color: $color-red;
    }
  }

  &-bonus {
    overflow: hidden;
    height: 24px;
    font-family: $font-PN;
    margin-top: 16px;
    position: relative;
    text-decoration: none;
    display: flex;

    @media #{$mmedia-md} {
      margin-top: 8px;
    }

    &:hover {
      opacity: .8;
    }

    & > div:first-child {
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
    }

    & > div:last-child {
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
    }

    &-link {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 2;
    }

    &-icon {
      flex: 0 0 24px;
      max-width: 24px;
      background: $color-red url('../images/ic/grafup.svg') 50% 50% no-repeat;
    }

    &-info {
      font-size: 14px;
      line-height: 24px;
      font-weight: bold;
      color: #fff;
      background-color: $color-green;
      padding: 1px 30px 0 8px;
      transition: all 0.3s ease;
      position: relative;

      &::after {
        content: '';
        position: absolute;
        right: 8px;
        top: 4px;
        width: 16px;
        height: 16px;
        mask-repeat: no-repeat !important;
        mask-position: center center !important;
        mask-size: auto 100% !important;
        mask-image: url('../images/ic/ic-smoney.svg');
        background-color: #fff;
      }
    }
  }

  &-volume {
    list-style: none;
    margin: 12px -4px -4px -4px;
    padding: 0;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    overflow: hidden;

    @media #{$mmedia-md} {
      margin: 6px -2px -2px -2px;
    }

    li {
      display: block;
      padding: 4px;

      @media #{$mmedia-md} {
        padding: 2px;
      }
    }

    a {
      display: block;
      border: 1px solid transparent;
      border-radius: 4px;
      padding: 4px 4px 3px 4px;
      font-size: 14px;
      font-weight: 300;
      line-height: 1;
      color: $color-light;
      white-space: nowrap;
      text-decoration: none;

      &:hover {
        color: $color-red;
      }

      &.active {
        border-color: $color-light;

        &:hover {
          border-color: $color-red;
          color: $color-red;
        }
      }
    }
  }

  &-article {
    font-size: 14px;
    line-height: 20px;
    color: $color-grey;
    margin-top: 8px;

    @media #{$mmedia-sm} {
      font-size: 12px;
      line-height: 16px;
    }
  }

  &-check {
    position: absolute;
    padding: 0;
    margin: 0;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    cursor: pointer;
    z-index: 10;

    input[type="checkbox"] {
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;

      &:checked {
        & ~ .pcard-check-bg {
          opacity: .8;
        }

        & ~ .pcard-check-body {
          opacity: 1;
        }
      }
    }

    &-body {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      color: #fff;
      font-family: $font-PN;
      text-align: center;
      opacity: 0;
      transition: all 0.3s ease;
      z-index: 2;
    }

    &-bg {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background: #3ebd6b;
      opacity: 0;
      transition: all 0.3s ease;
      z-index: 1;
    }

    &-number {
      width: 201px;
      height: 201px;
      padding-top: 18px;
      border: 8px solid #fff;
      border-radius: 50%;
      margin: 0 auto 24px auto;
      font-family: $font-PNC;
      font-size: 144px;
      line-height: 175px;
      text-align: center;
      letter-spacing: -2px;

      @media #{$mmedia-md} {
        width: 112px;
        height: 112px;
        margin-bottom: 32px;
        font-size: 72px;
        line-height: 88px;
        padding-top: 10px;
        letter-spacing: -2px;
      }
    }

    &-link {
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.3px;
      text-decoration-line: underline;
      text-transform: uppercase;
    }
  }

  &-noprice {
    font-family: $font-PNC;
    font-size: 24px;
    line-height: 24px;
    color: $color-light;
  }

  .lineinfo {
    position: absolute;
    left: 0;
    top: 0;
    max-width: 100%;
    z-index: 4;

    @media #{$mmedia-md} {
      transform: rotate(-90deg) translateX(-100%);
      margin-left: -4px;
      transform-origin: left top;
    }
  }
}

