            right: 0;
            top: 1px;
            width: 18px;
            height: 18px;
            mask-repeat: no-repeat !important;
            mask-position: center center !important;
            mask-size: auto 100% !important;
            mask-image: url('../images/ic/ic-smoney.svg');
            background-color: $color-default;
            z-index: 2;
          }
        }
      }
    }
  }
}

.norradio {
  position: relative;
  border: 1px solid #cdcdcd;
  border-radius: 4px;
  padding: 20px 30px 20px 80px;
  transition: all 0.3s ease;

  @media #{$mmedia-mm} {
    padding: 20px 16px 20px 52px;
  }

  &:hover {
    border-color: $color-red;
  }

  &-head {
    position: relative;
    display: block;
    padding: 0;
    cursor: pointer;
    margin: 0;

    input[type="radio"] {
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;

      &:checked ~ div {
        &::after {
          background-color: $color-red;
          border-color: $color-red;
        }
      }
    }

    & > div {
      position: relative;

      &::after {
        content: '';
        position: absolute;
        left: -50px;
        top: 2px;
        width: 20px;
        height: 20px;
        border: 1px solid $color-light;
        background-color: $color-white;
        border-radius: 50%;
        transition: all 0.3s ease;

        @media #{$mmedia-mm} {
          left: -37px;
        }
      }
    }
  }

  &-slide {
    display: none;
  }

  .orradio-title {
    margin-bottom: 0;
  }

  .orradio-name {
    padding-top: 4px;
  }

  &-list {
    &-el {
      &.active {
        .norradio-slide {
          display: block;
        }
      }
    }
  }
}

.section-controls {
  margin-bottom: 80px;

  @media #{$mmedia-md} {
    margin-bottom: 85px;

    + .page-title {
      margin-top: -25px;
    }
  }
}

.mb-24 {
  margin-bottom: 24px !important;
}

.mbtn-value {
  font-style: normal;
}

@-webkit-keyframes repaint {
  from {
    width: 99.9%;
  }
  to {
    width: 100%;
  }
}

@-moz-keyframes repaint {
  from {
    width: 99.9%;
  }
  to {
    width: 100%;
  }
}

@keyframes repaint {
  from {
    width: 99.9%;
  }
  to {
    width: 100%;
  }
}

.repaint {
  -webkit-animation: repaint 100ms;
  -moz-animation: repaint 100ms;
  -ms-animation: repaint 100ms;
  animation: repaint 100ms;
}

.sticky-spacer {
  .nofix & {
    display: none !important;
  }
}

.stickMe {
  will-change: transform;

  .nofix & {
    position: relative !important;
    top: auto !important;
    left: auto !important;
  }
}

#bitrix_custom_widget {
  -moz-transition: all 0.3s linear;
  -webkit-transition: all 0.3s linear;
  -o-transition: all 0.3s linear;
  transition: all 0.3s linear;
  position: fixed;
  bottom: 30px;
  width: 80px;
  height: 80px;
  border: 5px solid #df3733;
  z-index: 1042;
  cursor: pointer;
  background: #fff url(../images/content/chat/chat-1.svg);
  background-repeat: no-repeat;
  background-position: 12px 10px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  box-shadow: 0 0 30px 3px #df3733;
  right: -100px;
  opacity: 0;
  visibility: hidden;

  &.shown {
    right: 30px;
    opacity: 1;
    visibility: visible;
  }

  &.rotator {
    background-size: contain;
    background-position: 0 0;
    border-radius: 0;
    border: 0;
    box-shadow: none;
    background-color: transparent;
  }

  &.bg-1 {
    background-image: url(../images/content/chat/chat-1.svg);
  }

  &.bg-2 {
    background-image: url(../images/content/chat/chat-2.svg);
  }

  &.bg-3 {
    background-image: url(../images/content/chat/chat-3.svg);
  }

}

@keyframes rotateOut {
  from {
    opacity: 1;
  }

  to {
    -webkit-transform: rotate3d(0, 0, 1, 200deg);
    transform: rotate3d(0, 0, 1, 200deg);
    opacity: 0;
  }
}

.animate__rotateOut {
  -webkit-animation-name: rotateOut;
  animation-name: rotateOut;
  -webkit-transform-origin: center;
  transform-origin: center;
}

@keyframes rotateIn {
  from {
    -webkit-transform: rotate3d(0, 0, 1, -200deg);
    transform: rotate3d(0, 0, 1, -200deg);
    opacity: 0;
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.animate__rotateIn {
  -webkit-animation-name: rotateIn;
  animation-name: rotateIn;
  -webkit-transform-origin: center;
  transform-origin: center;
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

