.pagecard {
  position: relative;

  h3, .h3 {
    font-family: $font-PNC;
    font-weight: 900;
    margin-bottom: 24px;
    color: $color-grey;
    letter-spacing: normal;

    @media #{$mmedia-md} {
      font-size: 24px;
      margin-bottom: 24px;
    }

    span {
      display: inline-block;
      width: 32px;
      height: 32px;
      line-height: 32px;
      text-align: center;
      color: #fff;
      background-color: $color-red;
      font-family: $font-PN;
      font-weight: bold;
      border-radius: 50%;
      margin-left: 16px;
      font-size: 16px;
      vertical-align: middle;
    }
  }

  &-plusgrey {
    width: 48px;
    height: 48px;
    background: url('../images/system/plusgrey.svg') 50% 50% no-repeat;
    background-size: auto 31px;
    margin-left: auto;
    margin-right: auto;

    @media #{$mmedia-md} {
      width: 56px;
    }
  }

  &-plusgreen {
    width: 58px;
    height: 68px;
    background: url('../images/system/plusgreen.svg') 50% 50% no-repeat;
    background-size: contain;
  }

  &-arrowgreen {
    width: 78px;
    height: 56px;
    background: url('../images/system/arrowgreen.svg') 50% 50% no-repeat;
    background-size: contain;
  }

  &-elsegrey {
    width: 48px;
    height: 48px;
    background: url('../images/system/elsegrey.svg') 50% 50% no-repeat;
    background-size: 48px auto;
    margin-left: auto;
    margin-right: auto;

    @media #{$mmedia-md} {
      width: 64px;
    }
  }

  &-plusgreen {
    width: 48px;
    font-family: $font-PN;
    text-align: center;
    line-height: 1;
    color: transparent;
    -webkit-text-stroke: 2px $color-green;
    font-size: 128px;
    font-weight: 300;
  }

  &-item {
    width: 100%;
    padding-bottom: 88px;

    @media #{$mmedia-md} {
      padding-bottom: 56px;
    }

    &:last-child {
      padding-bottom: 0;
    }
  }

  &-delivery {
    position: relative;

    &-welcome {
      position: relative;

      &-attention {
        padding-left: 37px;
        background: url('../images/ic/ic-attention.svg') 0 0 no-repeat;
        background-size: 20px auto;
        font-family: $font-PN;
        font-size: 14px;
        line-height: 1.43;
        min-height: 18px;

        a {
          text-decoration: underline;
          color: $color-default;

          &:hover {
            text-decoration: underline;
            color: $color-red;
          }
        }
      }

      &-info {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: -4px -8px 12px -8px;
        line-height: 1.75;
        font-size: 16px;

        @media #{$mmedia-mm} {
          font-size: 18px;
          line-height: 1.56;
        }

        & > div {
          flex: 0 0 auto;
          width: auto;
          max-width: 100%;
          padding: 4px 8px;

          @media #{$mmedia-mm} {
            width: 100%;
          }
        }
      }

      &-city {
        color: $color-default;

        b {
          color: $color-red;
        }
      }

      &-btns {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: -4px;
        font-family: $font-PN;
        font-size: 14px;
        line-height: 1.43;
        letter-spacing: 0.3px;

        & > div {
          padding: 4px;
        }

        a {
          display: block;
          text-decoration: underline;
          text-transform: uppercase;
          padding: 0 4px;
          border-radius: 4px;
          margin-left: -4px;

          &:hover {
            color: #fff;
            background-color: $color-default;
            text-decoration: underline;
          }

          &.color-red {
            color: $color-red;

            &:hover {
              background-color: $color-red;
              color: #fff;
            }
          }
        }
      }

      &-more {
        display: inline-block;
        font-family: $font-PN;
        font-size: 14px;
        line-height: 20px;
        text-decoration: underline;
        color: $color-default;

        &:hover {
          color: $color-red;
          text-decoration: underline;
        }
      }

      & + .pagecard-delivery-list {
        padding-top: 40px;

        @media #{$mmedia-md} {
          padding-top: 24px;
        }
      }
    }

    &-list {
      position: relative;
      font-size: 16px;
      line-height: 1.33;

      &_el {
        margin-bottom: 40px;
        position: relative;
        padding-left: 80px;

        @media #{$mmedia-sm} {
          padding-left: 0;
        }

        &:last-child {
          margin-top: 0;
        }
      }

      &_num {
        position: absolute;
        left: 0;
        top: 0;
        padding-left: 38px;
        font-family: $font-PN;
        font-size: 32px;
        line-height: 31px;
        color: $color-red;
        background: url('../images/ic/ic-box.svg') 0 0 no-repeat;
        background-size: auto 30px;

        @media #{$mmedia-sm} {
          position: relative;
          margin-bottom: 16px;
        }
      }

      &_head {
        display: flex;
        align-items: center;
        margin-bottom: 16px;

        &_price {
          flex: 0 0 145px;
          max-width: 145px;
          margin-left: 16px;
          text-align: right;
          font-family: $font-PNC;
          font-size: 32px;
          line-height: 1;
          letter-spacing: -0.4px;
          color: $color-grey;

          @media #{$mmedia-md} {
            flex: 0 0 127px;
            max-width: 127px;
          }
        }

        &_content {
          flex-basis: 0;
          flex-grow: 1;
          max-width: 100%;

          & > span {
            display: block;
            font-family: $font-PN;
            font-size: 24px;
            line-height: 1.33;
            font-weight: bold;

            @media #{$mmedia-md} {
              font-size: 18px;
            }
          }

          & > p {
            margin: 0;
            padding: 0;
            font-size: 16px;
            font-weight: 300;
            line-height: 1.75;
            font-style: italic;

            @media #{$mmedia-md} {
              font-size: 14px;
              line-height: 1.71;
            }
          }
        }
      }

      &_slide {
        position: relative;
        display: none;

        ul {
          list-style: none;
          margin: 0;
        }
      }

      &_more {
        display: block;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        text-decoration: underline;
        font-family: $font-PN;
        color: $color-default;
        font-size: 14px;
        line-height: 1;

        &:hover {
          color: $color-red;
          text-decoration: underline;
        }
      }
    }
  }

  &-reviews {
    position: relative;

    &-info {
      position: relative;
      border: 1px solid $color-line;
      border-radius: 4px;
      padding: 24px 19px 24px 88px;
      min-height: 96px;
      margin-top: 24px;
      font-weight: normal;

      @media #{$mmedia-sm} {
        padding: 24px 16px;
      }

      & > p {
        font-family: $font-PN;
        font-size: 16px;
        line-height: 1.5;

        &:last-child {
          margin-bottom: 0;
        }
      }

      .mbtn {
        margin-top: 16px;
      }

      &-title {
        display: flex;
        align-items: center;
        margin-bottom: 8px;
        font-size: 16px;
        line-height: 32px;

        @media #{$mmedia-sm} {
          margin-bottom: 16px;
          font-size: 14px;
          line-height: 20px;
        }

        &-icon {
          position: absolute;
          left: 16px;
          top: 24px;
          width: 48px;
          height: 48px;
          border-radius: 50%;
          background-color: $color-green;
          display: flex;
          align-items: center;
          justify-content: center;

          @media #{$mmedia-sm} {
            position: relative;
            left: auto;
            top: auto;
            flex: 0 0 48px;
            max-width: 48px;
            margin-right: 16px;
          }

          img {
            display: block;
          }
        }

        &-text {
          display: block;
          font-weight: bold;
        }
      }

      &.status-00 {
        .pagecard-reviews-info-title-icon {
          background-color: transparent;
        }
      }

      &.status-01 {
        .pagecard-reviews-info-title-icon {
          background-color: transparent;
        }
      }

      &.status-02 {
        .pagecard-reviews-info-title-icon {
          background-color: transparent;
        }
      }

      ul {
        list-style: none;
        margin: 0 0 16px 0;
        padding: 0;
        font-family: $font-PN;
        font-size: 16px;
        line-height: 24px;
        font-weight: normal;

        @media #{$mmedia-md} {
          font-size: 14px;
          line-height: 20px;
        }

        &:last-child {
          margin-bottom: 0;
        }

        & > li {
          margin-top: 4px;
          padding-left: 28px;
          background: url('../images/ic/ic-attation.svg') 0 2px no-repeat;

          @media #{$mmedia-md} {
            background-position: 0 1px;
          }

          &:first-child {
            margin-top: 0;
          }

          &.active {
            color: $color-red;
            background-image: url('../images/ic/ic-check.svg');
          }
        }
      }
    }

    &-more {
      position: relative;
      border-top: 1px solid $color-grey;
      border-bottom: 1px solid $color-grey;
      padding-top: 20px;
      padding-bottom: 20px;
      margin-top: 24px;

      a {
        display: inline-block;
        text-transform: uppercase;
        color: $color-default;
        font-family: $font-PN;
        font-size: 14px;
        line-height: 1.17;
        text-decoration: underline;

        &:hover {
          color: $color-red;
          text-decoration: underline;
        }
      }
    }

    &_el {
      margin-top: 24px;
      padding-top: 24px;
      border-top: 1px solid $color-line;

      &:first-child {
        margin-top: 0;
        padding-top: 0;
        border: none;
      }
    }

    &_card {
      display: flex;
      position: relative;

      & + .pagecard-reviews_card {
        padding-left: 64px;
        padding-top: 24px;

        @media #{$mmedia-mm} {
          padding-left: 0;
        }
      }
    }

    &_img {
      flex: 0 0 80px;
      max-width: 80px;
      height: 80px;
      margin-right: 24px;
      background-position: 50% 50% !important;
      background-repeat: no-repeat !important;
      background-size: contain !important;

      @media #{$mmedia-sm} {
        flex: 0 0 48px;
        max-width: 48px;
        height: 48px;
        margin-right: 16px;
      }
    }

    &_noimg {
      flex: 0 0 80px;
      max-width: 80px;
      height: 80px;
      margin-right: 24px;
      background-position: 50% 50% !important;
      background-repeat: no-repeat !important;
      background-size: cover !important;
      background-color: $color-default;
      text-align: center;
      line-height: 80px;
      border-radius: 50%;
      color: #fff;
      font-size: 24px;
      font-weight: 300;

      @media #{$mmedia-sm} {
        flex: 0 0 48px;
        max-width: 48px;
        height: 48px;
        font-size: 14px;
        line-height: 48px;
        margin-right: 16px;
      }

      &.noimg_00 {
        background-image: url('../images/system/bg-noimg-00.jpg');
      }

      &.noimg_01 {
        background-image: url('../images/system/bg-noimg-01.jpg');
      }

      &.noimg_02 {
        background-image: url('../images/system/bg-noimg-02.jpg');
      }

      &.noimg_03 {
        background-image: url('../images/system/bg-noimg-03.jpg');
      }

      &.noimg_04 {
        background-image: url('../images/system/bg-noimg-04.jpg');
      }

      &.noimg_05 {
        background-image: url('../images/system/bg-noimg-05.jpg');
      }

      &.noimg_06 {
        background-image: url('../images/system/bg-noimg-06.jpg');
      }

      &.noimg_07 {
        background-image: url('../images/system/bg-noimg-07.jpg');
      }

      &.noimg_08 {
        background-image: url('../images/system/bg-noimg-08.jpg');
      }

      &.noimg_09 {
        background-image: url('../images/system/bg-noimg-09.jpg');
      }

      &.noimg_10 {
        background-image: url('../images/system/bg-noimg-10.jpg');
      }

      &.noimg_11 {
        background-image: url('../images/system/bg-noimg-11.jpg');
      }
    }

    &_content {
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;
    }

    &_head {
      margin-bottom: 16px;
      font-size: 16px;
      line-height: 1.5;

      @media #{$mmedia-sm} {
        margin-bottom: 8px;
      }

      span {
        display: block;
        font-weight: bold;
        font-size: 18px;
        line-height: 32px;

        @media #{$mmedia-sm} {
          font-size: 16px;
          line-height: 28px;
        }
      }

      p {
        padding: 0;
        margin: 0;
        font-family: $font-PN;
        font-size: 16px;

        @media #{$mmedia-sm} {
          font-size: 14px;
        }
      }
    }

    &_text {
      font-size: 16px;
      font-weight: 300;
      line-height: 1.75;

      @media #{$mmedia-sm} {
        font-size: 14px;
      }

      p {
        margin-bottom: 16px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      h5, .h5 {
        margin-bottom: 4px;
        padding: 0;

        &.likeup {
          position: relative;
          padding-left: 38px;

          @media #{$mmedia-sm} {
            padding-left: 34px;
          }

          &::after {
            content: '';
            position: absolute;
            left: 0;
            top: -7px;
            width: 26px;
            height: 26px;
            background: url('../images/ic/ic-likeup.svg') 0 0 no-repeat;
            background-size: contain;

            @media #{$mmedia-sm} {
              width: 24px;
              height: 24px;
              top: -5px;
            }
          }
        }

        &.likedown {
          position: relative;
          padding-left: 38px;

          &::after {
            content: '';
            position: absolute;
            left: 0;
            top: -2px;
            width: 26px;
            height: 26px;
            background: url('../images/ic/ic-likedown.svg') 0 0 no-repeat;
            background-size: contain;

            @media #{$mmedia-sm} {
              width: 24px;
              height: 24px;
              top: 0;
            }
          }
        }
      }
    }

    &_rating {
      position: relative;
      display: flex;
      align-items: center;

      &_number {
        line-height: 1;
        font-size: 24px;
        font-family: $font-PNC;
        font-weight: bold;
        letter-spacing: -0.75px;
        color: $color-grey;
        padding-left: 8px;
        padding-top: 6px;
        flex: 0 0 auto;
        width: auto;
        max-width: 100%;
      }

      &_stars {
        width: 119px;
        height: 20px;
        background: #fff url('../images/system/reviews-null.svg') 0 0 no-repeat;
        position: relative;

        i {
          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          background: #fff url('../images/system/reviews-full.svg') 0 0 no-repeat;
        }
      }
    }
  }

  &-listdocs {
    list-style: none;
    margin: 0;
    padding: 0;
    font-family: $font-PN;
    font-size: 18px;
    line-height: 28px;
    font-weight: normal;

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 26px;
    }

    & > li {
      margin-top: 8px;
      padding-left: 0 !important;

      &:first-child {
        margin-top: 0;
      }

      &::after {
        display: none;
      }

      a {
        display: inline-block;
        text-decoration: underline;
        color: $color-default;
        position: relative;
        padding-left: 26px;

        &:hover {
          text-decoration: underline;
          color: $color-red;
        }

        &::after {
          content: '';
          position: absolute;
          left: 0;
          top: 3px;
          width: 20px;
          height: 20px;
          background: url('../images/ic/ic-doc.svg') 0 0 no-repeat;
          background-size: contain;
        }
      }
    }
  }

  &-characteristic {
    width: 100%;
    border: 1px solid $color-line;
    font-family: $font-PN;
    font-size: 18px;
    line-height: 24px;
    font-weight: normal;
    margin-top: 16px;

    @media #{$mmedia-sm} {
      font-size: 16px;
      line-height: 20px;
      display: block;
      border: none;
      margin-top: 8px;
    }

    tbody {
      @media #{$mmedia-sm} {
        display: block;
      }
    }

    td {
      border-left: 1px solid $color-line;
      border-top: 1px solid $color-line;
      padding: 8px 16px;

      @media #{$mmedia-sm} {
        display: block;
        border-left: none;
      }

      &:first-child {
        border-left: none;
        background-color: $color-white;
        font-weight: bold;
        width: 236px;

        @media #{$mmedia-sm} {
          width: 100%;
          border-top: none;
        }
      }
    }

    tr {
      @media #{$mmedia-sm} {
        display: block;
        margin-top: 16px;
        border: 1px solid $color-line;
      }

      &:first-child {
        @media #{$mmedia-sm} {
          margin-top: 0;
        }

        td {
          @media #{$mmedia-msm} {
            border-top: none;
          }
        }
      }
    }
  }

  &-listgifts {
    &-wrapper {
      margin-top: -32px;
    }

    &-el {
      margin-bottom: 0;
    }

    .pcard-price {
      font-size: 32px;
      height: 32px;
      letter-spacing: -0.4px;
    }

    .pcard-text {
      font-size: 14px;
      margin-bottom: 8px;
    }

    .pcard-volume {
      margin-top: 4px;
    }

    .pcard-name {
      font-size: 14px;
      line-height: 20px;
    }

    .lcard-img {
      flex: 0 0 128px;
      max-width: 128px;
      height: 125px;

      @media #{$mmedia-md} {
        flex: 0 0 100%;
        max-width: 100%;
        margin-right: 0;
        margin-bottom: 16px;
        height: 146px;
      }
    }

    .lineinfo {
      position: absolute;
      left: 0;
      top: 0;
      height: 24px;
      margin: 0;
    }

    .lcard-body {
      @media #{$mmedia-md} {
        flex-direction: column;
      }
    }
  }

  &-cashback {
    position: relative;
    margin-top: -32px;

    @media #{$mmedia-md} {
      margin-top: 0;
    }

    &-info {
      padding-left: 37px;
      background: url('../images/ic/ic-attention.svg') 0 0 no-repeat;
      background-size: 20px auto;
      font-family: $font-PN;
      font-size: 14px;
      line-height: 1.43;

      &-text {
        position: relative;
        margin-bottom: 8px;

        a {
          text-decoration: underline;
          color: $color-default;

          &:hover {
            text-decoration: underline;
            color: $color-red;
          }
        }
      }

      &-more {
        display: inline-block;
        text-transform: uppercase;
        color: $color-default;
        font-family: $font-PN;
        font-size: 14px;
        line-height: 20px;
        text-decoration: underline;
        letter-spacing: 0.3px;

        &:hover {
          color: $color-red;
          text-decoration: underline;
        }
      }
    }

    &-card {
      position: relative;
      margin-bottom: 24px;
      padding: 24px 40px;
      color: #fff;
      background: $color-green url('../images/system/bg-bonus.jpg') 50% 50% no-repeat;
      border-radius: 4px;
      max-width: 470px;
      font-family: $font-PN;

      @media #{$mmedia-sm} {
        padding: 24px 16px;
      }

      h3, .h3 {
        color: #fff;
      }

      &-des {
        font-size: 14px;
        font-weight: bold;
        line-height: 16px;

        p {
          margin-bottom: 0;

          @media #{$mmedia-sm} {
            margin-bottom: 4px;

            &:last-child {
              margin-bottom: 0;
            }
          }
        }

        a {
          display: inline-block;
          text-transform: uppercase;
          color: #fff;
          font-size: 14px;
          font-weight: normal;
          line-height: 20px;
          text-decoration: underline;
          letter-spacing: 0.3px;
          margin-top: 8px;

          @media #{$mmedia-sm} {
            margin-top: 4px;
          }

          &:hover {
            color: #fff;
            opacity: .8;
            text-decoration: underline;
          }
        }
      }

      &-label {
        display: inline-block;
        font-size: 12px;
        line-height: 16px;
        padding: 4px 8px;
        color: $color-grey;
        text-transform: uppercase;
        font-weight: bold;
        margin-bottom: 8px;
        background-color: #fff;
        border-radius: 4px;
      }

      &-buybunus {
        margin-bottom: 28px;

        .row {
          .col-auto {
            margin-right: 16px;

            &:last-child {
              margin-right: 0;
            }
          }
        }
      }

      &-buyinfo {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        margin: -4px -12px;

        @media #{$mmedia-sm} {
          margin: -4px -12px;
        }

        & > div {
          padding: 4px 12px;

          @media #{$mmedia-sm} {
            padding: 4px 12px;
          }
        }
      }

      &-bigbonus {
        font-family: $font-PNC;
        font-size: 64px;
        line-height: 72px;
        height: 64px;
        display: inline-block;
        padding-right: 66px;
        position: relative;
        background: url('../images/ic/ic-smoney.svg') 100% 4px no-repeat;
        background-size: auto 56px;
        letter-spacing: -3px;

        @media #{$mmedia-sm} {
          font-size: 48px;
          line-height: 48px;
          height: 41px;
          background-position: 100% 1px;
          background-size: auto 40px;
          padding-right: 46px;
          letter-spacing: -0.75px;

        }

        &.plus {
          padding-left: 40px;

          &::after {
            content: '';
            position: absolute;
            left: 0;
            top: 9px;
            width: 48px;
            height: 48px;
            background: url(../images/system/grafsalebonus.svg) 0 50% no-repeat;
            background-size: auto 26px;

