.pcimages {
  position: relative;

  &-sliders {
    position: relative;

    @media #{$mmedia-md} {
      margin-top: -16px;
    }
  }

  &-wrapper {
    position: relative;
  }

  &-sl {
    position: relative;

    &-el {
      padding: 16px;
      background-color: #fff;
      display: none;

      &.swiper-slide {
        display: block;

        &.swiper-slide-active {
          opacity: 1 !important;
        }
      }

      & > span {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 5;
      }
    }

    &-close {
      position: absolute;
      right: 16px;
      top: 16px;
      width: 40px;
      height: 40px;
      background: url('../images/ic/close-red.svg') 50% 50% no-repeat;
      z-index: 10;
      display: none;
    }

    &-more {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      cursor: url('../images/ic/zoomin.svg') 0 0, auto;
      z-index: 10;

      @media #{$mmedia-md} {
        display: none;
      }
    }

    picture {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 380px;

      @media #{$mmedia-md} {
        height: 280px;
      }

      img {
        display: block;
        max-width: 100%;
        max-height: 100%;
      }
    }

    &-fix {
      position: relative;
      height: 412px;
      margin-bottom: 32px;

      @media #{$mmedia-md} {
        margin-bottom: 0;
        height: auto;
      }
    }

    &-wrapper {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      background-color: #fff;
      transition: all 0.3s ease;
      z-index: 10;

      @media #{$mmedia-md} {
        position: relative;
        left: auto;
        top: auto;
      }

      &.active {
        box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
        border-radius: 4px;
        width: 788px;
        height: 730px;

        @media #{$mmedia-lg} {
          width: 608px;
          height: 660px;
        }

        .pcimages-sl {
          cursor: pointer;

          &-el {
            width: 788px !important;

            @media #{$mmedia-lg} {
              width: 608px !important;
            }
          }
        }

        .pcimages-sl-close {
          display: block;
        }

        picture {
          height: 698px;

          @media #{$mmedia-lg} {
            height: 628px;
          }
        }

        .pcimages-sl-more {
          display: none;
        }

        .swiper-button {
          top: 314px;
          margin-top: 0;

          &.swiper-button-prev {
            left: 0;
            margin-left: 16px;
          }

          &.swiper-button-next {
            right: 0;
            margin-right: 16px;
          }
        }
      }
    }
  }

  &-tabssl {
    position: relative;
    width: 256px;
    padding-left: 8px;
    padding-right: 8px;
    margin: -8px auto -24px auto;

    @media #{$mmedia-md} {
      display: none;
    }

    &.slmin .swiper-wrapper {
      justify-content: center;
    }

    &-el {
      display: none;
      width: 80px;
      padding: 8px 8px 24px 8px;

      &.swiper-slide {
        display: block;
      }

      &.swiper-slide-thumb-active {
        span {
          border-color: #fff;
          box-shadow: 0 8px 16px rgba(230, 39, 57, .25);
          cursor: default;
        }
      }

      span {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 64px;
        height: 64px;
        padding: 8px;
        border: 1px solid $color-line;
        border-radius: 4px;
        cursor: pointer;
        transition: all 0.3s ease;

        &:hover {
          border-color: #fff;
          box-shadow: 0 8px 16px rgba(230, 39, 57, .25);
        }

        img {
          display: block;
          max-width: 100%;
          max-height: 100%;
        }
      }
    }
  }
}

