.quantity {
  position: relative;
  height: 52px;
  box-shadow: 0 8px 16px rgba(44, 44, 44, .1);
  border-radius: 32px;
  overflow: hidden;

  &:hover {
    input {
      box-shadow: 0 8px 16px $color-pink;
    }
  }

  &-button {
    position: absolute;
    top: 0;
    cursor: pointer;
    width: 52px;
    height: 52px;
    text-align: center;
    color: #fff;
    user-select: none;
    background-color: $color-default;
    box-shadow: 0 8px 16px rgba(44, 44, 44, .1);
    border-radius: 50%;
    border: none;
    outline: none !important;
    transition: all 0.3s ease;
    z-index: 2;

    &:not(:disabled):hover {
      background-color: $color-red;
    }

    &:disabled {
      background-color: $color-light;
      cursor: default;
    }

    &.quantity-up {
      right: 0;
      background-image: url('../images/system/plus.svg');
      background-position: 50% 50%;
      background-repeat: no-repeat;

      &.rotate {
        animation: rotate .5s linear;
      }
    }

    &.quantity-down {
      left: 0;
      background-image: url('../images/system/minus.svg');
      background-position: 50% 50%;
      background-repeat: no-repeat;

      &.rotateminus {
        animation: rotateminus .5s linear;
      }
    }
  }

  input {
    display: block;
    width: 100%;
    height: 52px;
    line-height: 32px;
    padding: 0 66px;
    text-align: center;
    margin: 0;
    border: none;
    font-family: $font-PN;
    font-size: 24px;
    font-weight: bold;
    color: $color-grey;
    opacity: 0;
    z-index: -100;

    &:focus {
      outline: 0;
    }
  }

  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type=number] {
    -moz-appearance: textfield;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    position: absolute;
    left: 52px;
    top: 0;
    right: 52px;
    display: flex;
    flex-direction: column;
    font-family: $font-PN;
    font-size: 24px;
    font-weight: bold;
    color: $color-grey;
    z-index: 2;

    & > li {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 52px;
    }
  }
}

$bottom: 56px;

.tab-wrapper {
  position: relative;

  .selections-sl {
    opacity: 1;

    @media #{$mmedia-mmd} {

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        bottom: $bottom;
        z-index: 1;
        border-left: 1px solid $color-line;
      }

      &::after {
        content: '';
        position: absolute;
        left: 99.9999%;
        top: 0;
        bottom: $bottom;
        margin-left: -2px;
        border-left: 1px solid $color-line;
      }

      .swiper-wrapper {
        border-top: 1px solid $color-line;
        flex-wrap: wrap;
        transform: none !important;
        padding-bottom: $bottom;
        width: calc(100% - 1px);

        &::before {
          content: '';
          position: absolute;
          bottom: $bottom;
          right: -1px;
          left: -1px;
          background: #fff;
          border-top: 1px solid $color-line;
        }
      }
    }
  }

  .swiper-scrollbar {
    background: #fff;
    height: 4px;
    border-radius: 2px;
    margin-top: 24px;

    @media #{$mmedia-mmd} {
      display: none;
    }
  }

  .swiper-scrollbar-drag {
    background: $color-red;
    border-radius: 2px;
    height: 4px;
  }
}

$tabTime: 2s;
$tabTime: var(--tabSpeed);

.tab-panel {
  @include transition_all(transform $tabTime, opacity calc(#{$tabTime} / 2) linear .1s);

  .catalogblock-el {
    padding-top: 16px;
    padding-bottom: 20px;
  }

  &:not(.active) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    transform: translateX(-100%);
    opacity: 0;
    pointer-events: none;
  }

  &.prev_active {
    ~ .next_active {
      transform: translateX(100%);
    }
  }

  &.next_active {
    transition-duration: 0s;
    transform: translateX(-100%);

    ~ .prev_active {
      transform: translateX(100%);
    }
  }

  &.active {
    transition-duration: $tabTime !important;
    transform: translateX(0) !important;
    opacity: 1;
  }
}

.tab-loader {
  padding-top: 34px;

  @media #{$mmedia-md} {
    padding-top: 22px;
  }
}

.tab-scroller {
  //margin: -4px -4px 0 -4px;

  &.__scroll-x {
    overflow-x: auto;
    padding: 0;

    .scrollbar-track {
      display: none !important;
