.text_xs {
  font-size: 1.2rem;
  line-height: 1.33 !important;

  & + .text_md {
    margin-top: -.5rem;
  }
}

.text_md {
  font-size: 2.6rem;
  line-height: 1.33 !important;
  font-weight: 300;

  & + .text_xs {
    padding-top: 1em;
  }

  & + * .text_xs {
    padding-top: 1em;
  }
}

.text_lg {
  font-size: 4.8rem;
}

.tt_uppercase {
  text-transform: uppercase;
}

.tt_none {
  text-transform: none !important;
}

.font_main {
  font-family: $proxima_nova;
}

.fw_bold {
  font-weight: 700;
}

.fw_light {
  font-weight: 300;
}

.text_muted {
  opacity: .7;
}

@media screen and (max-width: 767px) {
  .text_md {
    font-size: 2.3rem;

    & + .text_xs {
      padding-top: 0;
    }
  }
  .text_lg {
    font-size: 3.8rem;
  }
  .text_max_sm_center {
    text-align: center;
  }
}
