.brandtext {
  position: relative;
  display: flex;
  flex-wrap: wrap;

  &-fparagraph {
    font-family: $font-default;
    font-size: 16px;
    font-weight: 300;
    line-height: 28px;

    @media #{$mmedia-md} {
      font-size: 14px;
      line-height: 24px;
    }
  }
  &-slide {
    position: relative;
    display: none;
    font-size: 16px;
    line-height: 28px;
    padding-top: 20px;

    @media #{$mmedia-md} {
      font-size: 14px;
      line-height: 24px;
    }
  }
  &-more {
    display: block;
    position: relative;
    border-top: 1px solid $color-grey;
    border-bottom: 1px solid $color-grey;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-top: 20px;

    a {
      display: inline-block;
      text-transform: uppercase;
      color: $color-default;
      font-family: $font-PN;
      font-size: 14px;
      line-height: 1.17;
      text-decoration: underline;

      &:hover {
        color: $color-red;
        text-decoration: underline;
      }
      span {
        display: block;
        text-decoration: underline;

        & + span {
          display: none;
        }
      }
      &.active {
        span:first-child {
          display: none;
        }
        span + span {
          display: block;
        }
      }
    }
  }
}
