.mainicatalog {
  position: relative;
  margin-bottom: 64px;

  @media #{$mmedia-md} {
    margin-bottom: 40px;
  }

  &-text {
    font-weight: 300;
    margin-bottom: 16px;

    @media #{$mmedia-md} {
      margin-bottom: 8px;
    }

    a {
      color: $color-red;
      text-decoration: underline;

      &:hover {
        text-decoration: underline;
        color: $color-default;
      }
    }
  }
  &-body {
    list-style: none;
    margin: 0 -8px;
    padding: 0;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    white-space: nowrap;
    -ms-overflow-style: none;

    .mbtn {
      box-shadow: 0 8px 16px rgba(0,0,0,.2);
    }

    @media #{$mmedia-mmd} {
      .mbtn {
        display: block;
        width: 100%;
      }
    }
    @media #{$mmedia-md} {
      width: 100vw;
      margin: 0 0 0 50%;
      padding-top: 8px;
      padding-bottom: 24px;
      transform: translateX(-50%);
      flex-wrap: nowrap;
      overflow-y: hidden;
      overflow-x: auto;

      &::-webkit-scrollbar {
        width:0;
        height: 0;
      }
    }

    li {
      padding: 0 8px;

      @media #{$mmedia-mmd} {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
      }
      @media #{$mmedia-md} {
        padding-left: 4px;

        &:first-child {
          padding-left: 0;
        }
      }
    }
  }
}