.selections {
  position: relative;

  &-nav {
    list-style: none;
    margin: 0;
    padding: 0;
    background-color: $color-dgrey;
    border-radius: 4px 4px 0 0;
    display: flex;

    @media #{$mmedia-md} {
      display: none;
    }

    li {
      position: relative;
      flex: 0 0 33.333333%;
      max-width: 33.333333%;
    }
    a {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      min-height: 96px;
      padding: 8px 19px;
      text-align: center;
      color: #fff;
      text-decoration: none;
      border-radius: 4px;

      &:hover {
        background-color: $color-grey;
        color: #fff;
      }
      span {
        display: block;
        font-size: 18px;
        font-weight: bold;
        line-height: 1.78;
      }
      i {
        display: block;
        font-style: normal;
        text-transform: uppercase;
        font-family: $font-PN;
        font-size: 12px;
        line-height: 1.33;
      }
    }
  }
  &-content {
    border-left: 1px solid $color-line;
    position: relative;

    @media #{$mmedia-md} {
      border: none;
    }

    .mbtn-primary {
      @media #{$mmedia-md} {
        width: 100%;
      }
    }
  }
  &-plane {
    position: absolute;
    top: -96px;
    margin-left: -1px;
    width: 33.33333%;
    background-color: $color-white;
    border: 1px solid $color-line;
    border-radius: 4px 4px 0 0;
    z-index: 5;

    @media #{$mmedia-md} {
      display: none;
    }
  }
  &-cnav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 72px;
    padding: 8px 19px;
    text-align: center;
    color: #fff;
    text-decoration: none;
    border-radius: 4px;
    background-color: $color-default;

    &:hover {
      background-color: $color-grey;
      color: #fff;
    }
    span {
      display: block;
      font-family: $font-default;
      font-size: 18px;
      font-weight: bold;
      line-height: 1.78;
    }
    i {
      display: block;
      font-style: normal;
      text-transform: uppercase;
      font-family: $font-PN;
      font-size: 12px;
      line-height: 1.33;
    }

    @media #{$mmedia-mmd} {
      display: none;
    }
  }
  &-item {
    position: relative;
    display: none;

    @media #{$mmedia-md} {
      display: block;
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0 !important;
      }
    }

    &.tabc00 {
      .selections-blockdes {
        left: 0;
      }
    }
    &.tabc01 {
      .selections-blockdes {
        left: 33.33333%;
      }
    }
    &.tabc02 {
      .selections-blockdes {
        left: 66.66666%;
      }
    }

    &.active {
      display: block;

      @media #{$mmedia-md} {
        margin-bottom: 27px;

        .selections-body {
          display: block;
        }
      }

      .selections-cnav {
        display: none;
      }
    }
  }
  &-body {
    @media #{$mmedia-md} {
      display: none;
    }
  }
  &-btns {
    padding-top: 32px;
  }
  &-sl {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    opacity: 0;
    transition: all 0.5s ease;

    @media #{$mmedia-md} {
      flex-wrap: nowrap;
      opacity: 1 !important;
    }

    &.swiper-container {
      @media #{$mmedia-mmd} {
        overflow: inherit;
      }
      @media #{$mmedia-md} {
        margin-left: 0;
        margin-right: 0;
      }
    }
    &-content {
      position: relative;

      @media #{$mmedia-md} {
        width: 100vw;
        margin: 0 0 0 50% !important;
        transform: translateX(-50%);
      }
    }
    &-wrapper {
      position: relative;
    }
    &-el {
      border-right: 1px solid $color-line;
      border-bottom: 1px solid $color-line;
      flex: 0 0 33.33333%;
      max-width: 33.33333%;
      display: flex !important;
      height: auto;
      padding: 16px;
      position: relative;
      transition: all .1s ease;
      z-index: 1;

      @media #{$mmedia-md} {
        flex: 0 0 auto;
        max-width: 232px;
        border-bottom: none;

        &:last-child {
          border-top: none;
          border-right: none;
        }
        &.el-emptiness {
          display: none !important;
        }
      }
      &:hover {
        z-index: 2;
      }
    }
    .swiper-wrapper {
      @media #{$mmedia-mmd} {
        flex-wrap: wrap;
        transform: none !important;
      }
    }
  }
  &-blockdes {
    padding: 0 22px 22px 22px;
    position: absolute;
    top: -96px;
    width: 33.33333%;
    opacity: 0;
    z-index: 5;

    @media #{$mmedia-md} {
      position: relative;
      left: auto !important;
      top: auto;
      width: 100%;
      opacity: 1 !important;
      background-color: $color-white;
      border: 1px solid $color-line;
      border-radius: 4px;
      padding: 24px 16px 42px 16px;
    }

    &-img {
      width: 80px;
      height: 80px;
      border-radius: 50%;
      margin: 0 auto;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-size: cover;
    }
    &-name {
      font-weight: 300;
      font-size: 16px;
      line-height: 1.75;
      color: $color-dgrey;
      text-align: center;
    }
    &-post {
      font-family: $font-PN;
      font-size: 14px;
      line-height: 1.43;
      margin-bottom: 16px;
      color: $color-dgrey;
      overflow: hidden;
      text-align: center;
    }
    &-text {
      font-size: 14px;
      line-height: 1.75;
      font-weight: 300;
      margin-bottom: 16px;
    }
    &-title {
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      min-height: 96px;
      margin-bottom: 8px;

      @media #{$mmedia-md} {
        min-height: 1px;
        margin-bottom: 32px;
      }

      span {
        display: block;
        font-size: 18px;
        font-weight: bold;
        line-height: 1.78;
      }
      i {
        display: block;
        font-style: normal;
        text-transform: uppercase;
        font-family: $font-PN;
        font-size: 12px;
        line-height: 1.33;
      }
    }
    &-more {
      text-transform: uppercase;
      font-family: $font-PN;
      letter-spacing: 0.3px;
      font-size: 14px;
      line-height: 1.43;
      text-decoration: underline;
      color: $color-red;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  .swiper-container-horizontal>.swiper-scrollbar {
    left: 16px;
    width: auto;
    right: 16px;
    bottom: 0;
  }
  .swiper-scrollbar {
    background: #fff;
    height: 4px;
    border-radius: 2px;

    @media #{$mmedia-mmd} {
      display: none;
    }

    &-drag {
      background: $color-red;
      border-radius: 2px;
      height: 4px;
    }
  }
}
