.catalogthemes {
  position: relative;
  margin-bottom: 88px;

  @media #{$mmedia-md} {
    margin-bottom: 56px;
  }

  &-item {
    margin-top: 56px;

    @media #{$mmedia-md} {
      margin-top: 40px;
    }

    &:first-child {
      margin-top: 0;
    }
  }
  &-goods {
    list-style: none;
    margin: 0 -12px;
    padding: 24px 0 0 0;
    position: relative;
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    &::after {
      content: '';
      position: absolute;
      left: 12px;
      right: 12px;
      bottom: 0;
      height: 1px;
      background-color: $color-grey;
      transition: all 0.3s ease;
      z-index: 1;
    }
    & > li {
      padding: 0 12px 16px 12px;
      flex: 0 0 10%;
      max-width: 10%;

      @media #{$mmedia-sm} {
        flex: 0 0 20%;
        max-width: 20%;

        &:nth-child(n+6) {
          display: none;
        }
      }
    }
    &-img {
      height: 96px;
      display: flex;
      align-items: center;
      justify-content: center;

      @media #{$mmedia-md} {
        height: 58px;
      }

      img {
        display: block;
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
}
.catalogselections {
  position: relative;

  &-item {
    margin-top: 56px;
    position: relative;

    &:first-child {
      margin-top: 0;
    }
    &:hover {
      h2 a {
        color: $color-red;
      }
      .catalogselections-goods::after {
        background-color: $color-red;
      }
      .catalogselections-goods-number {
        color: $color-red;
        border-color: $color-red;
      }
    }
  }
  &-body {
    display: flex;
    flex-wrap: wrap;
  }
  &-link {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 5;
  }
  &-side {
    flex: 0 0 auto;
    width: auto;
    max-width: 100%;
    margin-right: 24px;

    @media #{$mmedia-md} {
      margin-right: 0;
      flex: 0 0 100%;
      max-width: 100%;
      padding-left: 16px;
      padding-right: 16px;
      border-left: 1px solid $color-grey;
      border-right: 1px solid $color-grey;
    }
  }
  &-content {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    padding-left: 24px;
    border-left: 1px solid $color-grey;

    @media #{$mmedia-md} {
      padding-left: 16px;
      padding-right: 16px;
      border-right: 1px solid $color-grey;
    }
  }
  &-goods {
    list-style: none;
    padding: 16px 0;
    margin: 0 -12px;
    display: flex;
    align-items: center;
    position: relative;

    &:hover::after {
      background-color: $color-red;
    }

    @media #{$mmedia-md} {
      padding-top: 4px;
    }

    &::after {
      content: '';
      position: absolute;
      left: 24px;
      right: 24px;
      bottom: 0;
      height: 1px;
      background-color: $color-grey;
      transition: all 0.3s ease;
    }
    &-number {
      border: 1px solid $color-light;
      border-radius: 4px;
      padding: 0 8px;
      font-family: $font-PNC;
      font-size: 16px;
      font-weight: normal;
      line-height: 26px;
      color: $color-light;
      text-decoration: none;
      transition: all 0.3s ease;

      &:hover {
        color: $color-red;
        border-color: $color-red;
        text-decoration: none;
      }
      span {
        display: inline-block;
        margin-left: 4px;

        @media #{$mmedia-sm} {
          display: none;
        }
      }
    }
    & > li {
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;
      padding-left: 12px;
      padding-right: 12px;

      &:last-child {
        flex: 0 0 auto;
        width: auto;
        max-width: 100%;
      }
    }
    &-img {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 167px;

      @media #{$mmedia-md} {
         height: 120px;
      }
      @media #{$mmedia-sm} {
         height: 58px;
      }

      img {
        display: block;
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
}