.hero {
  overflow: hidden;

  @media #{$mmedia-mmd} {
    padding: 16px 0;

    .swiper-container {
      height: 567px;

      &.__education {
        height: 600px;
      }
    }
  }

  @media #{$mmedia-mlg} {
    .swiper-container {
      height: 505px;

      &.__education {
        //height: 540px;
      }
    }
  }

  &-actions-card {

    &.__spacer .education-school {
      margin-bottom: 17px;
    }

    @media #{$mmedia-md} {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 20px 16px 5px;

      &.mob-white {
        &::before {
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
        }

        .education-school,
        .actions-card-name {
          color: #fff;
        }
      }

      &.mob-black {
        &::before {
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
        }

        .education-school,
        .actions-card-name {
          color: $color-default !important;
        }
      }
    }
  }

  .container {
    position: relative;
  }

  //@media #{$mmedia-mlg} {
  //}

  &-bullet_line {
    @include transition(width, var(--bulletLineDelay), linear);
    width: 0;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    border-radius: 2px;
    background: $color-red;

    &.__run {
      width: 100%;
    }
  }

  &-slider {
    position: relative;

    &_wrapper {
      //margin-right: -43%;
      margin: 0 -78%;

      @media #{$mmedia-msm} {
        //margin-left: 0;
        //margin-right: -79%;
        //margin-left: -89.5%;
      }

      @media #{$mmedia-mmd} {
        margin: 0 -54%;

        //margin-left: 0;
        //margin-right: -79%;
        //margin-left: -89.5%;
      }

      @media #{$mmedia-mlg} {
        //margin-right: -51%;
        //margin-left: -75.5%;
      }
    }

    &_wievport {
      position: relative;
      overflow: hidden;
      margin: 0 -8px;

      &::after,
      &::before {

        @media #{$mmedia-msm} {
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          width: 1px;
          background: $color-grey;
        }

        @media #{$mmedia-mmd} {
          bottom: 18px;
        }
      }

      &::before {
        left: 0;
      }

      &::after {
        right: 0;
      }

      .swiper-slide {
        padding-left: 8px;
        padding-right: 8px;
        position: relative;

        .actions-card {
          @include transition(opacity, var(--heroSliderSpeed));

          &_event {
            @extend %global_flex_block_column_nowrap_center_center;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            width: 182px;
            background: $color-red;
            color: #fff;
            font-family: $font-PNC;

            @media #{$mmedia-md} {
              flex-flow: row wrap;
              text-align: center;
              border-radius: 4px;
            }

            &--day {
              padding: 0 8px;
              font-size: 64px;
              line-height: 56px;
              letter-spacing: -.01em;

              @media #{$mmedia-md} {
                font-size: 32px;
                line-height: 1;
                margin-top: auto;
              }
            }

            &--month {
              font-size: 32px;
              line-height: 1;
              letter-spacing: -.01em;

              @media #{$mmedia-md} {
                font-size: 32px;
                line-height: 1;
                margin-top: auto;
              }
            }

            &--location {
              width: 100%;
              margin-top: 15px;
              font: bold 16px/24px $font-PN;
              text-align: center;

              @media #{$mmedia-md} {
                margin-top: 4px;
                margin-bottom: auto;
                font-size: 14px;
                line-height: 20px;
              }
            }
          }
        }

        &.swiper-slide-prev,
        &.swiper-slide-next {
          .actions-card {
            opacity: .2;
          }
        }

        .actions-card-img {
          height: 455px;
          margin-bottom: 0;

          @media #{$mmedia-md} {
            span {
              width: 159px;
              height: 190px;
            }
          }

          @media #{$mmedia-mmd} {
            height: 235px;
            margin-bottom: 22px;
            width: 410px;

            span {
              top: 0;
              bottom: 0;
              left: 100%;
              margin-left: 14px;
              right: auto;
              width: 233px;
            }
          }
        }

        @media #{$mmedia-sm} {
          .actions-card {
            border-radius: 4px;
            overflow: hidden;
          }
        }

        .actions-card-header {
          top: 10px;

          @media #{$mmedia-msm} {
            left: 16px;
          }
        }

        .actions-card-name {
          font: 900 32px/34px $font-PNC;
          margin-bottom: 12px;
          letter-spacing: 0;
          color: $color-grey;

          @media #{$mmedia-md} {
            font-size: 24px;
            line-height: 28px;
            color: #fff;
            z-index: 1;
            margin-bottom: 7px;
            -webkit-line-clamp: 3;
          }
        }

        .actions-card-text {
          font-family: $font-default;
          font-size: 16px;

          @media #{$mmedia-mlg} {
            -webkit-line-clamp: 5;
          }

          @media #{$mmedia-md} {
            color: #fff;
            font-size: 14px !important;
            line-height: 24px !important;
            z-index: 1;
            -webkit-line-clamp: unset;
          }

          @include iphone-only() {
            color: #fff;
            font-size: 14px !important;
            line-height: 24px !important;
            z-index: 1;
            -webkit-line-clamp: unset;
          }
        }

        .mbtn {
          @media #{$mmedia-md} {
            display: none;
          }
        }
      }

      @media #{$mmedia-md} {
        .__education .swiper-slide {
          @media #{$mmedia-md} {
            .hero-actions-card {
              bottom: 79px;
            }

            .actions-card-img {
              height: 455px - 87px;
              margin-bottom: 87px;

              > span {
                width: 100%;
                top: 100%;
                left: 0;
                right: 0;
                height: 79px;
                margin-top: 8px;
              }

              .actions-card_event {
                width: 100%;
              }
            }
          }
        }
      }

      @media #{$mmedia-mmd} {
        margin-left: -80px;
        margin-right: -80px;

        .swiper-slide {
          padding-left: 80px;
          padding-right: 80px;
          padding-bottom: 18px;

          &::before {
            content: '';
            position: absolute;
            right: 0;
            bottom: 18px;
            height: 100vh;
            border-right: 1px solid $color-grey;
          }
        }
      }
    }
  }

  .swiper-pagination {
    @extend %global_flex_block_row_nowrap_space_between_stretch;
    padding: 16px 0 24px;
    margin: 0 -4px;

    .swiper-pagination-bullet {
      @include transition(background, .3s);
      flex: 1 0 auto;
      margin: 0 4px;
      text-align: center;
      background: $color-line;
      font-size: 0;
      height: 4px;
      border-radius: 2px;
      position: relative;
      cursor: pointer;

      &::before {
        content: '';
        position: absolute;
        top: -20px;
        bottom: -20px;
        left: 0;
        right: 0;
      }

      &:hover:not(.swiper-pagination-bullet-active) {
        background: $color-grey;
      }

      &.swiper-pagination-bullet-active {
        cursor: default;
      }
    }

    @media #{$mmedia-msm} {
      padding: 22px 0 40px;
    }
  }

  .swiper-button {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 50vw;
    background: rgba(255, 255, 255, .0001);
    z-index: 2;

    @media #{$mmedia-mmd} {
      bottom: 18px;
    }

    &-prev {
      right: 100%;
      margin-right: calc(-15% + 8px);
      //margin-right: -8px;

      @media #{$mmedia-msm} {
        margin-right: calc(-12.8% + 8px);
      }

      @media #{$mmedia-md} {
        background: url('../images/system/arrow-left.svg') no-repeat right center;
      }

      @media #{$mmedia-mlg} {
        cursor: url('../images/system/arrow-left.svg'), auto;
        margin-right: calc(-10.25% - 16px);
      }

      &:hover {
        ~ .hero-slider_wievport {
          .swiper-slide-prev .actions-card {
            opacity: .5;
          }
        }
      }
    }

    &-next {
      left: 100%;
      margin-left: calc(-15% + 8px);

      @media #{$mmedia-msm} {
        margin-left: calc(-12.8% + 8px);
      }

      @media #{$mmedia-mmd} {
        //margin-left: calc(-23.5% - 16px);
      }

      @media #{$mmedia-md} {
        background: url('../images/system/arrow-right.svg') no-repeat left center;
      }

      @media #{$mmedia-mlg} {
        cursor: url('../images/system/arrow-right.svg'), auto;
        margin-left: calc(-10.25% - 16px);
      }

      &:hover {
        ~ .hero-slider_wievport {
          .swiper-slide-next .actions-card {
            opacity: .5;
          }
        }
      }
    }
  }
}

