.video_wrap {
  margin: 0 auto;
  max-width: 94rem;
  background: $brand_dark;

  &.blog_mod {
    margin: 5rem (-9.6rem) 1.6rem;
    max-width: none;
    @include bp($point_2) {
      margin: 5rem (-5rem) 1.6rem;
    }
    @include bp($point_3) {
      margin: 5rem (-2rem) 2.4rem;
    }
    @include bp($point_4) {
      margin: 2.6rem (-2rem) 0;
    }
  }
}

.video_player {
  width: 100%;
  height: 53rem;
  @include bp($point_2) {
    height: 46rem;
  }
  @include bp($point_3) {
    height: 28rem;
  }
  @include bp($point_4) {
    height: 18rem;
  }
}

.video_desc {
  padding: 4rem 9rem 5rem;
  color: $white;
  font-family: $second_font;
  @include bp($point_2) {
    padding: 4rem 7.8rem;
  }
  @include bp($point_3) {
    padding: 3.2rem 2rem;
    text-align: center;
  }
}

.video_desc_title {
  font-size: 3.6rem;
  font-weight: 700;
  @include bp($point_2) {
    font-size: 3.2rem;
  }
  @include bp($point_3) {
    font-size: 2.6rem;
  }
}

.video_desc_text {
  padding: 1.6rem 0 0 0;
  font-family: $font_1;
  font-size: 1.5rem;
  line-height: 1.7;
  @include bp($point_2) {
    font-size: 1.4rem;
  }
}

.video-block {
  overflow: hidden;
  position: relative;
  display: block;
  border: none;
}

.video-block__icon-wrap {
  @include flex_block_5(row, nowrap, center, center);
  width: 7.4rem;
  height: 7.4rem;
  border-radius: 50%;
  background: $color-red;
  transform: translateX(-50%) translateY(-50%);
  position: absolute;
  top: 50%;
  left: 50%;

  &::before {
    @extend %hor_vert_centr;
    content: '';
    position: absolute;
    border-top: 18px solid transparent;
    border-bottom: 18px solid transparent;
    border-left: 22px solid #fff;
    margin-left: 4px;

    @media #{$mmedia-md} {
      border-top: 8px solid transparent;
      border-bottom: 8px solid transparent;
      border-left: 12px solid #fff;
      margin-left: 2px;
    }
  }
}

.video-block-wrap {
  iframe {
    height: 100%;
    width: 100%;
  }
}

.icon.video-block__icon {
  font-size: 24px;
  color: #fff;
}

@media screen and (max-width: 767px) {
  .video-block__icon-wrap {
    width: 4rem;
    height: 4rem;
  }
  .icon.video-block__icon {
    font-size: 18px;
  }
}
