.brands {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  font-family: $font-PN;
  margin-top: -20px;
  margin-bottom: -20px;
  z-index: 2;

  @media #{$mmedia-md} {
    margin-top: -20px;
    margin-bottom: -20px;
  }

  .page-education & {
    margin-top: -15px;
    margin-bottom: 30px;
  }

  &-logo {
    @include transition_all(background .3s);
    background: $color-red;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    //margin-right: 8px;
    overflow: hidden;
    flex: 0 0 auto;

    &.__school {
      background: none;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
    }

    a:hover > & {
      //background-color: transparent;
    }

    @media #{$mmedia-md} {
      width: 24px;
      height: 24px;
    }
  }

  &-letter {
    display: block;
    font-size: 12px;
    line-height: 17px;
    padding: 0 4px;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    background-color: $color-red;
    color: #fff;
    border-radius: 4px;
    min-width: 16px;
    position: absolute;
    left: -40px;
    top: 50%;
    margin-top: -8px;
    z-index: -2;

    @media #{$mmedia-md} {
      left: -32px;

      .page-education & {
        left: -28px;
      }
    }
  }

  &-name {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    color: $color-dark;
    text-decoration: none;
    text-transform: uppercase;
    flex: 0 0 auto;
    width: auto;
    max-width: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: 24px;
    min-height: 32px;
    line-height: 32px;

    @media #{$mmedia-md} {
      margin-top: 12px;
      margin-bottom: 12px;
      margin-right: 12px;
      min-height: 24px;
      line-height: 24px;
    }

    .page-education & {
      max-width: calc(100% - 24px);

      @media #{$mmedia-md} {
        margin-top: 20px;
      }
    }

    &:hover {
      color: $color-red;

      & > i {
        border-color: $color-red;
        color: $color-red;
      }
    }

    &.letterhover {
      &::after {
        background-color: $color-red;
      }
    }

    &.name-wletter {
      margin-left: 40px;

      @media #{$mmedia-md} {
        margin-left: 32px;
      }

      &::after {
        left: -40px;

        @media #{$mmedia-md} {
          left: -32px;
        }
      }
    }

    &.name-lastinletter {
      margin-right: 40px;

      @media #{$mmedia-md} {
        margin-right: 24px;
      }

      &::after {
        right: 0;
      }
    }

    &.name-lastvisible {
      @media #{$mmedia-md} {
        & ~ .brands-name {
          display: none;
        }
        & ~ .brands-name.active {
          display: flex;
        }
      }
    }

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -16px;
      height: 1px;
      right: -24px;
      background-color: $color-dark;
      transition: all 0.3s ease;
      z-index: 1;

      @media #{$mmedia-md} {
        bottom: -8px;
      }
    }

    .brands-caption {
      display: block;
      font-size: 24px;

      @media #{$mmedia-md} {
        font-size: 18px;
      }
    }

    & > span {
      display: block;
      font-size: 24px;
      width: auto;
      max-width: 100%;

      @media #{$mmedia-md} {
        font-size: 18px;
      }
    }

    .brands-counter,
    & > i {
      display: block;
      font-style: normal;
      font-weight: 300;
      color: $color-light;
      border: 1px solid $color-light;
      font-family: $font-PNC;
      font-size: 24px;
      line-height: 1;
      padding: 4px 6px 2px 6px;
      border-radius: 4px;
      min-width: 28px;
      text-align: center;
      margin-left: 16px;
      transition: all 0.3s ease;

      @media #{$mmedia-md} {
        font-size: 18px;
        min-width: 24px;
        margin-left: 8px;
        padding-top: 3px;
        padding-bottom: 1px;
      }
    }

    .scrollbar-track {
      //display: none !important;
    }
  }

  &-more {
    width: 100%;
    display: none;
    margin-top: 16px;

    @media #{$mmedia-md} {
      display: block;
    }

    a {
      display: inline-block;
      font-size: 14px;
      line-height: 1.43;
      letter-spacing: 0.3px;
      color: $color-red;
      text-transform: uppercase;

      &:hover {
        text-decoration: underline;
        color: $color-dark;
      }
    }
  }

  &-scroll {
    flex: 0 1 auto;
    white-space: nowrap;
    position: relative;

    .webkit-scroll & {
      padding-left: 8px;

      .brands-scroll_inner {
        max-width: calc(100vw - 100px);
        overflow-y: auto;

        &::-webkit-scrollbar {
          display: none;
        }
      }

      .brands-counter,
      .brands-caption {
        display: inline-block;
        vertical-align: middle;
      }
    }

    @media #{$mmedia-md} {
      &.has-overflow-x {
        &::after,
        &::before {
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          width: 16px;
          z-index: 1;
          background: linear-gradient(270deg, #fff 0%, rgba(255, 255, 255, 0) 100%);
        }

        &::before {
          left: 0;
          transform: rotate(180deg);
        }

        &::after {
          right: 0;
        }
      }
    }

    .scrollbar-track {
      display: none !important;
    }

    .scroll-content {
      @extend %global_flex_block_row_nowrap_flex_start_center;
      padding-left: 8px;
    }
  }
}

