.mflcatalog {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  padding-left: 60px;
  z-index: 201;
  transform: translateX(100%);
  transition: transform 0.5s;

  @media #{$mmedia-mmd} {
    display: none !important;
  }

  &.active {
    transform: translateX(0);
  }

  .filter-fsearch {
    margin-left: 16px;
    margin-right: 16px;
  }
  &-body {
    position: absolute;
    left: 60px;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    background-color: #fff;
    box-shadow: -10px 0 20px rgba(0,0,0,.4);
    display: flex;
    flex-direction: column;
    z-index: 1;
  }
  &-content {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    overflow-x: hidden;
    overflow-y: scroll;

    .smooth-scroll & {
      padding-bottom: 32px;
    }
  }
  &-btn {
    flex: 0 0 76px;
    max-height: 76px;
    padding: 0 16px 24px 16px;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: -32px;
      height: 32px;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
      z-index: 5;
    }
  }
  &-mobileclose {
    outline: none !important;
    padding: 0;
    border: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 60px;
    height: 64px;
    background: $color-red url('../images/ic/close.svg') 50% 50% no-repeat;
    z-index: 10;

    &:hover {
      background-color: $color-redlight;
    }
  }
  &-title {
    min-height: 84px;
    line-height: 1;
    padding: 16px 16px 1px;

    & > span {
      display: block;
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -0.75px;
      color: $color-default;
      font-family: $font-PNC;
    }
  }
  &-clear {
    margin-top: 5px;
    margin-left: -4px;
    display: none;

    button {
      display: inline-block;
      outline: none;
      border: none;
      padding: 0 4px;
      background-color: transparent;
      border-radius: 4px;
      line-height: 20px;
      text-transform: uppercase;
      text-decoration: underline;
      font-family: $font-PN;
      font-size: 12px;
      letter-spacing: 0.3px;
      color: $color-red;

      &:hover {
        color: $color-white;
        background-color: $color-grey;
      }
    }
  }
  &-menu {
    padding: 0 6px 24px 16px;
    font-family: $font-PN;

    &-item {
      margin-top: 6px;
      border: 1px solid transparent;
      border-radius: 4px;

      &:first-child {
        margin-top: 0;
      }
      &.active {
        border-color: $color-red;

        .mflcatalog-menu-head {
          color: $color-red;
          background-color: transparent;
          padding-bottom: 0;
          //font-weight: bold;

          &::after {
            background-color: $color-red;
            margin-top: -2px;
            transform: rotate(180deg);
          }
        }
        .mflcatalog-menu-slide {
          display: block;
        }
      }
    }
    &-head {
      display: block;
      position: relative;
      font-family: $font-PN;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: 0.3px;
      text-transform: uppercase;
      color: $color-default;
      padding: 16px 72px 16px 16px;
      background-color: $color-white;
      border-radius: 4px;

      &:hover {
        border-color: $color-red;
        &::after {
          background-color: $color-red;
        }
      }
      &::after {
        content: '';
        position: absolute;
        right: 16px;
        width: 20px;
        height: 20px;
        top: 50%;
        margin-top: -10px;
        mask-repeat: no-repeat;
        mask-position: center center;
        mask-size: contain;
        background-color: $color-grey;
        mask-image: url('../images/ic/arrow-down.svg');
        transition: all 0.3s ease;
        z-index: 1;
      }

      .filter-list-ihead-status {
        position: absolute;
        right: 50px;
        top: 23px;
      }
      .filter-list-ihead-number {
        position: absolute;
        right: 50px;
        top: 20px;
      }
    }
    &-padd {
      padding-top: 5px;
    }
    &-slide {
      padding: 0 6px 14px 16px;
      display: none;
    }
    .filter-fcatalog {
      padding-top: 16px;

      &::after {
        content: '';
        position: absolute;
        left: 4px;
        right: 4px;
        top: 0;
        height: 1px;
        background-color: $color-red;
      }
    }
  }

  .flmenu {
    padding-left: 16px;
    padding-right: 16px;
  }
}
