.seotext {
  position: relative;
  display: flex;
  flex-wrap: wrap;

  &-side {
    flex: 0 0 200px;
    max-width: 200px;
    margin-right: 24px;

    @media #{$mmedia-md} {
      margin-right: 0;
      flex: 0 0 100%;
      max-width: 100%;
    }
  }
  &-content {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    padding-left: 24px;
    padding-right: 24px;
    border-left: 1px solid $color-grey;
    border-right: 1px solid $color-grey;

    @media #{$mmedia-md} {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &-slide {
    overflow: hidden;
    position: relative;

    &.active {
      &.slactive {
        height: 268px;

        @media #{$mmedia-md} {
          height: 416px;
        }
        &::after {
          content: '';
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          height: 32px;
          background: linear-gradient(180deg, rgba(255,255,255,0) 0%, #FFF 100%);
          z-index: 5;
        }
      }

      & + .seotext-more {
        display: block;
      }
    }
  }
  &-more {
    display: none;
    position: relative;
    border-top: 1px solid $color-grey;
    border-bottom: 1px solid $color-grey;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-top: 20px;

    a {
      display: inline-block;
      text-transform: uppercase;
      color: $color-default;
      font-family: $font-PN;
      font-size: 14px;
      line-height: 1.17;

      &:hover {
        color: $color-red;
        text-decoration: underline;
      }
      span {
        display: block;
        text-decoration: underline;

        & + span {
          display: none;
        }
      }
      &.active {
        span:first-child {
          display: none;
        }
        span + span {
          display: block;
        }
      }
    }
  }
}