.content_holder {
  font-size: 1.6rem;
  font-family: $second_font;
  color: #4a4a4a;

  p {
    margin: 0 0 .8em;
    line-height: 1.8;

    & + h2 {
      padding-top: 1em;
    }

    & + .text_columns,
    & + .columns {
      padding-top: 2.2em;
    }

    & + ul {
      padding-top: .5em;
    }

    & + p.danger_highlighted_wrap {
      padding-top: 1.7rem;
    }

    &:last-of-type {
      margin-bottom: 0;

      & + ul {
        padding-top: .8em;

        &.feedback__rating {
          padding-top: .6rem;
        }
      }
    }
  }

  ul {
    line-height: 1.8;
  }

  h1,
  h2 {
    font-family: $proxima_nova;
  }
}

p.danger_highlighted_wrap {
  font-family: $proxima_nova;
  font-size: 2rem;

  span {
    background-color: #fad4d8;
    padding: 8px 18px;
    margin: 0;
    display: inline-block;
    vertical-align: top;
  }
}

.ai-flex-end {
  align-items: flex-end;
}

a.link_base {
  color: #4a4a4a;
  border-bottom: .1rem solid rgba(#4a4a4a, 0.4);
  text-decoration: none !important;

  &:hover,
  &:focus {
    border-bottom-color: $brand_red;
    text-decoration: none;
    color: $brand_red;
  }
}

@media screen and (max-width: 767px) {
  .content_holder {
    font-size: 1.4rem;

    p {
      & + p.danger_highlighted_wrap {
        padding-top: 1rem;
      }

      & + .text_columns {
        padding-top: 1.4em;
      }
    }
  }
  p.danger_highlighted_wrap {
    font-size: 1.6rem;
    line-height: 1.4;

    span {
      margin: 0 -1.2rem;
      padding: 1.1rem 1.2rem;
    }
  }
}

