ul.triangle_list {

  li {
    position: relative;
    padding: 0 0 2.3rem 4.7rem;

    &::before {
      position: absolute;
      content: '';
      width: 1.6rem;
      height: 1.6rem;
      top: .6rem;
      left: 0;
      background-color: $brand_red;
    }

    &:last-child {
      padding-bottom: 0;
    }
  }

  & + h2 {
    padding-top: 1em;
  }

}

ul.dots_grey {
  li {
    position: relative;
    padding: 0 0 18px 25px;
    line-height: 25px;

    &::before {
      border-radius: 50%;
      -webkit-border-radius: 50%;
      position: absolute;
      content: '';
      width: 1rem;
      height: 1rem;
      top: .9rem;
      left: 0;
      background-color: rgba(#4a4a4a, .5);
    }

    &:last-child {
      padding-bottom: 0;
    }
  }
}

@media screen and (max-width: 767px) {
  ul.triangle_list li {
    padding: 0 0 2.3rem 0;

    &::before {
      width: 1rem;
      height: 1rem;
      top: .7rem;
      left: -2.2rem;
    }
  }
  ul.dots_grey {
    li {
      padding: 0 0 2rem 2.2rem;

      &::before {
        top: .8rem;
      }
    }
  }
}
