.page404 {
  position: relative;
  z-index: 2;

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    top: 136px;
    width: 562px;
    height: 264px;
    background: url('../images/system/bg-404.jpg') 0 -56px no-repeat;
    -webkit-background-size: 100% auto;
    z-index: -1;

    @media #{$mmedia-md} {
      top: 24px;
      left: 50%;
      margin-left: -344px;
      width: 357px;
      height: 203px;
      background-position: 0 0;
    }
    @media #{$mmedia-sm} {
      margin-left: -254px;
    }
    @media #{$mmedia-mm} {
      left: 0;
      margin-left: 16px;
    }
  }
  &-content {
    @media #{$mmedia-md} {
      padding-top: 97px;
    }
  }
  &-title {
    font-family: $font-PNC;
    font-weight: 900;
    font-size: 64px;
    line-height: 64px;
    color: $color-grey;
    margin-bottom: 32px;

    @media #{$mmedia-md} {
      font-size: 32px;
      line-height: 32px;
      margin-bottom: 24px;
    }
  }
  &-text {
    max-width: 608px;
    font-weight: 300;
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 40px;

    @media #{$mmedia-md} {
      font-size: 16px;
      line-height: 28px;
    }

    p {
      margin-bottom: 16px;

      @media #{$mmedia-md} {
        margin-bottom: 8px;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &-item {
    margin-bottom: 24px;

    &.page404-mb56 {
      margin-bottom: 56px;
    }
  }
  &-sl {
    position: relative;

    @media #{$mmedia-mmd} {
      display: flex;
      border-top: 1px solid $color-line;
      border-left: 1px solid $color-line;
      overflow: initial !important;
    }
    @media #{$mmedia-md} {
      width: 100vw;
      margin-left: 50%;
      transform: translateX(-50%);
      border: none;
    }

    &-el {
      border-right: 1px solid $color-line;
      border-bottom: 1px solid $color-line;
      padding: 16px;

      @media #{$mmedia-mmd} {
        flex: 0 0 33.33333%;
        max-width: 33.33333%;
        height: auto !important;
      }
      @media #{$mmedia-md} {
        border: none;
        width: 229px;
        border-right: 1px solid $color-line;

        &:last-child {
          border: none;
        }
      }
      @media #{$mmedia-sm} {
        width: 178px;
      }
    }
    .pcard-img {
      @media #{$mmedia-md} {
        height: 180px;
      }
    }
    &-wrapper {
      position: relative;
      padding-bottom: 16px;

      .swiper-container-horizontal>.swiper-scrollbar {
        left: 16px;
        width: auto;
        right: 16px;
        bottom: 0;
      }
      .swiper-scrollbar {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        background: #fff;
        height: 4px;
        border-radius: 2px;

        @media #{$mmedia-mmd} {
          display: none;
        }

        &-drag {
          background: $color-red;
          border-radius: 2px;
          height: 4px;
        }
      }
    }
  }
}