.pcontacts {
  position: relative;
  display: flex;
  flex-wrap: wrap;

  &-content {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    order: 1;

    @media #{$mmedia-md} {
      width: 100%;
    }
  }

  &-side {
    flex: 0 0 333px;
    max-width: 333px;
    margin-top: -24px;
    margin-bottom: -32px;
    margin-left: 80px;
    background-color: #fff;
    position: relative;
    z-index: 2;
    order: 2;

    @media #{$mmedia-md} {
      position: relative;
      flex: 0 0 100%;
      max-width: 100%;
      background-color: #fff;
      padding-top: 32px;
      padding-bottom: 32px;
      margin-top: 56px;
      margin-bottom: 0;
      margin-left: 0;
      z-index: 15;
    }

    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 32px;
      bottom: 42px;
      width: 114px;
      box-shadow: -10px 0 20px $color-white;
      z-index: -2;

      @media #{$mmedia-md} {
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        width: calc(100vh - 128px);
        transform: translateX(-50%);
        margin-left: 50%;
        box-shadow: 0 0 160px rgba(79, 79, 79, .2);
        border-radius: 40px;
      }
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-color: #fff;
      width: 100vh;
      transform: translateX(-50%);
      margin-left: 50%;
      display: none;
      z-index: -1;

      @media #{$mmedia-md} {
        display: block;
      }
    }
  }

  &-aside {
    position: relative;
    padding-top: 24px;
    padding-left: 24px;
    padding-bottom: 32px;
    width: 333px;
    background-color: #fff;
    z-index: 2;
    max-height: 100%;

    &__scroller {
      overflow-y: auto;
      max-height: 100%;
      padding-right: 10px;
      margin-right: 10px;
    }

    &__extra {
      @media #{$mmedia-md} {
        display: none;
      }
    }

    .pcontacts-aside-address_route {
      max-width: none;
    }

    @media #{$mmedia-md} {
      width: 100%;
      padding-top: 0;
      padding-left: 0;
      padding-bottom: 0;
      order: 2;
    }

    .ltitle {
      margin-bottom: 4px;
    }

    &-address {
      position: relative;

      &_text {
        font-weight: 300;
        font-size: 16px;
        line-height: 28px;
        margin-bottom: 16px;
      }

      &_info {
        font-family: $font-PN;
        font-size: 14px;
        line-height: 20px;

        b {
          font-weight: bold;
        }
      }

      &_route {
        font-family: $font-PN;
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 16px;
        padding: 16px 16px 16px 44px;
        background: $color-bggreen url('../images/ic/ic-map.svg') 16px 16px no-repeat;
        min-height: 52px;
        max-width: 264px;

        a {
          display: inline-block;
          text-decoration: underline;
          color: $color-default;

          &:hover {
            color: $color-red;
            text-decoration: underline;
          }
        }
      }
    }

    &-title {
      font-family: $font-PNC;
      font-weight: 900;
      font-size: 32px;
      line-height: 32px;
      margin-bottom: 4px;
      color: $color-grey;
    }

    &-text {
      font-family: $font-PN;
      font-weight: bold;
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 24px;
      color: $color-grey;
    }

    &-email {
      position: relative;

      a {
        display: inline-block;
        font-weight: 300;
        font-size: 16px;
        line-height: 28px;
        text-decoration: underline;
        color: $color-red;
        padding: 0 4px;
        margin-left: -4px;
        border-radius: 4px;

        &:hover {
          color: #fff;
          text-decoration: underline;
          background-color: $color-red;
        }
      }
    }

    &-phone {
      margin-bottom: 4px;

      & > a {
        display: inline-block;
        font-weight: 300;
        font-size: 16px;
        line-height: 28px;
        text-decoration: none;
        color: $color-red;
        padding: 0 4px;
        margin-left: -4px;
        border-radius: 4px;
        position: relative;

        &::after {
          content: '';
          position: absolute;
          left: 4px;
          right: 4px;
          bottom: 4px;
          height: 1px;
          background-color: $color-red;
          transition: all 0.3s ease;
          z-index: 1;
        }

        &:hover {
          color: #fff;
          text-decoration: none;
          background-color: $color-red;

          &::after {
            background-color: #fff;
          }
        }
      }

      &_status {
        font-family: $font-PN;
        margin-bottom: 4px;
        overflow: hidden;

        & > span {
          display: block;
          float: left;
          position: relative;
          font-size: 12px;
          line-height: 16px;
          padding: 4px 8px 4px 24px;
          border-radius: 4px;
          color: #fff;

          &::after {
            content: '';
            position: absolute;
            left: 8px;
            top: 50%;
            margin-top: -4px;
            width: 8px;
            height: 8px;
            background-color: #fff;
            border-radius: 50%;
            z-index: 2;
          }

          &.green {
            background-color: $color-green;
          }

          &.red {
            background-color: $color-red;
          }
        }
      }

      &_info {
        font-family: $font-PN;
        font-size: 14px;
        line-height: 20px;
      }
    }

    &-item {
      margin-bottom: 24px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &-tocontacts {
    font-family: $font-PN;
    font-size: 14px;
    line-height: 20px;
    padding: 16px 16px 16px 44px;
    background: $color-bggreen url('../images/ic/loop-conctacts.svg') 16px 16px no-repeat;
    min-height: 52px;
    margin-bottom: 32px;

    a {
      display: inline-block;
      text-decoration: underline;
      color: $color-default;

      &:hover {
        color: $color-red;
        text-decoration: underline;
      }
    }
  }

  &-text {
    font-weight: 300;
    font-size: 18px;
    line-height: 32px;
    margin-bottom: 40px;

    @media #{$mmedia-md} {
      margin-bottom: 24px;
    }
  }

  &-list {
    position: relative;

    & > div {
      margin-top: 16px;

      &:first-child {
        margin-top: 0;
      }
    }

    &-item {
      &.wform {
        border: 1px solid $color-line;
        border-radius: 4px;
        overflow: hidden;
        transition: all 0.3s ease;

        &:hover {
          border-color: $color-red;
          box-shadow: 0 8px 16px transparentize($translucent-red, .84);
        }
      }
    }

    &-head {
      font-family: $font-PN;
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 7px;
      font-weight: bold;
      padding: 24px 18px 0 18px;

      @media #{$mmedia-sm} {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 13px;
      }
    }

    &-content {
      position: relative;
      padding: 0 18px 24px 18px;
    }
  }

  &-mwinput {
    max-width: 303px;

    @media #{$mmedia-sm} {
      max-width: 100%;
    }
  }

  &-form {
    background-color: #fff;
    display: none;

    .forminput {
      margin-top: -9px;
    }

    .nonumberorder {
      display: none;
    }
  }

  &-menu {
    list-style: none;
    margin: 0;
    padding: 0;
    position: relative;
    z-index: 5;

    & > li {
      padding: 5px 0;

      &.active {
        .pcontacts-menu_more {
          font-weight: bold;

          & > a {
            color: $color-default;
            text-decoration: none !important;
            cursor: default;

            &::after {
              background-color: $color-red;
            }
          }
        }
      }
    }

    &_more {
      font-family: $font-PN;
      font-size: 16px;
      line-height: 20px;
      transition: all 0.3s ease;
      position: relative;

      & > a {
        display: inline-block;
        padding-left: 30px;
        text-decoration: underline;
        color: $color-default;

        &::after {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 21px;
          height: 20px;
          mask-repeat: no-repeat !important;
          mask-position: center center !important;
          mask-size: contain !important;
          background-color: $color-grey;
          mask-image: url('../images/ic/ic-check.svg');
          transition: all 0.3s ease;
          z-index: 1;
        }

        &:hover {
          color: $color-red;
          text-decoration: underline;
        }
      }
    }

    &_back {
      padding-left: 30px;
      font-family: $font-PN;
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 24px;
      margin-top: -4px;

      & > a {
        display: inline-block;
        text-decoration: underline;
        color: $color-default;

        &:hover {
          text-decoration: underline;
          color: $color-red;
        }
      }
    }
  }

  &-sent {
    padding: 76px 33px 20px 33px;
    background: $color-bggreen url('../images/ic/mail-submit.svg') 50% 20px no-repeat;
    font-family: $font-PN;
    font-size: 16px;
    line-height: 24px;
    border-radius: 4px;

    @media #{$mmedia-md} {
      padding-left: 20px;
      padding-right: 20px;
    }

    span {
      display: block;
      text-align: center;
      margin-bottom: 24px;
      font-family: $font-PN;
      font-weight: bold;
      font-size: 24px;
      line-height: 32px;

      @media #{$mmedia-sm} {
        font-size: 20px;
        line-height: 28px;
      }
    }

    p strong {
      font-family: $font-default;
      font-weight: 300;
      font-size: 16px;
      line-height: 28px;
    }

    &-back {
      position: relative;
      font-family: $font-PN;
      font-size: 14px;
      line-height: 20px;

      & > a {
        display: inline-block;
        color: $color-default;
        text-decoration: underline;

        &:hover {
          color: $color-red;
          text-decoration: underline;
        }
      }
    }
  }
}
