.lbrands {
  position: relative;

  &-directions {
    padding-bottom: 88px;

    @media #{$mmedia-mmd} {
      padding-bottom: 60px;
    }
  }

  &-title {
    color: $color-grey;
    font: 400 64px/56px $font-PNC;
    padding-bottom: 5px;
    border-bottom: 1px solid $color-grey;
    letter-spacing: -.02em;

    .row + & {
      margin-top: 60px;
    }

    .lbrands-item + & {
      margin-top: 80px;
    }

    @media #{$mmedia-md} {
      font-size: 48px;
      line-height: 1;

      .lbrands-item + & {
        margin-top: 40px;
      }
    }
  }

  &-item {
    display: flex;
    flex-wrap: wrap;
    padding-top: 56px;

    @media #{$mmedia-md} {
      padding-top: 40px;
    }

    &:first-child {
      padding-top: 0;
    }

    .page-education & {
      margin-bottom: 24px;
      padding-top: 0;
      flex-wrap: nowrap;

      .education-school__wrapper {
        flex: 1 0 auto;
        padding-bottom: 10px;

        .education-school_location {
          font-size: 16px;
          line-height: 24px;
        }

        @media #{$mmedia-md} {
          flex: 1 1 auto;
          padding-bottom: 13px;

          .education-school_location {
            font-size: 14px;
            line-height: 20px;
          }

          &.mob-small {
            padding-bottom: 0;

            .education-school {
              flex-flow: row nowrap;
              align-items: flex-start;
            }

            .education-school_logo {
              width: 56px;
              height: 56px;
              margin-right: 10px;
            }

            .education-school_name {
              font-size: 16px;
              line-height: 20px;
              letter-spacing: -0.01em;
            }

            .education-school_location {
              font-size: 12px;
              line-height: 16px;
            }
          }
        }
      }

      .lbrands-letter {
        + .education-school__wrapper {
          padding-bottom: 16px;
          border-bottom: 1px solid $color-grey;
        }
      }

      &.__school .education-school_logo {
        background: $color-red;
      }

      .education-school_name {
        font-size: 24px;
        line-height: 32px;
      }

      &.new-letter {
        margin-top: 40px;
      }

      @media #{$mmedia-md} {
        .education-school {
          flex-flow: column nowrap;
          align-items: flex-start;
        }


        .education-school_logo {
          margin-bottom: 16px;
        }
      }

      @media #{$mmedia-mmd} {
        margin-bottom: 30px;
        align-items: center;

        .education-school_logo {
          width: 128px;
          height: 128px;
          margin-right: 24px;
        }

        .lbrands-letter {
          margin-bottom: 20px;
        }

        &.new-letter {
          margin-top: 56px;
        }
      }
    }
  }

  &-letter {
    flex: 0 0 71px;
    max-width: 71px;
    height: 72px;
    text-align: center;
    line-height: 72px;
    color: #fff;
    background-color: $color-red;
    border-radius: 4px;
    font-family: $font-PN;
    font-weight: bold;
    font-size: 24px;
    margin-right: 88px;

    @media #{$mmedia-md} {
      flex: 0 0 45px;
      max-width: 45px;
      height: 48px;
      line-height: 48px;
      font-size: 18px;
      margin-right: 24px;
    }

    &:empty {
      opacity: 0;
      visibility: hidden;
    }
  }

  &-content {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
  }

  &-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-left: -24px;
    margin-right: -24px;
    margin-top: -32px;

    @media #{$mmedia-sm} {
      margin-top: -24px;
    }

    &-el {
      padding-left: 24px;
      padding-right: 24px;
      flex: 0 0 50%;
      max-width: 50%;
      position: relative;
      padding-top: 32px;

      @media #{$mmedia-sm} {
        flex: 0 0 100%;
        max-width: 100%;
        padding-top: 24px;
      }

      &:hover {
        .lbrands-list-title {
          color: $color-red;
        }

        .lbrands-list-number {
          color: $color-red;
          border-color: $color-red;
        }

        .lbrands-list-goods::after {
          background-color: $color-red;
        }
      }
    }

    &-link {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 5;
    }

    &-title {
      text-transform: uppercase;
      font-family: $font-PN;
      font-size: 24px;
      line-height: 1.33;
      margin-bottom: 16px;
      color: $color-grey;
      transition: all 0.3s ease;
    }

    &-goods {
      list-style: none;
      margin: 0 -8px;
      padding: 0 0 16px 0;
      position: relative;
      display: flex;
      align-items: center;
      text-align: center;

      @media #{$mmedia-md} {
        padding-bottom: 9px;
      }

      &::after {
        content: '';
        position: absolute;
        left: 8px;
        right: 8px;
        bottom: 0;
        height: 1px;
        background-color: $color-grey;
        transition: all 0.3s ease;
        z-index: 1;
      }

      & > li {
        padding: 0 8px;
        flex: 0 0 20%;
        max-width: 20%;
      }
    }

    &-number {
      display: inline-block;
      font-style: normal;
      color: $color-light;
      border: 1px solid $color-light;
      font-family: $font-PNC;
      font-size: 16px;
      line-height: 1;
      padding: 6px 6px 4px 6px;
      border-radius: 4px;
      min-width: 24px;
      text-align: center;
      transition: all 0.3s ease;
    }

    &-img {
      height: 104px;
      display: flex;
      align-items: center;
      justify-content: center;

      @media #{$mmedia-lg} {
        height: 72px;
      }
      @media #{$mmedia-md} {
        height: 56px;
      }

      img {
        display: block;
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
}

.brandsalph {
  list-style: none;
  margin: -2px -2px 38px -2px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;

  @media #{$mmedia-md} {
    width: 100vw;
    margin: 0 0 21px 50%;
    padding-bottom: 17px;
    transform: translateX(-50%);
    flex-wrap: nowrap;
    overflow-y: hidden;
    overflow-x: auto;

    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }

  & > li {
    padding: 0 2px;

    &.active {
      a:not(.brandsalph-disabled) {
        cursor: default;
        color: #fff;
        background-color: $color-red;
      }
    }

    @media #{$mmedia-mmd} {
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;
    }

    @media #{$mmedia-md} {
      padding-left: 4px;

      &:first-child {
        padding-left: 0;
      }
    }

    & > a {
      display: block;
      padding: 0 8px;
      min-height: 30px;
      line-height: 72px;
      font-size: 18px;
      font-family: $font-PN;
      color: $color-default;
      text-align: center;
      text-decoration: none;
      border-radius: 4px;

      @media #{$mmedia-mmd} {
        .sticky & {
          line-height: 48px;
        }

        &:not(.brandsalph-disabled):hover {
          color: #fff;
          background-color: $color-red;
        }
      }

      &.brandsalph-disabled {
        cursor: default;
        color: $color-light;
      }
    }
  }

  &-spacer {
    height: 72px;

    @media #{$mmedia-md} {
      height: 48px;
    }
  }

  &-holder {
    padding-top: 38px;
    padding-bottom: 0;

    @media #{$mmedia-md} {
      padding-top: 44px;
    }
  }

  &-container {
    position: relative;
    margin-bottom: -52px;
    padding-bottom: 92px;

    .sticky-spacer {
      display: none !important;
    }

    @media #{$mmedia-md} {
      padding-bottom: 62px;
    }

    .scrollbar-track {
      display: none !important;
    }

    &.scroll-fixed {
      .brandsalph-wrapper {
        position: fixed;
        z-index: 6;

        .container {
          box-shadow: 0 8px 16px rgba(44, 44, 44, 0.1);
        }

        @media #{$mmedia-md} {
          transform: translateY(calc(var(--currentVH) - 80px));

          .scroll-up & {
            transition: none;
          }
        }

        @media #{$mmedia-mmd} {
          transform: translateY(-80px);

          .scroll-down & {
            transform: translateY(80px);
          }
        }
      }

      @media #{$mmedia-md} {
        &:not(.scroll-fixed-end) .brandsalph > li > a {
          line-height: 56px;
        }
      }

      @media #{$mmedia-mmd} {
        &:not(.scroll-fixed-end) .brandsalph > li > a {
          line-height: 48px;
        }
      }

      &.scroll-fixed-end {
        .brandsalph-wrapper {
          position: absolute;
          top: auto !important;
          transform: none;
          transition: none;
          bottom: 0;

          .container {
            box-shadow: none;
          }
        }
      }
    }
  }

  &-wrapper {
    //@include transition_all(transform .6s);
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    &:not(.sticky) {
      margin-top: 0 !important;
      bottom: 0 !important;
      max-height: 72px;

      @media #{$mmedia-md} {
        max-height: 56px;

        .webkit-scroll & {
          .container {
            margin-left: -4px;
          }
        }
      }
    }

    @media #{$mmedia-md} {

      .webkit-scroll & {
        .container {
          overflow-x: auto;

          &::-webkit-scrollbar {
            display: none;
          }
        }
      }
    }

    &.sticky {
      z-index: 6;
      transition: 0s !important;
      transform: none !important;

      @media #{$mmedia-md} {
        top: auto !important;
        bottom: 70px !important;
        min-height: 48px;
      }

      .container {
        box-shadow: 0 8px 16px rgba(44, 44, 44, 0.1);
        max-width: 100vw;
        width: auto;
        display: inline-block;
        vertical-align: top;
        left: 50%;
        transform: translate(-50%, 0);
        position: absolute;
        top: 0;

        .brandsalph {
          padding: 0 16px;
        }
      }

      &:not(.sticky-end) {
        @media #{$mmedia-md} {
          .container {
              //transform: translateY(calc(var(--currentVH) - 160px)) !important;
              //margin-top: calc(var(--currentVH) - 80px) !important;
          }
        }

        .brandsalph > li > a {
          //line-height: 48px;

          @media #{$mmedia-md} {
            //line-height: 56px;
            padding: 0 10px;
          }
        }
      }
    }

    .scrollbar-track {
      display: none !important;
    }

    .brandsalph {
      margin: 0;
      padding: 0;
      transform: none;
      max-width: 100%;
      overflow: visible;
      flex-wrap: nowrap;
      background: #fff;

      > li {
        padding: 0 3px !important;
        background: #fff;

        &:first-child {
          border-radius: 8px 0 0 8px;
        }

        &:last-child {
          border-radius: 0 8px 8px 0;
        }

        @media #{$mmedia-md} {
          > a {
            line-height: 48px;
          }
        }
      }
    }

    .container {
      pointer-events: all;
      padding: 0;

      //@media #{$mmedia-md} {
      //  max-width: none;
      //}
    }

    &.text-center {
      .container {
        width: auto;
        display: inline-block;
        vertical-align: top;
      }

      .brandsalph {
        width: auto;
        max-width: 100vw;
      }
    }
  }
}

.link-tip {
  display: block;
  padding: 0 9px;
  font: 700 12px/16px $font-PN;
  color: #fff;
  background: $color-red;
  border-radius: 4px;
  text-transform: uppercase;
  margin: auto auto auto 8px;
}

