.columns {
  @include flex_block_5(row, wrap, flex-start, flex-start, flex-start);
  margin: 0 -1rem -2rem;

  & + p {
    padding-top: 3rem;
  }

  & + p.text_md {
    padding-top: 2.2em;
  }

  & + .s_seminar__section-head {
    padding-top: 6rem;
  }
}

.text_columns {
  @include flex_block_5(row, wrap, flex-start, flex-start, flex-start);
  margin: 0 -2.5rem -2rem;

  & + .text_columns {
    padding-top: 2.5em;
  }

  & + h2 {
    padding-top: 2.2em;
  }

  & + p {
    padding-top: 1em;
  }

  h2 {
    & + p {
      margin-top: -1.5rem;
    }
  }
}

[class^='col_'] {
  padding: 0 1rem 2rem;
}

.text_col {
  padding-right: 2.5rem;
  padding-left: 2.5rem;
}

.double_list_columns {
  column-count: 2;
  column-gap: 6rem;

  li,
  h2,
  ul {
    -webkit-column-break-inside: avoid;
    break-inside: avoid-column;
  }

  & + p {
    padding-top: 1.8em;
  }
}

.col_xs_6 {
  width: 50%;
}

[class^='col_'] {
  max-width: 100%;
}

@media screen and (min-width: $point-3) {
  .col_sm_8 {
    width: 66.6%;
  }
  .col_sm_7 {
    width: 58.33%;
  }
  .col_sm_6 {
    width: 50%;
  }
  .col_sm_5 {
    width: 41.6%;
  }
  .col_sm_4 {
    width: 33.3%;
  }
  .col_sm_3 {
    width: 25%;
  }
}

@media screen and (min-width: $point-2) {
  .col_md_12 {
    width: 100%;
  }
  .col_md_8 {
    width: 66.6%;
  }
  .col_md_7 {
    width: 58.33%;
  }
  .col_md_6 {
    width: 50%;
  }
  .col_md_5 {
    width: 41.6%;
  }
  .col_md_4 {
    width: 33.3%;
  }
  .col_md_3 {
    width: 25%;
  }
}

@media screen and (max-width: $point-2) {
  .double_list_columns {
    column-count: 1;
    column-gap: 0;
  }
  .text_columns {
    margin-left: -1.5rem;
    margin-right: -1.5rem;
    margin-bottom: -1.6em;
  }
  .text_col {
    padding-right: 1.5rem;
    padding-left: 1.5rem;
    padding-bottom: 1.6em;
  }
}

@media screen and (max-width: 767px) {
  .columns {
    flex-wrap: wrap;
    margin-right: -.5rem;
    margin-left: -.5rem;
    margin-bottom: -1.5rem;

    & + .s_seminar__section-head {
      padding-top: 3.4rem;
    }
  }
  [class^='col_'] {
    padding-right: .5rem;
    padding-left: .5rem;
    width: 100%;
  }
  .col_xs_6 {
    width: 50%;
  }
  .text_columns {
    margin-right: -.5rem;
    margin-left: -.5rem;

    & + .text_columns {
      padding-top: 3.5rem;
    }
  }
  .text_col {
    padding-right: .5rem;
    padding-left: .5rem;
  }
}
