.breadcrumb {
  list-style: none;
  margin: 0 0 40px 0;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  font-family: $font-PN;
  font-size: 14px;
  line-height: 1.43;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  white-space: nowrap;

  @media #{$mmedia-md} {
    margin-bottom: 32px;

    &-scroller {
      .webkit-scroll & {
        overflow-x: auto;

        &::-webkit-scrollbar {
          display: none;
        }
      }
    }
  }

  &.fixbread:not(.has-scroll) {
    @media #{$mmedia-mmd} {
      .breadcrumb-item {
        display: none;

        &:first-child,
        &:last-child {
          display: block !important;
        }

        &:last-child {
          padding-left: 30px;
          background: url('../images/ic/bread-mini.svg') 0 50% no-repeat;
        }
      }
    }
  }

  &-item {
    @extend %global_flex_block_row_nowrap_flex_start_center;
    padding-right: 11px;
    margin-right: 8px;
    position: relative;

    [class^="icon-"], [class*=" icon-"] {
      margin-right: 3px;
      font-size: 1.25em;
    }

    @media #{$mmedia-md} {
      display: none;
      max-width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &.active {
      max-width: 100%;
    }

    .breadcrumb:not(.has-scroll) & {
      &:nth-last-child(-n+2) {
        @media #{$mmedia-md} {
          display: block;
          padding-right: 0;
          margin-right: 0;
          padding-left: 11px;

          &::after {
            right: auto;
            left: 0;
            margin-top: -6px;
            transform: rotate(180deg);
          }
        }
      }

      &:last-child {
        @media #{$mmedia-md} {
          display: none;
        }
      }
    }

    &:last-child {
      padding-right: 0;
      margin-right: 0;

      &::after {
        display: none;
      }
    }

    &::after {
      content: '';
      position: absolute;
      right: 0;
      top: 50%;
      margin-top: -5px;
      width: 10px;
      height: 10px;
      background: url('../images/ic/arrow-breadcrumbs.svg') 100% 50% no-repeat;
      background-size: contain;
    }

    a {
      display: block;
      white-space: nowrap;
      text-decoration: underline;

      &:hover {
        color: $color-red;
        text-decoration: underline;
      }
    }

    .breadcrumb-scroller & {
      display: flex !important;
      flex: 0 0 auto;

      &::after {
      }
    }

    &:last-child {
      .smooth-scroll & {
        min-width: 1px;
        flex: 0 1 auto;
      }

      .webkit-scroll & {
        @media #{$mmedia-mmd} {
          min-width: 1px;
          flex: 0 1 auto;
        }
      }

      span {
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
}

.breadcrumb-scroller {
  .scrollbar-track {
    display: none !important;
  }
}

