.globalmenu {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 200;
  overflow: hidden;
  transform: translateX(100%);
  transition: transform 0.3s;

  &.active {
    @media #{$mmedia-md} {
      transform: translateX(0);

      & ~ .header {
        transform: translate(calc(-100% + 60px), 0);
      }
      & ~ .header-deskmbtn {
        transform: translateY(-100%);
        opacity: 0;
        //visibility: hidden;
      }
    }
  }
  &.msignup {
    .globalmenu-body {
      overflow: hidden;
    }
    .globalmenu-signup {
      transform: translateX(0);
    }
    .globalmenu-item {
      opacity: 0;
    }
  }
  &-body {
    position: absolute;
    left: 60px;
    top: 0;
    right: 0;
    bottom: 0;
    padding: 24px 16px;
    background-color: $color-default;
    box-shadow: -10px 0px 20px rgba(0,0,0,.4);
    overflow-x: hidden;
    overflow-y: auto;
  }
  &-logo {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 32px;
    margin-bottom: 32px;

    img {
      display: block;
      height: 100%;
    }
  }
  &-tosimushka {
    display: inline-block;
    position: relative;
    text-transform: uppercase;
    font-family: $font-PN;
    font-size: 18px;
    line-height: 1.33;
    padding-left: 35px;
    text-decoration: none;
    color: #fff;

    &:hover {
      color: #fff;
    }
    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 26px;
      height: 26px;
      border-radius: 50%;
      background: $color-red url('../images/ic/logo_s.svg') 50% 50% no-repeat;
      background-size: 10px auto;
    }
  }
  &-user {
    position: relative;
    margin-bottom: 32px;
    white-space: nowrap;

    .mbtn {
      display: flex !important;
      align-items: center;
      justify-content: center;
      max-width: 100%;
    }

    &-name {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      min-width: 1px;
    }
    &-bonus {
      line-height: 20px;
      padding: 0 8px;
      background-color: $color-red;
      color: #fff;
      border-radius: 4px;
      margin-left: 8px;
    }
  }
  &-item {
    margin-bottom: 32px;
    transition: all 0.3s ease;

    &:last-child {
      margin-bottom: 0;
    }
    .ltitle {
      margin-bottom: 16px;
    }
  }
  &-signup {
    position: absolute;
    left: 0;
    top: 141px;
    right: 0;
    bottom: 0;
    background-color: $color-default;
    transform: translateX(100%);
    transition: transform 0.3s;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 5;

    &::after {
      content: '';
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      background: linear-gradient(to bottom, rgba(44,44,44,1), rgba(44,44,44,0));
      height: 32px;
      z-index: 20;
    }

    .msignup-body {
      overflow-x: hidden;
      overflow-y: auto;
      padding: 32px 16px 16px 16px;
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
    }
  }

  .lmenu {
    a {
      color: #fff;
    }
    & + .lmenu {
      margin-top: 12px;
    }
  }

  .mbtn.activemsignup {
    background: $color-red url('../images/system/bg-mtitle.jpg') 100% 50% no-repeat;
    background-size: 100% auto;
    text-align: left;
    justify-content: flex-start;
    font-weight: normal;
    padding-left: 42px;
    color: #fff;
    border-color: transparent;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      left: 15px;
      top: 50%;
      margin-top: -11px;
      width: 12px;
      height: 22px;
      background: url('../images/ic/arrow-mmenu.svg') 50% 50% no-repeat;
      background-size: contain;
      z-index: 1;
    }
  }
  .mbtn-white {
    border: none;
    padding: 16px 14px;
  }
  .mbtn-white:hover, .mbtn-white:focus {
    border-color: transparent;
    background-color: #fff;
  }
}
.mcatalog {
  list-style: none;
  padding: 0;
  margin: 0;
  position: relative;

  & > li {
    margin-top: 8px;
    height: 60px;
    transition: all 0.5s ease;

    &:first-child {
      margin-top: 0;
    }
    &:nth-child(2) > a {
      top: calc(1*68px);
    }
    &:nth-child(3) > a {
      top: calc(2*68px);
    }
    &:nth-child(4) > a {
      top: calc(3*68px);
    }
    &:nth-child(5) > a {
      top: calc(4*68px);
    }

    & > a {
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      padding: 16px;
      color: #fff !important;
      text-decoration: none;
      font-family: $font-PN;
      font-size: 16px;
      letter-spacing: 0.3px;
      text-transform: uppercase;
      box-shadow: 0 8px 16px rgba(0,0,0,.2);
      border-radius: 4px;
      background-color: $color-grey;
      z-index: 4;

      &::after {
        content: '';
        position: absolute;
        right: 16px;
        top: 50%;
        margin-top: -10px;
        width: 10px;
        height: 20px;
        mask-repeat: no-repeat !important;
        mask-position: center center !important;
        mask-size: contain !important;
        mask-image: url('../images/ic/arrow-right.svg');
        background-color: #fff;
        transition: all 0.3s ease;
        z-index: 5;
      }
      & > span {
        display: block;
      }
      & > i {
        position: absolute;
        right: 12px;
        bottom: 0;
        top: 0;
        width: 89px;
        background-repeat: no-repeat !important;
        background-position: 50% 100% !important;
        opacity: .6;
        transition: all 0.3s ease;
        z-index: 3;
      }
    }
    &.active {
      & > a {
        color: $color-default !important;
        text-decoration: none;
        background-color: $color-line;
        top: 0;
        padding-left: 42px;

        &::after {
          right: calc(100% - 26px);
          transform: rotate(180deg);
          background-color: $color-dgrey;
        }
        & > i {
          opacity: 1;
        }
      }
      .mcatalog-sub {
        transform: translateX(0);
      }
    }
    &.noactive {
      & > a {
        transform: translateX(calc(-100% - 16px));
      }
    }
  }
  &-sub {
    position: absolute;
    top: 0;
    padding: 80px 16px 0 16px;
    left: -16px;
    width: calc(100% + 32px);
    background-color: $color-default;
    transform: translateX(100%);
    transition: transform 0.5s;
    z-index: 3;

    .lactions {

      @media #{$mmedia-md} {
        text-transform: uppercase;
      }
      & > li {
        width: 100%;
        flex-wrap: wrap;

        & > div {
          width: 100%;
          color: #fff;
          padding-top: 8px;
        }
      }
    }
    .lmenu {
      margin: -10px;

      li {
        padding: 10px 10px;
      }
    }
  }
}
