//------------------------------------------------styles for pagination
.pag_wrap {
  @include flex_block_5(row, nowrap, flex-start, center, flex-start);
}

.pag_wrap__text {
  text-transform: uppercase;
  font-family: $proxima_nova;
  color: $brand_red;
  margin-right: 2.2em;
}

.pag_list {
  @include flex_block_5(row, nowrap, center, center, center);
  margin: 0 -.5rem;

  &._lg {
    text-transform: uppercase;
    font-family: $proxima_nova;

    .pag_item {
      color: $brand_red;
      padding: 0 3.5rem;

      &.prev_mod,
      &.next_mod {
        padding: 0 .5rem;
      }
    }

    .pag_link {
      text-decoration: none;
      width: 5.4rem;
      height: 5.4rem;
      line-height: 5.2rem;
      font-size: 1.6rem;
      font-weight: 400;
      color: $brand_red;
      border-color: $brand_red;
      border-width: .1rem;

      &.prev_mod {
        &::before {
          content: '';
        }
      }

      &.next_mod {
        &::before {
          content: '';
        }
      }

      &.active_mod {
        background-color: transparent;
        border-color: transparent;
        color: $brand_red;
      }

      &:hover {
        background-color: $brand_red;
        color: $white;
        text-decoration: none;
      }

      &:focus {
        text-decoration: none;
      }
    }
  }
}

.pag_item {
  padding: 0 .5rem;

  a {
    text-transform: uppercase;
    font-weight: 700;
    height: 3rem;
    min-width: 3rem;
    text-align: center;
    font-size: 1.4rem;
    border: .2rem solid $gray_19;
    color: $dark_5;
    font-family: $proxima_nova;
    line-height: 2.6rem;
    display: block;
    letter-spacing: 1px;
  }

  &.prev_mod,
  &.next_mod {
    a {
      padding: 0 1.1rem;
    }
  }

  &.prev_mod {
    a {
      &::before {
        content: '';
      }
    }
  }

  &.next_mod {
    a {
      &::before {
        content: '';
      }
    }
  }
}

.pag_link {
  @extend %transition_all;

  &.active_mod {
    background-color: #c4c4c4;
    border-color: #c4c4c4;
    color: $white;
  }

  &:hover {
    background-color: $gray_19;
    text-decoration: none;
  }

  &:focus {
    text-decoration: none;
  }
}

@media screen and (max-width: $point_3) {
  .pag_list {
    &._lg {
      .pag_item {
        &.prev_mod {
          a {
            &::before {
              display: none;
            }
          }
        }

        &.next_mod {
          a {
            &::before {
              display: none;
            }
          }
        }
      }
    }
  }
  .pag_item {
    position: relative;

    &.prev_mod,
    &.next_mod {
      a {
        padding: 0 0.1rem;

        &::before {
          content: '';
          display: inline-block;
          vertical-align: middle;
        }
      }
    }

    &.prev_mod {
      a {
        &::before {
          @include arrow(left, #707070, .8rem, .2rem);
          margin: -2px -1px 0 0;
        }
      }
    }

    &.next_mod {
      a {
        &::before {
          @include arrow(right, #707070, .8rem, .2rem);
          margin: -2px 0 0 -1px;
        }
      }
    }
  }

}

@media screen and (max-width: 767px) {
  .pag_wrap__text {
    display: none;
  }
  .pag_list._lg {
    width: 100%;
    justify-content: center;
  }
}

