.cabinet {
  &-wrapper {
    font-family: $font-PN;

    ul, ol {
      padding: 0;
      margin: 0;
      list-style: none;
    }

  }

  &-user {
    @media #{$mmedia-mmd} {
      margin: auto 0 9px;

      .navbar-list .navbar-item:first-child {
        margin-left: 8px;
      }

      //position: absolute;
      //bottom: 9px;
      //right: 0;
    }

    @media #{$mmedia-md} {
      @include flex_block(row nowrap, space-between, stretch);

      .navbar-link {
        //margin: 0 0 0 8px;
      }
    }

    &_info {
      font-size: 16px;

      @media #{$mmedia-mmd} {
        font-size: 24px;
      }
    }

    &_menu {
      @media #{$mmedia-md} {
        padding-top: 20px;
      }
    }

    &_name {
      font-weight: bold;
      font-size: 18px;
      line-height: 24px;
      color: $color-red;
      padding-bottom: 16px;
      border-bottom: 1px solid #cdcdcd;
    }

    &_tabs {

      @at-root ul#{&} {
        padding: 32px 0;

        @media #{$mmedia-md} {
          padding: 10px 0;
        }
      }

      li {
        margin-bottom: 8px;
      }
    }

    &_tab-link {
      color: $color-default;
      display: block;
      position: relative;
      font-weight: bold;
      font-size: 18px;
      line-height: 28px;
      text-decoration: none;
      padding-left: 30px;

      &:hover {
        color: $color-red;
      }

      .active > & {
        color: $color-light;
        cursor: default;

        &::before {
          @extend %vert_centr;
          content: '';
          position: absolute;
          left: 8px;
          width: 8px;
          height: 8px;
          border-radius: 100%;
          background: $color-red;

          @media #{$mmedia-md} {
            left: 0;
          }
        }
      }

      @media #{$mmedia-md} {
        padding-left: 15px;
      }
    }
  }

}

