.mainaction {
  position: relative;
  background-color: #ddd;
  overflow: hidden;
  border-radius: 4px;
  margin-bottom: 40px;
  color: $color-white;

  @media #{$mmedia-md} {
    margin-bottom: 32px;
  }

  &-image {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
  }
  &-bgdesktop {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-position: 50% 50% !important;
    background-repeat: no-repeat !important;
    background-size: auto 100% !important;
    z-index: 1;

    @media (max-width: 425px) {
      display: none;
    }
  }
  &-bgmobile {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-position: 50% 50% !important;
    background-repeat: no-repeat !important;
    background-size: auto 100% !important;
    z-index: 1;

    @media (min-width: 426px) {
      display: none;
    }
  }
  &-good {
    position: absolute;
    width: 473px;
    top: 60px;
    right: 30px;
    bottom: 45px;
    z-index: 2;

    @media #{$mmedia-lg} {
      width: 375px;
      right: 24px;
    }
    @media #{$mmedia-md} {
      width: 255px;
    }
    @media #{$mmedia-sm} {
      bottom: auto;
      height: 240px;
      width: 200px;
      right: 9px;
      top: 21px;
      justify-content: flex-end;
    }

    picture {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;

      @media #{$mmedia-sm} {
        justify-content: flex-end;
      }
    }
    img {
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &-link {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 5;
  }
  &-body {
    max-width: 656px;
    min-height: 522px;
    padding: 40px 48px 48px 48px;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 3;

    @media #{$mmedia-md} {
      min-height: 400px;
      max-width: 485px;
      padding: 24px 32px 32px 32px;
    }
    @media #{$mmedia-sm} {
      min-height: 456px;
      padding: 16px 16px 20px 16px;
    }
  }
  &-title {
    flex: 0 0 auto;
    width: auto;
    max-width: 100%;
  }
  &-foot {
    flex: 0 0 auto;
    width: auto;
    max-width: 100%;
    margin-top: auto;
    padding-top: 40px;
  }
  &-price {
    font-family: $font-PNC;
    font-size: 64px;
    line-height: .87;
    letter-spacing: -2px;
    margin-top: 16px;

    @media #{$mmedia-md} {
      font-size: 56px;
      line-height: 1;
    }
    @media #{$mmedia-sm} {
      font-size: 48px;
    }
  }
  &-priceold {
    font-family: $font-PNC;
    font-size: 32px;
    line-height: 1;
    letter-spacing: -.4px;
    text-decoration: line-through;

    @media #{$mmedia-md} {
      font-size: 28px;
    }
    @media #{$mmedia-sm} {
      font-size: 24px;
    }
  }
  &-title {
    font-family: $font-PNC;
    font-weight: 900;
    font-size: 48px;
    line-height: 1;
    margin-bottom: 8px;
    display: block;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;

    @media #{$mmedia-md} {
      font-size: 40px;
    }
    @media #{$mmedia-sm} {
      font-size: 32px;
    }
  }
  &-text {
    font-weight: 300;
    font-size: 24px;
    line-height: 1.75;
    display: block;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;

    @media #{$mmedia-md} {
      font-size: 18px;
      line-height: 1.75;
    }
    @media #{$mmedia-sm} {
      font-size: 16px;
    }
  }
}
